// <Desktop-specific layout - secondary navigation never present
main > .landing--functional {
    @include media('<small') {
        .landing--functional__finder {
            @include default-margin();

            .finder--mini {
                margin-left: 0;
                margin-right: 0;

                .finder__query {
                    margin-left: 0;
                    margin-right: 0;
                }
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .landing--functional {
            .landing--functional__finder {
                @include standard-grid();

                .landing--functional__finder__tabs {
                    grid-column: 3 / 23;
                }

                .finder--mini {
                    margin-left: 0;
                    margin-right: 0;

                    .finder__query {
                        margin-left: 0;
                        margin-right: 0;
                    }
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .landing--functional {
                .landing--functional__finder {
                    @include x-column-grid(18);

                    .landing--functional__finder__tabs {
                        grid-column: 3 / 17;
                    }

                    .finder--mini {
                        margin-left: 0;
                        margin-right: 0;

                        .finder__query {
                            margin-left: 0;
                            margin-right: 0;
                        }
                    }
                }
            }
        }
    }
}

// Generic styles
.landing--functional__finder {
    .landing--functional__finder__cta {
        margin-bottom: $measure--threequarters;

        a {
            padding-bottom: $measure--quarter;
            &:after {
                @include font-family('sharp');
                content: '\f061';
                margin-left: $measure--half;
            }
        }
    }

    .landing--functional__finder__tabs__columns,
    .landing--functional__finder__links__content--links {
        max-width: none;
        margin-left: 0;

        li {
            list-style: none;
            margin: 0 0 $measure--small1;
            padding: $measure--quarter 0 0;

            a {
                @include anchor-inline($color-gray10);

                font-weight: $font-weight-medium;
            }
        }

        @include media('>=tablet') {
            column-count: 2;
            column-gap: $measure--small2;
        }
    }

    .landing--functional__finder__links__content--links,
    .landing--functional__finder__tabs {
        //Icon color
        .landing--functional__finder__tabs__columns__icon {
            @include betatype();

            color: $color-red50;
            font-weight: $font-weight-light;
            margin-right: $measure--half;
        }

        // Subject icons
        .icon--arts_and_media {
            &:before {
                content: '\f618';
            }
        }
        .icon--business {
            &:before {
                content: '\f0b1';
            }
        }
        .icon--computing_and_computer_science {
            &:before {
                content: '\f108'; //not in FA6 -desktop f390
            }
        }
        .icon--engineering {
            &:before {
                content: '\f013';
            }
        }
        .icon--health {
            &:before {
                content: '\f5f8';
            }
        }
        .icon--history {
            &:before {
                content: '\f70f';
            }
        }
        .icon--languages {
            &:before {
                content: '\f075';
            }
        }
        .icon--literature {
            &:before {
                content: '\f040'; //not in FA6 - pencil  f303
            }
        }
        .icon--mathematics {
            &:before {
                content: '\f64c';
            }
        }
        .icon--politics_and_law {
            &:before {
                content: '\f0e3';
            }
        }
        .icon--psychology {
            &:before {
                content: '\f0c0';
            }
        }

        // Method of study icons
        .icon--class-based {
            &:before {
                content: '\f63d';
            }
        }
        .icon--full-time {
            &:before {
                content: '\f017';
            }
        }
        .icon--online {
            &:before {
                content: '\f109';
            }
        }
        .icon--part-time {
            &:before {
                content: '\f4fd';
            }
        }
        .icon--summer_school {
            &:before {
                content: '\f784';
            }
        }
        .icon--workshop {
            &:before {
                content: '\f685';
            }
        }

        .icon--apprentice {
            &:before {
                content: '\f64a';
            }
        }
    }

    .landing--functional__finder__links__content--wrapper {
        border: $border-width-hairline $border-style-solid $color-gray3;
        padding-bottom: $measure--small3;
        padding-left: $measure--small1;
        padding-right: $measure--small1;
        padding-top: $measure--basic;
        background-color: $color-gray1;
    }

    .landing--functional__finder__tabs {
        .tabs {
            .tabs__panel {
                border-top: none;
                border: $border-width-hairline $border-style-solid $color-gray3;
                padding-bottom: $measure--small3;
                padding-left: $measure--small1;
                padding-right: $measure--small1;
                padding-top: $measure--basic;
            }

            .tabs__links {
                justify-content: left;

                li button[aria-selected='true'] span {
                    border-bottom-color: $color-red50;
                    color: $color-red50;
                }
            }

            .tabs__panel__content {
                padding: 0;
            }
        }

        .landing--functional__finder__tabs__heading--nested,
        .landing--functional__finder__tabs__heading--above {
            font-weight: $font-weight-medium;
        }

        .landing--functional__finder__tabs__heading--nested {
            display: none;

            @include media('>=tablet') {
                display: block;
                margin-right: $measure--small4;
                margin-top: $measure--half;
            }
        }

        .landing--functional__finder__tabs__heading--above {
            display: none;

            @include media('<tablet') {
                display: block;
            }
        }

        .tabs__panel__heading {
            @include sr-only();
        }
    }
}
