@import 'linked-heading';

.anchor-inline--gray {
    @include anchor-inline(
        $color-gray12,
        $color-gray12,
        $color-nearblack,
        $color-outline--dark
    );
}

.anchor-inline--gray4 {
    @include anchor-inline(
        $color-gray4,
        $color-gray4,
        $color-nearblack,
        $color-outline--dark
    );
}

.anchor-inline--dark {
    @include anchor-inline(
        $color-gray11,
        $color-gray11,
        $color-purewhite,
        $color-outline
    );
}

.anchor-discreet {
    @include anchor-discreet;
}

.anchor-inline--notvisited {
    @include anchor-inline(
        $color-inline-link,
        $color-inline-link,
        $color-text--dark,
        $color-outline
    );
}

.anchor-cta-like {
    @include cta-like-anchors;
}

.anchor-underline-transition {
    @include underline-transition;
}

ul.cta-like-anchor-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: none;
    margin: $measure--basic 0 0;

    li.cta-like-anchor {
        width: calc(1 / 2 * 100% - (1 - 1 / 2) * #{$measure--half});
        list-style-type: none;

        @include media('<tablet') {
            width: 100%;
        }

        a {
            @include cta-outlined-light-bg($color-gray11, $color-gray6);
            width: 100%;
            height: 100%;
        }
    }
}

ul.cta-like-anchor-inline {
    display: flex;
    max-width: none;
    margin: $measure--basic 0 0;

    li.cta-like-anchor {
        list-style-type: none;
        margin-right: $measure--basic;
        &:last-child {
            margin-right: 0;
        }

        @include media('<tablet') {
            width: 100%;
        }

        a {
            @include cta-like-anchors;
            span {
                margin: 0;
            }
        }

        .cta-like-anchor__text {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            flex-wrap: wrap;

            @include media('>tablet') {
                flex-wrap: nowrap;
            }
        }
    }
}
