$colors: (
    blue50: (
        $color-blue50,
        $color-purewhite,
    ),
    blue70: (
        $color-blue70,
        $color-purewhite,
    ),
    blue90: (
        $color-blue90,
        $color-purewhite,
    ),
    brown40: (
        $color-brown40,
        $color-gray10,
    ),
    green50: (
        $color-green50,
        $color-gray10,
    ),
    orange50: (
        $color-orange50,
        $color-gray10,
    ),
    orange80: (
        $color-orange80,
        $color-purewhite,
    ),
    pink90: (
        $color-pink90,
        $color-purewhite,
    ),
    purewhite: (
        $color-purewhite,
        $color-gray10,
    ),
    purple50: (
        $color-purple50,
        $color-purewhite,
    ),
    purple80: (
        $color-purple80,
        $color-purewhite,
    ),
    red50: (
        $color-red50,
        $color-purewhite,
    ),
    teal90: (
        $color-teal90,
        $color-purewhite,
    ),
    yellow20: (
        $color-yellow20,
        $color-gray10,
    ),
);

@each $class, $color in $colors {
    $bgColor: nth($color, 1);
    $textColor: nth($color, 2);
    @include media('>=tablet') {
        .card__side-image {
            &.background--#{$class} {
                figure {
                    &:after {
                        background-color: $bgColor;
                    }
                }
            }
        }
    }
    .card__side-image {
        &.background--#{$class} {
            figure {
                @include media('<tablet') {
                    background-color: $bgColor;
                }
            }
            .card__side-image__text {
                color: $textColor;
                background-color: $bgColor;
            }

            img {
                margin-bottom: calc(
                    #{-$measure--quarter / 2}
                ); // Stops Firefox adding white space below image
            }

            &.background--purewhite {
                a {
                    @include anchor-inline();
                }
            }

            &:not(.background--purewhite) {
                a {
                    &:link,
                    &:visited {
                        color: $textColor;
                        border-bottom-color: $textColor;
                        background-image: none;
                    }
                    &:active,
                    &:focus,
                    &:hover {
                        color: $textColor;
                        background-color: transparent;
                        background-image: none;
                        border-bottom-color: $textColor;
                        outline-color: $textColor;
                    }
                }
            }
        }
    }
}

.card__side-image {
    display: flex;
    margin-top: $measure--threequarters;

    @include media('>=tablet') {
        background: $color-purewhite;
        &[data-image-position='left'] {
            figure {
                &:after {
                    left: 0;
                }
            }
        }
        &[data-image-position='right'] {
            flex-direction: row-reverse;
            .card__side-image__text {
                padding-left: 0;
            }
            figure {
                &:after {
                    right: $measure--small4;
                }
            }
        }
        figure {
            &[data-image-width='wide'] {
                max-width: 50%;
            }
            &[data-image-width='narrow'] {
                max-width: 200px;
            }
            min-width: 200px;
            padding: 0 0 $measure--small4 0;
        }
        &:not(.background--purewhite) {
            .card__side-image__text {
                margin-top: $measure--small4;
            }
        }
    }

    @include media('<tablet') {
        flex-direction: column;
        figure {
            &[data-image-position='right'] {
                flex-direction: row-reverse;
                text-align: right;
                margin-right: $measure--basic;
            }
            padding: $measure--small2 0 0;
            img {
                max-width: 50%;
            }
        }
        &.background--purewhite {
            padding: $measure--threequarters 0 0;
            margin-top: 0;
            .card__side-image__text {
                padding: 0;
            }
        }
        &:not(.background--purewhite) {
            margin-top: $measure--medium3;
            figure {
                margin-top: -$measure--medium3;
                background: none;
                img {
                    padding-left: $measure--basic;
                }
            }
        }
    }

    @include media('<mobile') {
        figure {
            &[data-image-position='right'] {
                margin-right: 0;
            }
            img {
                max-width: 100%;
            }
        }
    }

    figure {
        margin-top: 0;
        &:after {
            content: '';
            height: 100%;
            margin-left: $measure--small4;
            position: absolute;
            width: 100%;
            picture {
                &:after {
                    margin-left: 0;
                    margin-top: $measure--small3;
                    top: 0;
                }
            }
        }
    }
    .card__side-image__text {
        min-width: 50%;
        padding: 0 $measure--basic $measure--basic;
    }

    figure {
        overflow: hidden;
        position: relative;

        img {
            z-index: z-index('above');
            position: relative;
        }
    }
}
