.unibuddy-card {
    background-color: var(--color-purewhite);
    padding: $measure--basic;
    @include box-shadow;
}

.unibuddy-card--info {
    display: flex;
}

.unibuddy-card--info__image {
    img {
        max-width: $measure--large2;
        height: $measure--large2;
        object-fit: cover;
        border-radius: 100%;
    }

    .fa-user {
        font-size: $measure--medium7;
    }
}

.unibuddy-card--info__details {
    display: flex;
    flex-direction: column;
    padding-left: $measure--basic;

    .unibuddy-card--info__details--name {
        color: $color-red50;
        font-weight: bold;
    }

    .unibuddy-card--info__details--level {
        font-weight: bold;
    }
}

.unibuddy-card--text {
    padding-bottom: $measure--basic;
}
