@mixin selected-select() {
    background-color: $color-purewhite;
    border-color: $color-red60;
    color: $color-red60;
    font-weight: $font-weight-medium;
}

.no-js {
    .wrapper--finder__filters {
        .wrapper--finder__filters--mobile__toggle {
            display: none;
        }

        &[data-open='false'] {
            .finder__filters {
                display: block;
            }
        }
    }
}

.finder__reset {
    @include media('>=tablet') {
        @include alphatype();
        @include cta--border(
            $background: $color-red60,
            $color: $color-purewhite,
            $border: $color-red60,
            $hover-color: $color-purewhite,
            $hover-border: $color-red80,
            $outline: $color-nearblack,
            $corners: round
        );

        height: $measure--medium4;
        margin-right: $measure--basic;
    }
}

.finder__filter:last-child {
    margin-bottom: $measure--threequarters;
}

.finder__select {
    color: $color-nearblack;
    height: auto;
    margin-top: $measure--half;
    position: relative;

    @include media('<tablet') {
        margin-bottom: $measure--half;
    }

    label {
        @include smalltype();

        color: $color-red50;
        display: block;
        font-weight: $font-weight-text;
        padding-bottom: $measure--basic;
    }

    select {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        background-color: $color-gray1;
        border-color: $color-gray4;
        border-style: $border-style-solid;
        border-width: $border-width-default;
        bottom: $measure--quarter;
        color: $color-nearblack;
        font-weight: $font-weight-text;
        height: $measure--medium4;
        position: relative;
        width: 100%;

        &:focus {
            @include outline-properties();
        }
    }

    &:not(.finder__select--sort) {
        &.finder__select--selected {
            select {
                @include selected-select();
            }
        }
    }

    &.finder__select--sort {
        &.finder__select--selected {
            select {
                @include media('<tablet') {
                    @include selected-select();
                }
            }
        }
    }
}

.finder__checkbox {
    @include font-family('default');
    @include body-font-features();
    @include alphatype();

    align-items: center;
    color: $color-nearblack;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    font-weight: $font-weight-text;
    height: $measure--medium4;
    margin-bottom: $measure--threequarters;
    margin-top: $measure--quarter;
    position: relative;

    &:focus-within {
        @include outline-properties();
    }

    label {
        display: flex;
        overflow: hidden;
        padding-bottom: $measure--basic;
        padding-left: $measure--half;
        padding-top: $measure--basic;
    }

    input {
        appearance: none;
        height: 0;
        left: 0;
        position: absolute;
        top: 0;
        width: 0;

        &:focus {
            outline: none;
        }
    }

    .finder__checkbox__indicator {
        align-items: center;
        background-color: $color-purewhite;
        border-color: $color-gray4;
        border-style: $border-style-solid;
        border-width: $border-width-default;
        color: $color-nearblack;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: $measure--small4;
        justify-content: center;
        width: $measure--small5;
    }

    input:checked + .finder__checkbox__indicator {
        background-color: $color-red60;
        border-color: $color-red60;
        color: $color-purewhite;
    }
}

.finder__tag {
    button,
    a {
        @include alphatype();
        @include cta--border(
            $background: $color-gray1,
            $color: $color-nearblack,
            $border: $color-gray1,
            $hover-color: $color-nearblack,
            $hover-border: $color-gray3,
            $outline: $color-nearblack,
            $corners: square
        );

        height: $measure--medium4;
        margin-bottom: $measure--threequarters;
        margin-right: $measure--basic;
        margin-top: $measure--quarter;
        padding-left: $measure--quarter;
        padding-right: $measure--quarter;

        @include media('<tablet') {
            height: unset;
        }

        .icon {
            color: $color-red60;
        }
    }
}

.wrapper--finder__filters {
    .finder__filters {
        @include media('>=tablet') {
            display: block;

            .finder__filter,
            .finder__filters__reset--desktop {
                margin-right: $measure--basic;
            }

            .finder__filter:not(.finder__tag),
            .finder__filters__reset--desktop {
                width: calc(33% - #{$measure--basic});

                @include media('>=desktop') {
                    width: calc(25% - #{$measure--basic});
                }
            }

            .finder__filters__reset--desktop {
                margin-bottom: $measure--threequarters;
                margin-right: $measure--basic;
                margin-top: $measure--quarter;
            }
        }
    }
}

.wrapper--finder_filters--filters {
    @include media('<small') {
        padding-left: $measure--small2;
        padding-right: $measure--small2;
    }

    @include media('>=tablet') {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
}

.finder__filters__heading {
    @include deltatype();

    background-color: $color-gray3;
    display: flex;
    justify-content: space-evenly;
    margin-top: 0;
    padding: $measure--threequarters $measure--small2;

    @include media('>mobile') {
        justify-content: center;
    }

    @include media('<tablet') {
        padding: 0;

        > .icon,
        > .finder__filters__heading__text {
            padding: $measure--half;
        }
    }

    @include media('>=tablet') {
        background-color: transparent;
        justify-content: space-between;

        .finder__reset {
            display: none;
        }
    }

    .icon {
        margin-right: $measure--half;
        margin-top: $measure--quarter;
    }
}

.wrapper--finder__filters
    .finder__filters
    label:not(.finder__filters__label--always) {
    @include media('>=tablet') {
        @include sr-only();
    }
}

.finder__filters__nofilters {
    margin-bottom: $measure--basic;
}

.finder__filters__reset--desktop,
.finder__filter {
    + .finder__filters__nofilters {
        display: none;
    }
}

.wrapper--finder__filters {
    @include media('<tablet') {
        &[data-open='true'] {
            background-color: $color-purewhite;
            bottom: 0;
            height: 100vh;
            left: 0;
            position: fixed;
            right: 0;
            top: 0;
            width: 100vw;
            z-index: z-index('modal');

            .wrapper--finder__filters--mobile__filters {
                -webkit-overflow-scrolling: touch;
                height: 100vh;
                overflow-y: scroll;
                width: 100vw;
            }

            .wrapper--finder__filters--mobile__filters__content {
                padding-bottom: $measure--medium9;
            }
        }

        .finder__filters {
            margin-bottom: $measure--medium3;

            @include media('>=tablet') {
                padding-left: $measure--small2;
                padding-right: $measure--small2;
            }
        }

        .wrapper--finder_filters--filters {
            background-color: $color-purewhite;
        }

        .wrapper--finder__filters--mobile__apply {
            @include media('>=tablet') {
                display: none;
            }
            background-color: $color-red50;
            bottom: 0;
            color: $color-purewhite;
            position: fixed;
            width: 100%;

            button {
                @include gammatype();
                @include cta--basic--subelement(
                    $color: $color-purewhite,
                    $background: $color-red50,
                    $hover-color: $color-purewhite,
                    $hover-background: $color-red70,
                    $outline: $color-purewhite,
                    $corners: square,
                    $class: 'wrapper--finder__filters--mobile__apply__text'
                );

                border: 0;
                margin-top: $measure--basic;
                padding-bottom: $measure--basic;
                padding-top: $measure--basic;
                width: 100%;

                .icon {
                    left: $measure--basic;
                    padding-bottom: $measure--quarter;
                    padding-left: 0;
                    padding-right: 0;
                    padding-top: $measure--quarter;
                    position: absolute;
                }

                .wrapper--finder__filters--mobile__apply__text {
                    border-bottom-color: $color-purewhite;
                    border-bottom-style: $border-style-solid;
                    border-bottom-width: $border-width-default;
                }
            }
        }

        .finder__reset {
            @include cta--basic--subelement(
                $color: $color-purewhite,
                $background: $color-red60,
                $hover-color: $color-purewhite,
                $hover-background: $color-red80,
                $outline: $color-purewhite,
                $corners: round,
                $class: 'finder__reset__text'
            );

            @include media('>mobile') {
                margin-left: $measure--medium1;
            }

            .icon {
                display: none;
            }
        }

        .finder__filters__reset--desktop {
            display: none;
        }
    }

    @include media('<tablet') {
        &[data-open='false'] {
            padding-left: $measure--small2;
            padding-right: $measure--small2;
            text-align: right;

            .finder__filters,
            .wrapper--finder__filters--mobile__apply {
                display: none;
            }
        }
    }

    @include media('>=tablet') {
        .wrapper--finder__filters--mobile__apply {
            display: none;
        }
    }
}

.wrapper--finder__filters--mobile__toggle {
    @include media('>=tablet') {
        display: none;
    }
    @include gammatype();
    @include cta--border(
        $background: $color-purewhite,
        $color: $color-nearblack,
        $border: $color-nearblack,
        $hover-color: $color-purewhite,
        $hover-border: $color-nearblack,
        $outline: $color-nearblack,
        $corners: square
    );

    margin-top: $heading-margin;
    width: 100%;

    &[data-filters-applied='true'] {
        border-color: $color-red50;

        .wrapper--finder__filters--mobile__toggle__count {
            color: $color-red50;
        }
    }
}
