.imageColLayout-wrap__section {
    @include standard-grid();

    h2 {
        grid-column: 1/-1;
    }
}

.imageColLayout {
    &.imageColLayout--2,
    &.imageColLayout--3 {
        @include media('>between') {
            display: flex;
            .imageColLayout--card {
                &:not(:last-of-type) {
                    padding-right: $measure--basic;
                }
            }
        }
    }
    column-count: 3;
    column-gap: $measure--small2;
    grid-column: 1/-1;
    margin: $measure--basic 0;

    @include media('<=tablet') {
        column-count: 1;
    }
}

.imageColLayout--card {
    display: inline-block;
    width: 100%;
    @include media('<=tablet') {
        img {
            width: 100%;
        }
    }
}

.imageColLayout-card--image {
    img {
        min-width: 100%;
    }
}
