@mixin navigation-anchor(
    $color: $color-purewhite,
    $background: $color-gray10,
    $hover-color: $color-purewhite,
    $hover-background: $color-gray8,
    $outline: $color-purewhite
) {
    @include anchor-reset();

    background-color: $background;
    color: $color;
    transition: background-color ease-out $transition-duration;

    &:focus,
    &:active {
        color: $color;

        span {
            outline-color: $outline;
            outline-offset: $border-width-thick;
            outline-style: $border-style-solid;
            outline-width: $border-width-default;
        }
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-background;
            color: $hover-color;
        }
    }
}

@mixin navigation-button(
    $color: $color-purewhite,
    $background: $color-gray10,
    $hover: $color-gray8,
    $outline: $color-purewhite
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);

    background-color: $background;
    color: $color;
    transition: background-color ease-out $transition-duration;

    &:focus {
        span {
            color: $color;
        }
    }

    &:hover {
        @include media('hover') {
            background-color: $hover;
            color: $color;
        }
    }
}

@mixin navigation-button-transition-helper($bg-border-color) {
    background: $bg-border-color;
    border: $border-width-thicker $border-style-solid $bg-border-color;
    border-bottom: $border-width-thicker $border-style-solid $bg-border-color;
    width: $measure--small4;

    &:focus,
    &:active,
    &:hover {
        outline-offset: -$border-width-hairline;
        border-bottom: $border-width-thicker $border-style-solid
            $bg-border-color;
    }
}
