.research-degree {
    .highlighted-border--left {
        padding: $measure--quarter $measure--basic;
        margin: $measure--half 0;
        border-left: $measure--quarter $border-style-solid $color-purple70;

        .research-degree__fees {
            display: flex;

            p {
                padding-right: $measure--basic;
                flex-basis: 50%;

                span {
                    display: block;

                    &.research-degree__fees__value {
                        color: $color-purple70;
                        font-weight: $font-weight-medium;
                    }
                }
            }
        }
    }

    .icon-wrapper {
        margin: 0 $measure--basic 0 0;
        display: table;
        line-height: $measure--small3;
        padding-bottom: #{$measure--quarter + ($measure--quarter / 2)};

        .icon {
            display: table-cell;
            padding: $measure--quarter $measure--half 0 0;
            margin: $measure--quarter $measure--quarter 0 0;
            color: $color-purple70;
        }
    }
}

.research-degree .banner-text.banner--center {
    background: $color-gray9;
}

.research-degree {
    @include banner($color-gray9);
}

.research-degree__standfirst-block {
    @include content-grid;
}

.research-degree__standfirst {
    max-width: 100%;
    grid-column: 1 / -1;
    margin: $measure--medium1 0;

    @include media('<small') {
        @include default-padding();
    }

    @include media('>=tablet') {
        grid-column: 2/-2;
    }

    @include media('>between') {
        grid-column: 3/-2;
    }

    @include media('>desktop') {
        grid-column: 4/-3;
    }

    .standfirst {
        color: $color-purewhite;
    }
}

.wrapper--research-degree__pre-overview {
    padding: $measure--basic 0;
}

.wrapper--research-degree__pre-overview,
.wrapper--research-degree__overview {
    @include media('>=tablet') {
        @include content-grid;
    }
    @include media('<small') {
        padding-left: $measure--basic;
        padding-right: $measure--basic;
    }
}
.wrapper--research-degree__research-projects {
    @include media('>=tablet') {
        @include content-grid;
    }
    @include media('<small') {
        @include default-padding;
    }
}

.wrapper--research-degree__promo-box {
    @include media('<small') {
        @include default-padding;
    }
}

.wrapper--research-degree__key-information {
    @include media('>=tablet') {
        grid-column: 3/-3;
    }
}

.wrapper--research-degree__org-units-research-centres,
.wrapper--research-degree__key-information,
.research-degree__testimonials__content,
.research-degree__overview__content {
    grid-column: 1/-1;
    @include media('>=tablet') {
        grid-column: 2/-2;
    }
    @include media('>between') {
        grid-column: 3/-3;
    }
}

.wrapper--research-degree__students {
    .featured-profiles-block {
        margin: 0;
    }
}

.wrapper--featured-profiles__profile {
    margin-top: $measure--small3;
}

.wrapper--featured-profiles__heading-intro {
    margin-top: $measure--threequarters;
}

.wrapper--research-degree__promo-box,
.research-degree__overview {
    @include media('>=tablet') {
        grid-column: 2/-2;
    }
    @include media('>desktop') {
        grid-column: 3/-3;
    }
}

.wrapper--research-degree__research-projects {
    @include media('<tablet') {
        padding-top: $measure--small2;
        padding-bottom: $measure--small2;
    }

    h2.research-degree__research-projects__heading {
        margin-top: $measure--medium1;
        grid-column: 2/-2;
    }

    .research-degree__research-projects {
        grid-column: 1/-1;
        @include media('>=small') {
            grid-column: 2/-2;
        }
        .slider {
            padding: 0;
            @include media('>=tablet') {
                margin: $measure--basic 0;
            }
            > .slide {
                background: transparent;
            }
        }
    }
}

.research-degree__org-units-research-centres {
    ul.research-centres {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .research-degree__org-units-research-centres__org-units__label {
        &:after {
            content: '\2219';
            margin: 0 $measure--half;
        }

        &:last-child {
            &:after {
                display: none;
            }
        }
    }

    ul {
        @include media('>between') {
            columns: 2;
        }
        margin-left: 0;
        max-width: 100%;
        li {
            list-style-type: none;
        }
    }
}

.wrapper--research-degree__key-information {
    .key-information--research {
        .key-information--research__main {
            margin-top: $measure--basic;
        }
    }
}

.wrapper--research-degree__promo-box {
    margin-top: $measure--basic;
    padding: 0;
    .promo-content {
        grid-column: 1/-1;
        margin: 0;
        @include media('>tablet') {
            margin-bottom: $measure--basic;
        }
    }
}

.research-degree__overview {
    padding-top: $measure--basic;
    margin-bottom: $measure--basic;
    grid-column: 1/-1;
    @include content-grid;

    .show-more {
        h2 {
            @include heading-centered();
        }
    }

    .research-degree__overview__content {
        .show-more,
        .accordion {
            grid-column: 1/-1;
        }

        .show-more {
            @include media('>=small') {
                display: grid;
                @include x-column-grid(12);

                h2,
                .show-more__content,
                .show-more__button__container {
                    grid-column: 2 / -2;
                }
            }
        }
    }
}

.no-js {
    .research-degree__overview__content {
        .research-degree__overview__content__apply-links {
            display: none;
            &.research-degree__overview__content__apply-links--multiple-pathways {
                display: none;
            }
        }
    }
    .how-to-apply--research--no-js {
        display: block;
    }
}

.js .how-to-apply--research--no-js {
    display: none;
}

.research-degree__overview__content {
    .research-degree__overview__content__apply-links {
        @include media('>=tablet') {
            display: flex;
        }
        &.research-degree__overview__content__apply-links--multiple-pathways {
            @include media('>=tablet') {
                display: block;
            }
        }
        .wrapper--cta-block {
            display: flex;
            .cta-block {
                width: 100%;
                &:nth-child(odd) {
                    margin-right: $measure--basic;
                }
                li,
                button,
                a,
                span {
                    width: 100%;
                }
                li {
                    text-align: center;
                }
            }
        }
    }
}

.wrapper--research-degree__testimonials {
    margin-top: $measure--basic;
    @include content-grid;
    @include media('<small') {
        @include default-padding;
    }

    h2 {
        grid-column: 1/-1;
    }

    .research-degree__testimonials__content {
        .related-content.slider {
            grid-column: 1/-1;

            .slider__active-slide {
                grid-column: 1/-1;

                @include media('>tablet') {
                    grid-column: 2/-2;
                }

                @include media('>between') {
                    grid-column: 3/-4;
                }
            }

            .slider__controls__wrap {
                margin: $measure--basic 0;
                grid-column: 7/-1;
            }
        }
    }
}

.js {
    .research-degree {
        .related-content {
            padding-bottom: $measure--small2;
            &[data-style='arrows']:not(.slider) {
                padding-bottom: $measure--small2;
                @include media('<small') {
                    @include default-padding;
                }
            }
        }
        .research-degree__research-projects {
            .related-content {
                padding-bottom: 0;
                > li {
                    padding: 0;
                    ul {
                        margin: 0;
                        background-color: $color-gray1;
                    }
                }
            }
        }
    }
}
