@mixin anchor-icon(
    $color: $color-text,
    $visited: $color-text,
    $hover: $color-red50,
    $outline: $color-text
) {
    @include anchor-reset-properties();

    border-color: $color;
    color: $color;
    display: inline-block;

    &:hover {
        @include media('hover') {
            border-color: $hover;

            .icon {
                color: $hover;
            }
        }
    }

    &:active,
    &:focus {
        @include outline-properties($outline);
    }
}
