////
/// Skip links
/// @group patterns
////

.skip-links {
    background-color: $color-gray10;
    border: 0;
    clip: rect(0, 0, 0, 0);
    color: $color-purewhite;
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;

    .skip-links__link {
        @include cta--basic(
            $color-purewhite,
            $color-red50,
            $color-purewhite,
            $color-red70,
            $color-purewhite,
            square
        );

        > .skip-links__intro {
            @include sr-only();
        }
    }

    &:active,
    &:focus,
    &:focus-within {
        clip: auto;
        height: auto;
        margin: 0;
        overflow: visible;
        padding-bottom: $measure--half;
        padding-top: $measure--half;
        position: relative;
        z-index: z-index('above');
        width: auto;

        .skip-links__link {
            clip: auto;
            height: auto;
            margin: 0;
            overflow: visible;
            padding-bottom: $measure--half;
            padding-left: $measure--small2;
            padding-right: $measure--small2;
            padding-top: $measure--half;
            position: static;
            width: auto;
        }
    }

    .skip-links__content {
        @include standard-box();

        display: flex;
        flex-direction: row;

        p {
            align-self: center;
            display: flex;
            margin-bottom: $measure--quarter;
            margin-right: $measure--basic;
            margin-top: $measure--quarter;
            max-width: unset;
        }
    }

    .skip-links__content ul {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        list-style: none;
        margin-left: 0;
        margin-top: 0;
        max-width: unset;

        li {
            margin-bottom: $measure--quarter;
            margin-left: 0;
            margin-top: $measure--quarter;

            &:not(:last-of-type) {
                margin-right: $measure--basic;
            }
        }
    }
}
