////
/// Statistics block
/// @group patterns
////
.statistics-block {
    background: $color-purewhite;

    @include media('>=tablet') {
        padding: $measure--basic 0;
    }

    // Block renders as slider < tablet
    @include media('<tablet') {
        padding: 0 $measure--small2 $measure--basic $measure--small2;
        margin-top: 0;

        &[data-slider-render='tablet'] {
            .slider-block--mobile {
                [data-visible='false'] {
                    display: none;
                }
                .slider-block__control {
                    display: flex;
                    .slider-block__control__buttons--dot {
                        display: block;
                    }
                }
            }
        }
    }

    .statistics-block__heading {
        padding: $measure--basic;
        text-align: center;
    }

    .ksp__items {
        @include default-margin();

        margin: 0;
        max-width: none;
        text-align: center;

        // Generic properties for all statistics
        .ksp__item {
            border-right: $border-width-default $border-style-solid $color-gray1;
            list-style-type: none;
            padding-left: $measure--basic;
            padding-right: $measure--basic;

            &:last-of-type {
                border-right: 0;
            }

            // Control vertical right border depending on device width
            @include media('<tablet') {
                border-right: 0;
                padding: 0;
                margin: 0;
            }

            h2 {
                @include h1style();

                font-weight: $font-weight-light;

                .animate--number__unit--post {
                    @include alphatype();
                    margin-left: $measure--quarter;
                }
            }
        }

        .ksp__item__overline {
            @include font-family('default');
            color: $color-gray7;
            font-size: $font-size--alpha;
            font-weight: $font-weight-book;
        }

        .ksp__item__pre-figure,
        .ksp__item__figure,
        .ksp__item__post-figure,
        .ksp__item__text-only {
            @include font-family('heading');

            font-weight: $font-weight-light;
            display: inline;
            color: $color-red50;
        }

        .ksp__item__text {
            @include font-family('default');

            color: $color-gray10;
            font-size: $font-size--alpha;
            font-weight: $font-weight-text;
            margin: $measure--basic 0;
        }

        .ksp__item__icon {
            display: block;
            margin: 0 auto;
            width: 52px; // Icon height matches text height.

            @include media('<tablet') {
                margin: 0;
                display: inline;
            }
        }

        .ksp__item__heading--top-padding,
        .ksp__item__icon--top-padding {
            @include media('<tablet') {
                padding-top: $measure--basic;
            }
        }

        // Animation
        .animation-incomplete {
            opacity: 0;
        }

        .animation-incomplete.animation-complete {
            opacity: 1;
            transition: opacity 1500ms;
        }

        .animate--number--init {
            .animate--number__unit {
                opacity: 0;
            }
        }

        .animate--number--complete {
            .animate--number__unit {
                transition: opacity 1s;
                opacity: 1;
            }
        }

        // Control individual statistic width based on parent class
        &.ksp__items--2 {
            @include media('>=tablet') {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;

                .ksp__item {
                    width: 50%;
                }
            }
        }

        &.ksp__items--3 {
            @include media('>=tablet') {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;

                .ksp__item {
                    width: 33%;
                    padding: 0 $measure--basic;
                }
            }
        }

        &.ksp__items--4 {
            @include media('>=tablet') {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;

                .ksp__item {
                    width: 25%;
                }
            }
        }
    }
}
