.ux-personas {
    margin-left: 0;

    li {
        align-items: flex-end;
        display: flex;
        flex-direction: row;
        list-style-type: none;

        figure {
            flex-basis: 25%;
            text-align: center;
        }

        div {
            flex-basis: 75%;
            margin-left: $measure--basic;
            padding-bottom: $measure--half;
        }
    }
}
