// > Desktop
main .find-us {
    @include media('>=small') {
        @include content-grid();
        .find-us__content {
            grid-column: 2/-2;
        }
    }
}

// < Desktop
main .find-us {
    @include media('<small') {
        .find-us__content {
            grid-column: 1/-1;
            @include default-margin();
        }
    }
}

// Generic
.find-us {
    padding-bottom: $measure--medium1;
}

.find-us__header,
.campus-map,
.campus-map__controls,
.find-us__content {
    grid-column: 1/-1;
}

.find-us .tabs__panel__content {
    margin: 0 auto;
    max-width: 55ch;
}

.find-us__header {
    background: $color-pink80;
    text-align: center;
    color: $color-purewhite;
    padding: $measure--small3;
}

.campus-map {
    background: $color-gray1;
    display: flex;
    flex-direction: column;
}
.campus-map__controls {
    display: flex;
    flex-direction: column;
    margin: $measure--small2;
    position: relative;
}

.campus-map__controls__container {
    display: flex;
    @include media('<mobile') {
        flex-direction: column;
    }
}

.campus-map__controls__search__wrap {
    background: $color-purewhite;
    flex: 2;
    margin-right: $measure--small4;
    position: relative;
    display: flex;
    border: $border-width-hairline $border-style-solid $color-gray3;

    &:focus-within {
        outline: $border-width-default $border-style-dotted $color-nearblack;
        outline-offset: $border-width-default;
    }

    @include media('>=small') {
        margin-right: $measure--small4;
    }

    @include media('<small') {
        margin-right: $measure--basic;
    }

    @include media('<mobile') {
        flex: 1;
        margin-right: 0;
        margin-bottom: $measure--basic;
    }

    &:before {
        content: '\f002';
        font-family: 'Font Awesome 6 Pro';
        font-weight: $font-weight-book;
        position: absolute;
        color: $color-gray6;
        margin: $measure--basic;
    }

    .campus-map__controls__search {
        border: none;
        padding: $measure--threequarters $measure--medium2;
        flex: 1;
        width: 100%;
        @include smalltype();
        font-feature-settings: normal;

        &:focus,
        &:active {
            outline: none;
        }
    }

    .campus-map__controls__search__clear {
        @include epsilontype();
        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: $measure--small5;
        justify-content: center;
        position: absolute;
        right: $measure--half;
        top: 0.35rem;
        width: $measure--small5;

        &:focus {
            @include outline-properties();
        }

        .campus-map__controls__search__clear__text {
            @include sr-only();
        }

        .icon {
            /*
            // temp overide with xmark-temp-styling below

            --fa-primary-color: #{$color-gray7};
            --fa-primary-opacity: 1;
            --fa-secondary-color: #{$color-gray1};
            --fa-secondary-opacity: 1;
            */
            @include xmark-temp-styling;

            letter-spacing: 0;
            line-height: 1;
        }

        &:focus,
        &:active {
            outline: $border-width-default $border-style-dotted $color-nearblack;
            outline-offset: $border-width-default;
        }
    }
}

.campus-map__controls__locations__heading__wrap {
    flex: 1;
    position: relative;

    .campus-map__controls__locations__heading {
        background: $color-purewhite;
        border: $border-width-hairline $border-style-solid $color-gray3;
        width: 100%;
        padding: $measure--threequarters;
        cursor: pointer;
        display: flex;
        @include smalltype();

        &:focus,
        &:active {
            outline: $border-width-default $border-style-dotted $color-nearblack;
            outline-offset: $border-width-default;
        }

        &[data-show='false'] {
            span {
            }
            &:after {
                content: '\f0d7'; //f0d7 f0dd
            }
        }

        &[data-show='true'] {
            position: absolute;
            border-bottom: 0;
            z-index: z-index('inpage');

            @include media('<mobile') {
                position: initial;
            }

            &:after {
                content: '\f00d'; //\f0de
            }
        }

        span {
            flex: 1;
            text-align: left;
            width: 100%;
            display: block;
        }

        &:after {
            font-family: 'Font Awesome 6 Pro';
            font-weight: $font-weight-extrablack;
            right: 0;
            color: $color-red60;
            text-align: right;
        }
    }
}

.campus-map__controls__locations {
    background: $color-purewhite;
    display: flex;
    flex-wrap: wrap;
    box-shadow: rgba(0, 0, 0, 0.25) 0 $measure--basic $measure--medium3 -$measure--threequarters;

    &:focus {
        outline: 0;
        border: 0;
    }

    .accordion {
        flex-basis: 50%;
        position: relative;

        button {
            border-top: solid $border-width-default transparent;
        }

        @include media('<=tablet') {
            flex-basis: 100%;
        }

        .accordion__body {
            position: absolute;
            width: 100%;
            z-index: z-index('above');
        }
    }

    &[data-show='true'] {
        display: inherit;
        position: absolute;
        width: 100%;
        top: 2.93rem;
        z-index: z-index('above');
        border: $border-width-hairline $border-style-solid $color-gray3;

        @include media('<mobile') {
            top: $measure--large4;
        }
    }

    &[data-show='false'] {
        display: none;
    }
}

.find-us__content {
    @include media('<small') {
        @include default-margin();
    }
}

.query__suggestions__wrapper {
    position: absolute;
    left: 0;
    width: 100%;

    #query__suggestions {
        position: absolute;
        background: $color-purewhite;
        z-index: z-index('inpage');
        margin: 0;
        padding: $measure--basic;
        width: 100%;
        max-width: 100%;
        top: $measure--medium3;
        box-shadow: rgba(0, 0, 0, 0.25) 0 $measure--basic $measure--medium3 -$measure--threequarters;
        border-radius: 0 0 $measure--half $measure--half;
        list-style: none;

        &[data-show='false'] {
            display: none;
        }
    }
}

// Google map popup
@include media('<tablet') {
    .gm-style-iw.gm-style-iw-c {
        width: 25rem;
    }
}
