.promo-area__wrap {
    padding-top: $measure--small4;
    padding-bottom: $measure--small4;

    .promo-area {
        display: grid;
        position: relative;
        margin-bottom: $measure--medium3;
        @include x-column-grid(24);

        &.layout__left--full {
            .promo-area__content {
                grid-column: 2 / 13;

                @include media('<=small') {
                    grid-column: 2 / 18;
                }

                @include media('<=tablet') {
                    grid-column: 4 / 22;
                }

                @include media('<=mobile') {
                    grid-column: 3 / 23;
                }
            }
        }

        &.layout__right--full {
            .promo-area__content {
                grid-column: 13 / 24;

                @include media('<=small') {
                    grid-column: 8 / 24;
                }

                @include media('<=tablet') {
                    grid-column: 4 / 22;
                }

                @include media('<=mobile') {
                    grid-column: 3 / 23;
                }
            }
        }

        &.layout__right--full,
        &.layout__left--full {
            @include media('<tablet') {
                margin-bottom: 0;
            }

            img {
                @include media('<tablet') {
                    height: auto;
                    width: auto;
                    max-width: inherit;
                }

                @include media('<mobile') {
                    height: 100%;
                }
            }
        }

        &.layout__left--split {
            .promo-area__content {
                grid-column: 2 / 12;
                margin-right: -$measure--medium3;

                @include media('<desktop') {
                    grid-column: 1 / 12;
                }

                @include media('<small') {
                    grid-column: 1 / 14;
                }

                @include media('<tablet') {
                    margin-right: inherit;
                }
            }

            .promo-area__image {
                grid-column: 11 / 24;
                margin-top: -$measure--medium7;
                padding-left: $measure--small4;

                @include media('<desktop') {
                    grid-column: 11 / 25;
                }

                @include media('<small') {
                    grid-column: 8 / 25;
                }

                @include media('<between') {
                    grid-column: 11 / 25;
                    padding-left: 0;
                }
            }
        }

        &.layout__right--split {
            .promo-area__content {
                grid-column: 14 / 24;
                margin-left: -$measure--medium3;

                @include media('<desktop') {
                    grid-column: 14 / -1;
                }

                @include media('<small') {
                    grid-column: 12 / -1;
                }

                @include media('<tablet') {
                    margin-left: inherit;
                }
            }

            .promo-area__image {
                grid-column: 2 / 15;
                margin-top: -$measure--medium7;
                padding-right: $measure--small4;

                @include media('<desktop') {
                    grid-column: 1 / 15;
                }

                @include media('<small') {
                    grid-column: 1 / 18;
                }

                @include media('<=between') {
                    grid-column: 1 / 15;
                    padding-right: 0;
                }
            }
        }

        &.layout__left--split,
        &.layout__right--split {
            margin-top: $measure--medium7;
            margin-bottom: 0;

            @include media('<tablet') {
                margin-top: 0;
            }

            .promo-area__content {
                margin-top: 0;
                margin-bottom: 0;

                @include media('<tablet') {
                    grid-column: 2 / 24;
                    padding: $measure--basic 0 0 0;

                    .promo-area__heading {
                        margin-right: $measure--small1;
                        margin-left: $measure--small1;
                    }

                    .promo-area__container {
                        @include media('<=mobile') {
                            padding: $measure--small2;
                        }

                        @include media('>mobile') {
                            padding: $measure--medium1;
                        }
                    }

                    .promo-area__text {
                        p:first-of-type {
                            margin-top: 0;
                        }
                    }

                    .promo-area__image--mobile {
                        text-align: center;
                        display: inline;

                        img {
                            @include media('<tablet') {
                                width: 100%;
                            }
                        }
                    }
                }
            }

            .promo-area__image {
                @include media('<tablet') {
                    display: none;
                }

                figure,
                picture {
                    height: 100%;
                    margin: 0;
                }

                img {
                    height: auto;
                    width: 100%;
                    max-width: inherit;
                }
            }
        }

        /// general all layout styles
        .promo-area__content {
            padding: $measure--medium1;
            margin-top: $measure--medium8;
            z-index: z-index('above');
            color: $color-purewhite;
            margin-bottom: -$measure--small4;

            p {
                @include font-family('heading');
                @include heading-font-features();
                @include betatype();
            }

            @include media('<=tablet') {
                margin-top: $measure--small4;
                margin-bottom: $measure--small4;
            }

            .promo-area__heading {
                margin-top: 0;
                font-weight: $font-weight-book;
                margin-bottom: $measure--basic;
            }

            .promo-area__text__details {
                margin-top: $measure--small3;

                p {
                    @include font-family('default');
                    @include body-font-features();
                    @include alphatype();
                }

                span {
                    margin-right: $measure--half;
                }
            }

            .promo-area__cta {
                display: inline-block;
                margin-top: $measure--basic;

                @include media('<between') {
                    display: block;

                    ul {
                        max-width: inherit;
                    }
                    a {
                        display: block;
                    }
                }
            }

            &.background--pink90,
            &.background--red60,
            &.background--teal70 {
                .cta-block__cta a {
                    @include cta-outlined-dark-bg();
                }
            }

            &.background--brown10 {
                color: $color-gray10;

                .promo-area__text__details {
                    span {
                        color: $color-red60;
                    }
                }

                .cta-block__cta a {
                    @include cta-outlined-light-bg();
                }
            }
        }

        .promo-area__image--mobile {
            display: none;
        }

        .promo-area__image {
            height: 100%;
            overflow: hidden;
            position: absolute;
            width: 100%;
            z-index: z-index('default');

            figure {
                margin-top: 0;
            }

            picture {
                display: block;
                height: 100%;
            }

            @include media('<=tablet') {
                figure {
                    height: 100%;
                }
            }
        }
    }
}
