main {
    @include media('>=small') {
        .content-hub {
            grid-column: 1 / span 24;
        }

        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .content-hub {
                grid-column: 7 / span 18;
            }
        }
    }
}

.content-hub__block-one {
    grid-template-rows: auto;
    background-image: linear-gradient(to top, $color-purewhite, $color-gray1);

    > .content {
        grid-area: main;
        margin: 0;
        display: grid;
        grid-template-rows: auto;

        @include media('<=mobile') {
            grid-template-columns: repeat(6, 1fr);
        }

        @include media('>mobile') {
            grid-template-columns: repeat(12, 1fr);
        }

        @include media('>between') {
            @include standard-grid();
        }
    }
}

.content-hub {
    .banner,
    .banner-text {
        .cta-block {
            display: flex;
            grid-column: 1 / span 24;
            justify-content: center;
            margin-top: $measure--small4;
            max-width: unset;
            text-align: center;

            .cta-block__cta {
                a {
                    @include cta-outlined-icon-dark-bg();
                }
            }
        }
    }
}

.content-hub__block-one .banner {
    @include standard-grid();

    grid-column: 1 / -1;
}

.content-hub .banner__standfirst {
    grid-column: 1 / -1;
    background-color: $color-red70;
    color: $color-purewhite;

    @include media('>=tablet') {
        grid-column: 3 / -3;
    }

    h1 {
        text-align: center;
    }

    .standfirst {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}

.content-hub__block-one .subject-finder {
    @include standard-grid();

    @include media('<=tablet') {
        padding: 0 0;
        @include default-margin();
    }

    grid-column: 1/-1;
    padding-bottom: $measure--medium3;
    margin-top: $measure--basic;
}

.subject-finder.subject-finder--columns-2 {
    .subject-finder__subject-description {
        grid-column: 3/13;

        @include media('<=tablet') {
            grid-column: 1/-1;
            padding: 0;
        }
    }

    .embedded-video {
        grid-column: 13/-3;
        padding-bottom: 0;
        padding-top: 0;

        @include media('<=tablet') {
            grid-column: 1/-1;
            padding: 0;
        }

        .youtube-video {
            padding-left: 0;
        }
    }

    .subject-description--image {
        grid-column: 13/-3;

        @include media('<=tablet') {
            grid-column: 1/-1;
            padding: 0;
        }

        picture {
            display: flex;

            img {
                flex-basis: 100%;
            }
        }
    }
}

.subject-finder.subject-finder--columns-1 {
    .subject-finder__subject-description {
        grid-column: 7/19;

        @include media('<=tablet') {
            grid-column: 1/-1;
            padding: 0;
        }
    }
}

.content-hub__block-one .explore {
    grid-column: 3/-3;
    margin-top: $measure--small4;

    @include media('<=tablet') {
        grid-column: 1/-1;
        padding: 0 0 $measure--small4 0;
    }

    .explore__heading {
        text-align: center;
        margin: 0;
    }
}

.content-hub__block-one .cta-like-anchor-block {
    .cta-like-anchor {
        a {
            justify-content: center;
            text-align: center;
            &:hover,
            &:active {
                color: $color-nearblack;
            }
        }
    }
}

.content-hub__block-two {
    margin-left: 0;
    margin-right: 0;
}

.content-hub__block-two {
    .careers {
        background-color: $color-brown10;
        padding: $measure--basic 0;
        margin-bottom: 0;

        @include media('>=tablet') {
            @include standard-grid;
        }
        @include media('>=between') {
            margin-bottom: 0;
        }

        .careers__intro {
            @include media('<tablet') {
                @include default-margin;
            }
        }

        .pathway-cards {
            @include media('<tablet') {
                @include default-margin;
            }

            .pathway {
                margin-top: initial;
                padding-top: initial;

                .pathway__cards {
                    padding-left: 0;
                    padding-right: 0;
                    grid-template-columns: repeat(3, 1fr);
                }
            }
        }

        .card--pathway {
            a {
                @include media('>tablet') {
                    @include media('hover') {
                        &:hover {
                            background-color: $color-purewhite;
                        }
                    }

                    &:active,
                    &:focus {
                        background-color: $color-purewhite;
                    }

                    .card--pathway__content {
                        min-height: 15rem;
                    }
                }
                @include media('<=small') {
                    background-color: $color-purewhite;
                }
            }
        }
    }
}

.content-hub__block-two .careers-accordions {
    background-color: $color-brown10;
    position: relative;
    overflow: hidden;

    @include media('>=tablet') {
        @include standard-grid;
    }

    .careers-accordions--content {
        @include media('>=tablet') {
            grid-column: 3 / 23;
        }

        @include media('<=tablet') {
            @include default-margin;
        }
    }

    & > h2 {
        margin-top: $measure--small4;

        @include media('>=tablet') {
            grid-column: 3 / 23;
        }

        @include media('<=tablet') {
            @include default-margin;
        }
    }

    .accordion {
        position: relative;
        z-index: z-index('above');
        margin-bottom: $measure--small4;

        @include media('>=tablet') {
            grid-column: 3 / 23;
        }

        @include media('<=tablet') {
            @include default-margin;
        }
    }

    .accordion__heading {
        button {
            background: $color-purewhite;
            color: $color-nearblack;
        }

        &[data-open='true'] {
            button {
                background: $color-pink90;
                color: $color-purewhite;
                border-color: $color-purewhite;

                &:focus {
                    border-color: $color-purewhite;
                }

                @include media('hover') {
                    &:hover {
                        background: $color-pink90;
                        color: $color-purewhite;
                    }
                }
            }

            & + .accordion__body {
                margin-bottom: $measure--basic;
                border-color: $color-purewhite;
            }

            & + .accordion__body:last-child {
                margin-bottom: 0;
            }
        }
    }

    .accordion__body {
        background: $color-pink90;
        color: $color-purewhite;
    }

    .pathway {
        .pathway__cards {
            .card--pathway a {
                @include pathway-card(
                    $card-text-color: $color-purewhite,
                    $card-text-hover-color: $color-nearblack,
                    $card-background-color: transparent,
                    $card-background-hover-color: $color-purewhite,
                    $card-heading-color: $color-purewhite,
                    $section-title-color: $color-purewhite,
                    $border-color: $color-pink80,
                    $outline-color: $color-purewhite
                );

                @include media('hover') {
                    &:hover {
                        &:visited {
                            .card--pathway__content__heading,
                            p,
                            ul {
                                color: $color-nearblack;
                            }
                            .card--pathway__content__heading__text {
                                border-bottom-color: $color-nearblack;
                            }
                        }
                    }
                }

                @include media('<=small') {
                    color: $color-nearblack;

                    .card--pathway__content__heading,
                    p,
                    ul {
                        color: $color-nearblack;
                    }

                    .card--pathway__content__heading__text {
                        border-bottom-color: $color-nearblack;
                    }

                    &:visited {
                        .card--pathway__content__heading,
                        p,
                        ul {
                            color: $color-nearblack;
                        }
                        .card--pathway__content__heading__text {
                            border-bottom-color: $color-nearblack;
                        }
                    }
                }

                .card--pathway__content {
                    min-height: 15rem;
                }
            }

            a.card--pathway__content--border {
                border-top: $measure--half solid $color-pink80;
                max-width: unset;
            }
        }
    }
}

.content-hub__block-two .careers__intro {
    @include media('>=tablet') {
        grid-column: 3 / 23;
    }
}

.content-hub__block-two .pathway-cards {
    @include media('>=tablet') {
        grid-column: 5 / 21;
    }
    @include media('>between') {
        grid-column: 3 / 23;
    }
    @include media('>desktop') {
        grid-column: 7 / 19;
    }
}

.content-hub__block-two .pathway-cards {
    @include media('>=tablet') {
        grid-column: 3 / -3;
    }

    @include media('<tablet') {
        grid-column: 1/-1;
    }

    .card--pathway__block {
        @include media('>=tablet') {
            @include standard-grid;
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

.content-hub__block-two .outcomes {
    grid-area: outcomes;
    margin: $measure--small4 0;
}

.content-hub__block-two .outcomes h2:nth-of-type(1) {
    text-align: center;
}

.content-hub__block-two .swiper-viewport {
    padding: $measure--basic 0;
    grid-column: 1/-1;
    .swiper-container {
        grid-column: 1/-1;
    }
    @include media('<tablet') {
        padding: $measure--basic 0 $measure--basic;
    }
}

.content-hub__block-two .gallery {
    @include media('>=tablet') {
        @include standard-grid;
    }
    @include media('<tablet') {
        @include default-margin;
    }
    .section-title {
        border-bottom: $border-width-hairline solid $color-gray7;
        padding-bottom: $measure--basic;
        grid-column: 3/-3;
        margin: $measure--basic 0 0;
    }
}

.content-hub__block-two .testimonial {
    @include media('>=tablet') {
        @include standard-grid;
    }
    @include media('<=tablet') {
        @include default-margin;
    }

    h2 {
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 2/-2;
        }
        @include media('>tablet', '<small') {
            @include default-margin;
        }
    }

    .related-content {
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 2/-2;
        }
        justify-content: initial;
    }

    .card--testimonial-feature__details__column {
        &:first-child {
            @include media('<=mobile') {
                display: none;
            }
        }
    }

    .card--testimonial__quote {
        @include gammatype();
        @extend .meta;
        font-weight: $font-weight-light;
    }
}

.content-hub__block-two .testimonial .slider {
    display: grid;
}

.content-hub__block-two .testimonial .slider li {
    grid-column: 1/-1;
}

.content-hub__block-three .news-events .related-content .card--event,
.content-hub__block-three .news-events .related-content .card--news {
    break-inside: avoid;
    height: fit-content;
    box-shadow: none;
}

.content-hub__block-three .news-events .related-content .card img {
    transition: none;
}

.content-hub__block-three .news-events {
    .content-hub__block-three__happening:not(:empty) {
        padding-bottom: $measure--small4;
    }
}

.content-hub__block-three {
    .news-events {
        @include media('>tablet') {
            @include standard-grid;
        }

        @include media('<=tablet') {
            padding-top: $measure--quarter;
        }

        .content-hub__block-three__happening {
            @include standard-grid;
            display: grid;
            grid-column: 1 / -1;

            &:not(:empty) {
                padding-bottom: $measure--small4;
            }

            .related-content__heading {
                grid-column: 1 / -1;

                @include media('<=tablet') {
                    @include default-margin;
                }
            }

            .related-content {
                grid-column: 1 / -1;

                @include media('<=tablet') {
                    @include default-margin;
                }

                @include media('>=tablet') {
                    grid-column: 2 / 24;
                    margin-left: 0;
                    margin-right: 0;
                }
            }

            .slider__controls {
                grid-column: 1 / -1;
                margin-right: $measure--basic;
            }
        }

        .content-hub__block-three__research__centres {
            grid-column: 1 / -1;

            @include media('>=tablet') {
                grid-column: 2 / 10;
            }

            @include media('<=tablet') {
                @include default-margin;
            }

            a {
                @include anchor-inline($color-gray10);
                font-weight: $font-weight-medium;
            }
        }

        .content-hub__block-three__research {
            margin-bottom: $measure--small4;
            h2 {
                @include media('<tablet') {
                    @include standard-box;
                }
            }

            .related-content {
                grid-column: 1 / -1;

                @include media('>=tablet') {
                    grid-column: 10 / 18;
                }

                @include media('<=between') {
                    margin-bottom: $measure--medium3;
                }
            }
        }
    }
}

.content-hub__block-three {
    .news-events {
        h2 {
            @include media('<=tablet') {
                @include default-margin;
            }

            @include media('>=tablet') {
                grid-column: 2 / span 11;
            }

            @include media('>=small') {
                grid-column: 2 / span 11;
            }
        }
        .content-hub__block-three__research {
            @include media('>=tablet') {
                grid-column: 6 / span 14;
            }

            @include media('>=small') {
                grid-column: 2 / span 11;
            }

            h2:nth-of-type(1) {
                grid-column: 1 / -1;
                margin-bottom: $measure--basic;
            }
        }

        .content-hub__block-three__case-study-card {
            @include media('<=tablet') {
                @include default-margin;
                padding-top: $measure--basic;
            }
            @include media('>=tablet') {
                grid-column: 6 / span 14;
            }

            @include media('>=small') {
                grid-column: 13 / span 11;
            }
        }
    }

    .content-hub__block-three__research__centres__fill-column {
        .content-hub__block-three__research {
            grid-column: 2 / span 22;

            .content-hub__block-three__research__centres {
                ul {
                    @include media('>=tablet') {
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        max-width: unset;

                        li {
                            margin-left: $measure--threequarters;
                        }
                    }
                }
            }
        }
    }
}

.content-hub__block-three
    .news-events
    .content-hub__block-three__research__centres__title {
    text-align: left;
}

.content-hub__block-three
    .news-events
    .content-hub__block-three__happening
    h2.section-title,
.content-hub__block-three
    .news-events
    .content-hub__block-three__happening
    h2.related-content__heading {
    text-align: center;
    margin-bottom: $measure--basic;
}

.js {
    .subject__course-list {
        padding: $measure--small4;
        background-color: $color-teal10;
        border-top: $measure--basic solid $color-teal40;

        .paginated-list {
            max-width: initial;
            .page {
                max-width: initial;

                @include media('<tablet') {
                    margin-top: $measure--half;
                }
            }
        }
    }
    .testimonial .related-content[data-style='arrows']:not(.slider) {
        grid-template-columns: none;
    }
}

.subject__course-list {
    h3 {
        margin-top: initial;
    }

    .paginated-list {
        padding-bottom: $measure--basic;
    }

    .pagination__controls {
        padding-top: $measure--basic;

        button {
            background-color: $color-purewhite;

            &:hover,
            &:active,
            &:focus {
                background-color: $color-purewhite;
            }
        }
    }

    ul:not(.paginated-list) {
        @include media('>tablet') {
            max-width: initial;
            columns: 2;
            column-gap: $measure--medium7;
        }

        li:first-child {
            margin-top: initial;
        }
    }
}
