@mixin linked-img() {
    margin-top: 0;
    margin-left: 0;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: flex-start;
    max-width: none;

    // Different widths depending on logos per row
    &[data-logos-row='2'] {
        li {
            width: 50%;
            &:nth-child(odd) {
                padding-left: 0;
            }
            &:nth-child(even) {
                padding-right: 0;
            }
        }
    }

    &[data-logos-row='3'] {
        li {
            width: 33%;
            @include media('>=small') {
                &:nth-child(3n-2) {
                    padding-left: 0;
                }
                &:nth-child(3n) {
                    padding-right: 0;
                }
            }
            @include media('<small') {
                width: 50%;
                &:nth-child(odd) {
                    padding-left: 0;
                }
                &:nth-child(even) {
                    padding-right: 0;
                }
            }
        }
    }

    li {
        margin-top: $measure--half;
        padding: $measure--threequarters;

        a {
            @include anchor-img;
            overflow: hidden;
            background-image: none;
            background-size: 0;
            transition: none;
            display: block;
            border: $border-width-hairline $border-style-solid $color-linkblue;

            .linked-image {
                position: relative;
                padding: $measure--basic;

                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                    background: transparent;
                    transition: background-color 0.3s $transition-function;
                }

                &:after {
                    font-family: 'Font Awesome 6 Pro';
                    font-weight: $font-weight-normal;
                    content: '\f14c';
                    position: absolute;
                    background: transparent;
                    color: $color-linkblue;
                    bottom: 0;
                    right: $border-width-default;
                    -webkit-font-smoothing: antialiased;
                    display: inline-block;
                    font-style: normal;
                    font-variant: normal;
                    text-rendering: auto;
                    line-height: 1;
                    font-size: $font-size--icon;
                    transition: all 0.3;
                }
            }

            &:hover {
                @include media('hover') {
                    border-bottom: $border-width-hairline $border-style-solid
                        $color-linkblue;
                    background-image: none;
                    background-size: 0;
                    transition: none;

                    .linked-image {
                        &:before {
                            background-color: rgba($color-gray7, 0.1);
                            z-index: z-index('above');
                        }

                        &:after {
                            content: '\f14c';
                            font-weight: $font-weight-extrablack;
                            transition: all 0.3;
                        }
                    }
                }
            }

            &:active {
                .linked-image {
                    &:after {
                        content: '\f14c';
                        font-weight: $font-weight-extrablack;
                    }
                    &:before {
                        background-color: rgba($color-gray7, 0.1);
                    }
                }
            }

            &:focus,
            &:active {
                border-bottom: $border-width-hairline $border-style-solid
                    $color-linkblue;
            }

            img {
                display: block;
                object-fit: cover;
                transition: all 0.3s $transition-function;
                margin: auto;
            }

            &:visited {
                border-bottom: $border-width-hairline $border-style-solid
                    $color-linkblue;
            }
        }

        &[data-link='false'] {
            img {
                padding: $measure--basic;
                border: $border-width-hairline $border-style-solid $color-gray2;
            }
        }
    }
}
