.card--module {
    .card__details {
        border-color: $color-gray5;
        border-style: $border-style-solid;
        border-width: $border-width-hairline;
        display: block;

        @include media('<=between') {
            background-color: $color-gray1;
        }

        .card__details__text {
            padding-bottom: $measure--basic;
        }

        .card__type {
            color: $color-red50;
        }

        .card__tag {
            .icon {
                color: $color-red50;
            }
        }

        .card--module__code {
            @include font-family('default');
            @include body-font-features();

            border-right-width: $border-width-hairline;
            border-right-style: $border-style-solid;
            border-right-color: $color-gray4;
            font-weight: $font-weight-book;
            margin-right: $measure--half;
            padding-right: $measure--half;
        }
    }
}
