.navigation--secondary__header {
    display: none;
}

.navigation--secondary {
    display: none;

    @include media('>=small') {
        display: block;
        margin-top: $measure--small2;

        .navigation__level2,
        .navigation__level3 {
            background-color: $color-gray1;
        }

        .navigation__level3,
        .navigation__level4,
        .navigation__level5,
        .navigation__level6 {
            list-style: none;
            margin-left: 0;
            margin-top: 0;
            max-width: unset;

            & > li > .navigation__controls {
                & > button {
                    align-self: center;
                    margin-left: $measure--threequarters;
                    margin-right: $measure--threequarters;
                    padding-bottom: $measure--quarter;
                    padding-left: $measure--quarter;
                    padding-right: $measure--quarter;
                    padding-top: $measure--quarter;
                }
            }

            li {
                &.navigation__current > .navigation__controls > a > span {
                    display: inline;
                    border-bottom: $border-width-default $border-style-solid
                        $color-red50;
                }
            }
        }

        .navigation__level3,
        .navigation__level4 {
            li {
                @include content-style();
                @include smalltype();

                max-width: unset;

                &.navigation__hierarchy > .navigation__controls > a {
                    span {
                        border-bottom: $border-width-default $border-style-solid
                            $color-nearblack;
                    }
                }

                &.navigation__current > .navigation__controls > span:focus {
                    outline: none;

                    > span {
                        @include outline-properties($color-nearblack);
                    }
                }
            }
        }

        .navigation__level5,
        .navigation__level6 {
            li {
                @include content-style();
                @include smalltype();

                font-weight: $font-weight-text;
                max-width: unset;

                &.navigation__hierarchy > .navigation__controls > a {
                    span {
                        border-bottom: $border-width-default $border-style-solid
                            $color-nearblack;
                    }
                }

                &.navigation__current > .navigation__controls > span:focus {
                    outline: none;

                    > span {
                        @include outline-properties($color-nearblack);
                    }
                }
            }
        }

        .navigation__level3 {
            color: $color-nearblack;

            > li {
                line-height: $line-height-ratio--body;
            }

            & > li > .navigation__controls {
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray4;

                & > a {
                    @include navigation-anchor(
                        $color-nearblack,
                        $color-gray1,
                        $color-nearblack,
                        $color-gray2,
                        $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray1,
                        $color-gray2,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: $measure--half;
                }
            }
        }

        .navigation__level4 {
            background-color: $color-gray2;
            color: $color-nearblack;

            > li {
                line-height: $line-height-ratio--body;
            }

            & > li > .navigation__controls {
                & > a {
                    @include navigation-anchor(
                        $color-nearblack,
                        $color-gray2,
                        $color-nearblack,
                        $color-gray4,
                        $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray2,
                        $color-gray4,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{2 * $measure--half};
                }
            }
        }

        .navigation__level5 {
            background-color: $color-gray3;
            color: $color-nearblack;

            & > li > .navigation__controls {
                & > a {
                    @include navigation-anchor(
                        $color-nearblack,
                        $color-gray3,
                        $color-nearblack,
                        $color-gray5,
                        $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray3,
                        $color-gray5,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{3 * $measure--half};
                }
            }
        }

        .navigation__level6 {
            background-color: $color-gray4;
            color: $color-nearblack;

            & > li > .navigation__controls {
                & > a {
                    @include navigation-anchor(
                        $color-nearblack,
                        $color-gray4,
                        $color-nearblack,
                        $color-gray6,
                        $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray4,
                        $color-gray6,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{4 * $measure--half};
                }
            }
        }

        .navigation__level7 {
            background-color: $color-gray5;
            color: $color-nearblack;

            & > li > .navigation__controls {
                & > a {
                    @include navigation-anchor(
                        $color-nearblack,
                        $color-gray5,
                        $color-nearblack,
                        $color-gray7,
                        $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray5,
                        $color-gray7,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{5 * $measure--half};
                }

                a > span {
                    display: inline;
                    border-bottom: $border-width-default $border-style-solid
                        $color-red60;
                }
            }
        }

        & > .navigation--secondary__header {
            @include h6style();

            background-color: $color-gray1;
            color: $color-nearblack;
            display: block;
            line-height: $line-height-ratio--body;
            margin-top: 0;

            a {
                @include navigation-anchor(
                    $color: $color-nearblack,
                    $background: $color-gray1,
                    $hover-color: $color-nearblack,
                    $hover-background: $color-gray2,
                    $outline: $color-nearblack
                );

                &,
                &:active,
                &:focus,
                &:hover {
                    border-bottom: $border-width-hairline $border-style-solid
                        $color-gray4;
                }
            }

            a > span {
                border-bottom: $border-width-default $border-style-solid
                    $color-nearblack;
            }

            a,
            & > span {
                display: block;
                padding-bottom: $measure--half;
                padding-left: $measure--half;
                padding-right: $measure--half;
                padding-top: $measure--half;
                width: 100%;
            }

            & > span {
                @include anchor-default-outline($color-nearblack);

                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray4;
            }

            &.navigation--secondary__header--current {
                & > a {
                    background-color: $color-gray1;
                    & > span {
                        border-bottom: $border-width-default $border-style-solid
                            $color-red50;
                    }
                }
            }
        }

        .navigation__controls {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
        }

        a,
        .navigation__current > .navigation__controls > span {
            display: block;
            padding: $measure--half;
            width: 100%;
        }

        li {
            margin: 0;
        }
    }
}

.js .navigation--secondary {
    .navigation__level4,
    .navigation__level5,
    .navigation__level6 {
        display: none;
    }

    li {
        &[data-open='true'] {
            & > .navigation__controls > a > span,
            & > .navigation__controls > span > span {
                font-weight: $font-weight-bold;
            }

            & > ul {
                display: block;
            }
        }
    }

    button {
        .navigation__button__icon {
            &:before {
                @include font-family('sharp');
                content: '\f067';
                font-weight: $font-weight-light;
            }
        }

        .navigation__button__text {
            @include screenreader-only();
        }

        &[aria-expanded='true'] {
            .navigation__button__icon {
                &:before {
                    content: '\f068';
                }
            }
        }
    }
}
