.global-header__search {
    @include media('<small') {
        margin: $measure--half;
    }

    @include media('>=small') {
        width: 100%;
    }

    .global-header__search__wrapper {
        @include content-style();

        align-items: center;
        display: inline-flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        margin-top: 0;

        @include media('<small') {
            @include betatype();

            flex-direction: column-reverse;
        }
    }

    > a,
    .global-header__search__button {
        transition: transform 0.3s ease;

        @include media('<small') {
            @include cta--border--subelement(
                transparent,
                $color-nearblack,
                $color-gray6,
                $color-nearblack,
                $color-purewhite,
                $color-nearblack,
                $color-purewhite,
                square
            );

            display: flex;
            height: $measure--medium7;
            padding-bottom: 0;
            padding-left: 0;
            padding-right: 0;
            padding-top: 0;
            width: $measure--medium7;
        }

        &:active,
        &:focus {
            @include outline-properties($color-nearblack);
        }

        .global-header__search__text {
            @include smalltype();

            font-weight: $font-weight-book;
        }

        @include media('<small') {
            justify-content: center;

            .global-header__search__icon {
                @include epsilontype();
            }
        }

        @include media('>=small') {
            display: flex;
            height: 100%;

            &:hover {
                @include media('hover') {
                    transform: scale(1.2);
                }
            }

            .global-header__search__icon {
                @include epsilontype();
                color: $color-red50;
            }

            .global-header__search__text {
                display: none;
            }
        }

        @include media('>=desktop') {
            @include h5style();
            margin-top: 0;
        }

        align-items: center;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-end;
    }
}

.js .global-header__search {
    .global-header__search__button[aria-expanded='false'] {
        & + .global-header__search__modal {
            display: none;
        }
    }

    .global-header__search__button[aria-expanded='true'] {
        @include media('<small') {
            @include cta--basic--subelement(
                $color-purewhite,
                $color-nearblack,
                $color-purewhite,
                $color-gray10,
                $color-purewhite,
                square
            );

            border: 0;
            margin-right: $measure--half;
            margin-top: $measure--half;
            padding-left: 0;
            padding-right: 0;
            padding-top: 0;
        }

        &:active,
        &:focus {
            @include outline-properties($color-purewhite);
        }

        .global-header__search__icon {
            color: $color-purewhite;
        }

        & + .global-header__search__modal {
            @include box-shadow();

            background: $color-nearblack;
            color: $color-purewhite;
            display: block;
            left: 0;
            min-height: 100vh;
            position: absolute;
            right: 0;
            top: 0;
            z-index: z-index('navigation');
            width: 100vw;
            transform: translate(-50%, 0%);
            left: 50%;

            .global-header__search__modal__header {
                @include media('<small') {
                    align-items: flex-start;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    justify-content: space-between;
                }

                @include media('>=small') {
                    @include standard-grid();
                    @include standard-box();

                    grid-template-rows: repeat(3, 1fr);
                }

                @include media('>=desktop') {
                    grid-row-gap: $measure--basic;
                    grid-template-rows: repeat(2, 1fr);
                }

                .global-header__search__button {
                    @include media('>=small') {
                        grid-column: 24 / span 1;
                        margin-top: $measure--basic;
                    }
                }
            }

            .global-header__search__modal__body {
                @include default-padding();

                padding-bottom: $measure--small4;

                @include media('>=small') {
                    @include standard-grid();
                    @include standard-box();
                }

                h2 {
                    @include h2style();

                    margin-top: $measure--small4;
                    text-align: center;
                    grid-column: span 24;
                }

                h3 {
                    @include h3style();

                    margin-top: $measure--small2;
                }

                form {
                    grid-column: 4 / span 18;
                    margin-top: $content-element-margin;
                }
            }

            .global-header__search__modal__body__results {
                grid-column: 4 / span 18;

                @include media('>=small') {
                    @include content-grid();

                    .search--inline__suggestions,
                    .search--inline__results__updating--suggestions {
                        grid-column: 1 / span 6;

                        &:only-child {
                            grid-column: 7 / span 6;
                        }
                    }

                    .search--inline__topresults,
                    .search--inline__results__updating--topresults {
                        grid-column: 7 / span 12;

                        &:only-child {
                            grid-column: 4 / span 12;
                        }
                    }
                }

                .search--inline__topresults,
                .search--inline__results__updating--topresults {
                    ul {
                        list-style: none;
                        margin-top: $content-element-margin;
                    }

                    .card--search {
                        padding-bottom: $measure--basic;

                        a {
                            @include anchor-reset();

                            background-color: transparent;
                            color: $color-purewhite;

                            &:hover {
                                @include media('hover') {
                                    color: $color-purewhite;
                                }
                            }

                            &:active,
                            &:focus {
                                @include outline-properties($color-purewhite);

                                color: $color-purewhite;
                            }

                            .card__heading {
                                @include h4style();

                                background-image: linear-gradient(
                                    120deg,
                                    $color-purewhite 0%,
                                    $color-purewhite 100%
                                );
                                background-position: 0 100%;
                                background-repeat: no-repeat;
                                background-size: 100% 0;
                                border-bottom-color: $color-purewhite;
                                border-bottom-style: $border-style-solid;
                                border-bottom-width: $border-width-default;
                                display: inline;
                                max-width: $max-line-length;
                                transition: background-size $transition-duration
                                    $transition-function;

                                @include media('reducemotion') {
                                    transition: none;
                                }
                            }

                            &:hover {
                                @include media('hover') {
                                    .card__thumbnail {
                                        transform: scale(1.02);
                                    }

                                    .card__heading {
                                        background-size: 100% 100%;
                                        background-image: linear-gradient(
                                            120deg,
                                            $color-purewhite 0%,
                                            $color-purewhite 100%
                                        );
                                        color: $color-nearblack;
                                    }
                                }
                            }
                        }

                        .card__label {
                            @include smalltype();

                            color: $color-gray4;
                            font-style: italic;
                        }

                        .card__details {
                            display: flex;
                            flex-wrap: nowrap;

                            @include media('<small') {
                                flex-direction: column;
                            }

                            @include media('>=small') {
                                flex-direction: row;
                            }
                        }

                        .card__thumbnail + .card__details__text {
                            @include media('<small') {
                                padding-left: 0;
                                padding-top: $measure--basic;
                            }

                            @include media('>=small') {
                                padding-left: $measure--basic;
                                padding-top: 0;
                            }
                        }

                        .card__details__text {
                            width: 100%;
                        }

                        .card__description {
                            @include content-style();

                            margin-top: $content-element-margin;
                            max-width: $max-line-length;
                        }

                        .card__url {
                            @include smalltype();

                            margin-top: $measure--basic;
                            max-width: unset;
                            color: $color-gray1;
                            word-break: break-word;
                        }

                        .card__thumbnail {
                            align-self: flex-start;
                            flex-shrink: 0;
                            height: auto;
                            transition: transform $transition-duration
                                ease-in-out;
                            width: $measure--huge5;

                            @include media('reducemotion') {
                                transition: none;
                            }
                        }

                        & + .card--search {
                            border-top: $border-width-hairline
                                $border-style-solid $color-gray4;
                            padding-top: $measure--basic;
                        }
                    }
                }
            }

            .header-logo {
                @include media('<small') {
                    align-self: center;
                    justify-content: flex-start;
                    padding-bottom: $measure--half;
                    padding-left: $measure--half;
                    padding-right: $measure--half;
                    padding-top: $measure--half;
                    width: $measure--huge5;
                }

                @include media('>=small') {
                    grid-column-end: span 3;
                    grid-column-start: 1;
                    grid-row-end: span 2;
                    grid-row-start: 1;
                }

                a {
                    @include anchor-img();

                    display: inline-block;
                    width: 100%;

                    @include media('<small') {
                        height: $measure--medium7;
                    }

                    @include media('>=small') {
                        margin-bottom: $measure--small2;
                        margin-top: $measure--small2;
                    }

                    &:active,
                    &:focus {
                        outline-color: $color-purewhite;
                    }

                    .logo {
                        width: 100%;

                        @include media('<small') {
                            height: 100%;
                        }
                    }
                }
            }
        }
    }

    .veil--search {
        @include veil();

        &[data-on='true'] {
            position: fixed;
        }
    }

    .search--inline__query__icon--wrapper {
        background-color: $color-purewhite;
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--basic;
        padding-top: #{$measure--quarter + $border-width-hairline};

        @include media('<tablet') {
            display: none;
        }
    }

    .search--inline__query {
        > div {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            button {
                @include cta--basic--subelement(
                    $color: $color-purewhite,
                    $background: $color-red50,
                    $hover-color: $color-purewhite,
                    $hover-background: $color-red70,
                    $outline: $color-purewhite,
                    $corners: square,
                    $class: 'search--inline__query__submit__content'
                );

                @include media('<small') {
                    margin-left: $measure--basic;

                    .search--inline__query__submit__text {
                        @include sr-only();
                    }
                }

                @include media('>=small') {
                    margin-left: $measure--small2;

                    .search--inline__query__submit__icon {
                        display: none;
                    }
                }
            }
        }
    }

    .search--inline__icon {
        background-color: $color-purewhite;
        color: $color-gray7;
        font-weight: $font-weight-text;
        margin-bottom: $measure--half;
        margin-top: $measure--threequarters;
    }

    .search--inline__query__input {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        @include media('>=tablet') {
            @include gammatype();
        }

        background-color: $color-purewhite;
        display: flex;
        flex-grow: 1;
        font-weight: $font-weight-text;
        padding: 0;
        position: relative;

        &:focus-within {
            @include outline-properties($color-purewhite);
        }

        input {
            @include font-family('default');
            @include body-font-features();
            @include gammatype();

            border: $border-width-hairline solid transparent;
            font-style: normal;
            height: 100%;
            min-width: 0; // Constrains width in Firefox
            padding-bottom: $measure--half;
            padding-left: $measure--half;
            padding-right: $measure--medium5;
            padding-top: $measure--half;
            width: 100%;

            &:focus {
                color: $color-gray10;
                font-style: normal;
                font-weight: $font-weight-medium;
                outline: 0;
            }

            &::placeholder {
                font-style: italic;
            }

            &:placeholder-shown {
                color: $color-gray7;
            }
        }

        .search--inline__clear {
            @include epsilontype();

            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            height: $measure--small5;
            justify-content: center;
            position: absolute;
            right: $measure--half;
            top: $measure--quarter;
            width: $measure--small5;
            background-color: initial;
            padding: 0;

            &:focus {
                @include outline-properties();

                outline-offset: -#{$border-width-thicker};
            }

            .search--inline__clear__text {
                @include sr-only();
            }

            .icon {
                /*
                // temp overide with xmark-temp-styling below

                --fa-primary-color: #{$color-gray7};
                --fa-primary-opacity: 1;
                --fa-secondary-color: #{$color-gray1};
                --fa-secondary-opacity: 1;
                */
                letter-spacing: 0;
                line-height: 1;
                @include xmark-temp-styling;
            }

            &:hover {
                background-color: transparent;

                .icon {
                    /*
                    // temp overide with xmark-temp-styling below

                    --fa-primary-color: #{$color-gray1};
                    --fa-secondary-color: #{$color-gray7};
                    */
                    @include xmark-temp-styling-hover;
                }
            }
        }
    }

    .search--inline__results__updating {
        @include gammatype();

        margin-bottom: $heading-margin;
        margin-top: $heading-margin;

        .icon {
            color: $color-purewhite;
        }
    }

    .search--inline__suggestions {
        ul {
            list-style: none;
            margin-left: 0;
            max-width: $max-line-length;
        }

        li {
            @include content-style();
            @include betatype();

            margin-top: $content-element-margin;
        }

        a {
            @include anchor-inline(
                $color: $color-purewhite,
                $visited: $color-purewhite,
                $hover: $color-nearblack,
                $outline: $color-purewhite
            );

            font-weight: $font-weight-light;

            .search--inline__suggestion {
                font-weight: $font-weight-medium;
            }
        }
    }
}
