// <Desktop-specific layout - secondary navigation never present
main > .case-study--research {
    @include media('<small') {
        padding-bottom: $measure--half;
        .case-study--research__overview__wrapper {
            @include default-margin();

            .case-study--research__overview {
                @include default-padding();
            }
        }

        .case-study--research__details__section {
            @include standard-box();
        }

        .case-study--research__details {
            h2 {
                @include standard-box();
            }
        }
    }

    @include media('>=tablet', '<small') {
        .case-study--research__content__wrapper {
            @include x-column-grid(24);
            display: grid;
            grid-column-gap: $measure--small2;
            @include default-margin();
        }
    }

    .case-study--research__content {
        @include media('>=tablet', '<small') {
            grid-column: 4/-4;
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);

        > .case-study--research {
            grid-column: 1 / span 24;

            .case-study--research__overview__wrapper,
            .case-study--research__video__wrapper,
            .case-study--research__content__wrapper,
            .case-study--research__details__wrapper {
                @include x-column-grid(24);
                display: grid;

                .case-study--research__overview {
                    @include x-column-grid(22);

                    display: grid;
                    grid-column: 2 / -2;
                }

                .case-study--research__video {
                    grid-column: 6 / -6;

                    .embedded-video {
                        margin-top: $measure--small2;

                        padding-bottom: 0;
                        padding-left: 0;
                        padding-right: 0;
                        padding-top: 0;
                    }

                    .youtube-video {
                        width: 100%;
                    }
                }

                .case-study--research__content,
                .case-study--research__details {
                    grid-column: 3/-3;
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .case-study--research {
                grid-column: 7 / span 18;

                .case-study--research__overview__wrapper,
                .case-study--research__video__wrapper,
                .case-study--research__content__wrapper,
                .case-study--research__details__wrapper {
                    @include content-grid();

                    .case-study--research__overview {
                        @include x-column-grid(16);
                        display: grid;
                        grid-column: 2 / -2;

                        .case-study--research__overview__researchers {
                            grid-column: 3 / -3;
                        }
                    }

                    .case-study--research__video {
                        grid-column: 3 / -3;

                        .embedded-video {
                            margin-top: $measure--small2;
                            padding-bottom: 0;
                            padding-left: 0;
                            padding-right: 0;
                            padding-top: 0;
                        }

                        .youtube-video {
                            width: 100%;
                        }
                    }

                    .case-study--research__content,
                    .case-study--research__details {
                        grid-column: 4 / span 12;
                    }
                }
            }
        }
    }
}

// Generic styles
.case-study--research {
    .case-study--research__overview {
        background-color: $color-gray13;
        padding: $measure--small2 0 $measure--medium1 0;

        .case-study--research__overview__researchers {
            .label {
                font-weight: $font-weight-medium;
                margin-right: $measure--half;
            }
        }
    }

    .case-study--research__video__wrapper {
        @include default-margin;
    }

    .case-study--research__content {
        margin-bottom: $measure--basic;

        @include media('<tablet') {
            .content-fade-in,
            .case-study--research__content__share-icons,
            .infographic {
                @include default-margin;
            }
        }

        h2 {
            margin-top: $measure--small2;
        }

        .case-study--research__content__share-icons__wrap {
            margin-bottom: $measure--small4;
            margin-left: 0;
            margin-right: 0;
            margin-top: $measure--small4;
        }

        .case-study--research__content__share-icons {
            border-bottom: $border-width-hairline $border-style-solid
                $color-gray2;
            border-top: $border-width-hairline $border-style-solid $color-gray2;
            position: relative;
        }

        .case-study--research__content__share-icons__icons {
            ul {
                list-style-type: none;
                margin: $measure--small2 0;
                display: flex;
                max-width: none;
            }

            li {
                margin-right: $measure--threequarters;
                margin-top: 0;
            }

            li.copyLink {
                margin-right: 0;
            }
        }

        .case-study--research__content__video {
            @include media('<tablet') {
                @include default-margin;
            }
        }
    }

    .case-study--research__whats-happening {
        background-color: $color-gray1;
        display: grid;
        @include x-column-grid(18);
        @include media('<small') {
            @include default-padding;
        }
        margin-bottom: $measure--small4;

        h2 {
            text-align: center;
            margin-top: 0;
            grid-column: 1/-1;
        }

        .related-content {
            @include x-column-grid(18);
            grid-column-gap: $measure--small2;
            grid-column: 1/-1;

            @include media('>=mobile') {
                grid-column: 4/-4;
            }

            @include media('>=tablet') {
                grid-column: 5/-5;
            }

            @include media('>=between') {
                grid-column: 6/-6;
            }

            @include media('>=small') {
                display: grid;
                grid-column: 2/-2;
                grid-template-columns: repeat(3, 1fr);
            }
        }

        @include media('>=mobile', '<tablet') {
            .card__details {
                flex-direction: column;
            }

            .card__thumbnail {
                width: initial;
            }

            .card--news__label--news + .card__thumbnail + .card__details__text {
                padding-top: initial;
                margin-top: initial;
            }
        }

        @include media('>=small') {
            .card {
                margin-top: 0;
            }
        }

        @include media('<small') {
            .card:first-of-type {
                margin-top: initial;
            }
        }
    }

    .case-study__related-case-studies {
        @include default-margin();

        .related-content__heading {
            margin-top: $measure--small4;
        }

        h2,
        h3 {
            text-align: center;
        }
    }

    .case-study--research__details__wrapper {
        background-color: $color-gray1;

        .case-study--research__details {
            padding-bottom: $measure--small2;
        }

        .case-study--research__details__section {
            background-color: $color-purewhite;
            margin-top: $measure--basic;
            padding: $measure--basic;

            .icon {
                margin-right: $measure--half;
            }

            .label {
                color: $color-gray7;
                font-weight: $font-weight-medium;
            }
        }
    }
}
