// <Desktop-specific layout - secondary navigation never present
main > .news {
    grid-column: 1 / -1;

    @include media('<small') {
        grid-column: 1 / -1 !important;

        .banner__standfirst {
            @include x-column-grid(22);

            h1,
            .labels,
            .standfirst {
                grid-column: 3 / 21;
            }
        }

        .news__summary,
        .news__wrap,
        .news__body,
        .news__hashtags__wrap,
        .news__social-icons,
        .news__related-courses,
        .news__related-subjects,
        .news__related-academics,
        .news__schools,
        .news__related-centres,
        .news__related-events,
        .news__related-people {
            grid-column: 4 / 22;
        }

        .news__contact,
        .news__related-news,
        .news__related-bayes-courses {
            grid-column: 1 / -1;
        }

        &.news--fullwidth {
            grid-column: 1 / span 24; /* dimitra said never navigation present, thus always full width */
        }
    }

    @include media('<tablet') {
        .banner__standfirst {
            @include x-column-grid(24);

            h1,
            .labels,
            .standfirst {
                grid-column: 1/-1;
            }
        }

        .news__summary,
        .news__wrap,
        .news__body,
        .news__hashtags__wrap,
        .news__social-icons,
        .news__related-courses,
        .news__related-subjects,
        .news__related-academics,
        .news__schools,
        .news__related-centres,
        .news__related-events,
        .news__related-people {
            grid-column: 3 / 23;
            overflow: hidden;

            @include media('<=mobile') {
                grid-column: 1 / 24;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .news {
            grid-column: 1 / -1 !important;

            .banner__standfirst {
                @include x-column-grid(20);
            }

            .news__summary,
            .news__wrap,
            .news__body,
            .news__hashtags__wrap,
            .news__social-icons,
            .news__related-courses,
            .news__related-subjects,
            .news__related-academics,
            .news__schools,
            .news__related-centres,
            .news__related-events,
            .news__related-people {
                grid-column: 7 / 19;
            }

            .news__contact,
            .news__related-news,
            .news__related-bayes-courses {
                grid-column: 1 / -1;
            }

            &.news--fullwidth {
                grid-column: 1 / span 24; /* dimitra said never navigation present, thus always full width */
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .news {
                grid-column: 7 / -1;
            }
        }
    }
}

// Generic styles
main > .news {
    .content {
        padding: 0;
        margin: 0;
    }

    .banner__standfirst {
        display: grid;

        h1,
        .labels,
        .standfirst {
            grid-column: 1/-1;
        }
    }

    .news__container {
        @include standard-grid();
        @include media('<mobile') {
            @include default-margin();
        }

        > div:last-child {
            border-bottom: none;
        }
    }

    .news__summary {
        padding-bottom: $measure--small2;

        p {
            @include standfirst();
            margin-top: 0;
        }
    }

    .news__wrap {
        border-top: $border-width-hairline $border-style-solid $color-gray4;
        padding-bottom: $measure--threequarters;
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;
    }

    .news__published {
        p {
            @include smalltype();

            color: $color-gray8;
            max-width: unset;
        }
    }

    .news__body {
        blockquote {
            p {
                @include font-family('heading');
                @include gammatype();
                @include heading-font-features();

                font-weight: $font-weight-light;
                line-height: $line-height-ratio--body;
            }
        }

        .embedded-video {
            margin-top: $measure--small2;
            padding: 0;
        }

        .carousel-gallery-default {
            margin-top: $measure--small2;
            margin-bottom: $measure--small2;
            overflow: hidden;
        }
    }

    .news__hashtags__title {
        color: $color-gray7;
        font-weight: $font-weight-medium;
        margin-top: $measure--small2;
    }

    ul.news__hashtags {
        flex-wrap: wrap;
    }

    .news__social-icons {
        margin: $measure--basic 0;

        @include media('>=tablet') {
            margin: $measure--small4 0;
        }

        p {
            font-weight: $font-weight-medium;
            margin-right: $measure--basic;
        }

        ul {
            margin-left: 0;
            margin-right: 0;
            display: flex;
            flex-direction: row;
        }

        li {
            list-style: none;
            margin-right: $measure--half;
        }
    }

    .news__related-courses {
        .card--related-course a {
            width: 100%;
        }
    }

    .news__related-academics {
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;
        .related-content {
            li:only-child {
                grid-column: 1/-1;
            }
            &.card--profile {
                padding: 0;
                margin-bottom: $measure--basic;
            }
        }
    }

    .news__related-subjects,
    .news__related-centres,
    .news__schools {
        padding-bottom: $measure--small1;
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;

        ul {
            margin-left: 0;
        }

        li {
            list-style: none;

            &:before {
                @include font-family('sharp');

                color: $color-red50;
                content: '\f0a9';
                display: inline-block;
                font-weight: $font-weight-hairline;
                margin-right: $measure--half;
            }
        }
    }

    .news__related-news,
    .news__related-bayes-courses {
        background: $color-purewhite;
        .news__related-news__container,
        .news__related-bayes-courses__container {
            @include media('<small') {
                @include default-margin();
            }
            margin: 0 $measure--small2;
            h2 {
                text-align: center;
            }
        }
    }

    .news__contact {
        @include footer-cards--dark();
        @include default-padding();

        background-color: $color-red70;

        h2 {
            @include full-width-heading($color-red60);
        }

        .card--contact .card__details .card__tag .icon {
            color: $color-purewhite;
        }
    }
}

.js .news__related-news .news__related-news__container .slider__controls {
    @include media('>=small') {
        @include default-margin();
    }
}
