// <Desktop-specific layout - secondary navigation never present
main > .landing--latest {
    @include standard-grid();

    .landing__standfirst {
        grid-column: 1/-1;
        @include standard-grid();
    }

    .landing__contact,
    .landing--latest__content,
    .landing--latest__event-categories,
    .landing--latest__featured-news,
    .wrapper--landing--latest__content,
    .wrapper--landing--latest__latest-content--news,
    .wrapper--landing--latest__latest-content--events {
        @include media('<small') {
            grid-column: 1/-1;
            @include default-padding();
        }
        grid-column: 2/-2;
    }

    @include media('>=tablet', '<small') {
        .wrapper--landing--latest__content {
            @include standard-grid();

            margin-left: 0;
            margin-right: 0;

            .landing--latest__content,
            .landing--latest__event-categories {
                grid-column: 4 / span 18;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .landing--latest {
            .landing__standfirst > .standfirst {
                grid-column: 4 / span 18;
            }

            .wrapper--landing--latest__content,
            .wrapper--landing--latest__event-categories {
                @include standard-grid();
            }

            .landing--latest__content,
            .landing--latest__event-categories {
                grid-column: 1 / span 14;
            }
        }
    }

    @include media('>=desktop') {
        > .landing--latest {
            .landing__standfirst > .standfirst,
            .landing--latest__content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .landing--latest {
                .landing__standfirst,
                .wrapper--landing--latest__content,
                .wrapper--landing--latest__event-categories {
                    @include content-grid();
                }

                .landing--latest__content,
                .landing--latest__event-categories {
                    grid-column: 1 / span 12;
                }
            }
        }
    }
}

// Generic styles
.landing--latest {
    @include media('>=tablet') {
        .latest-content > ul {
            justify-content: left;
        }
    }
    .wrapper--landing--latest__latest-content--news {
        @include media('<small', '>=tablet') {
            .card--news a {
                flex-direction: column;

                img {
                    width: 100%;
                }
            }
        }
    }
}

.landing__standfirst > .standfirst {
    @include media('<small') {
        grid-column: 1/-1;
        @include standard-box();
    }
}

.js .landing--latest {
    .related-content {
        padding: 0;
    }
}

.landing--latest {
    .wrapper--landing--latest__latest-content--events {
        margin-top: $measure--small4;

        .latest-content {
            margin-top: 0;
        }
    }

    .landing--latest__event-categories {
        p {
            @include font-family('heading');
            @include heading-font-features();
            @include gammatype();

            font-weight: $font-weight-normal;
        }
    }

    .wrapper--landing--latest__latest-content--news,
    .wrapper--landing--latest__latest-content--events {
        .related-content {
            @include media('<tablet') {
                margin-left: 0;
                margin-right: 0;
            }
        }
    }

    .wrapper--landing__contact {
        background-color: $color-pink80;
        color: $color-purewhite;
        grid-column: 1/-1;

        .landing__contact__heading {
            border-bottom-color: $color-pink70;
        }

        .landing__contact {
            .related-content {
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                padding: 0;

                @include media('<tablet') {
                    flex-direction: column;
                }

                @include media('>=tablet') {
                    flex-direction: row;
                    margin-top: $measure--small2;
                }

                .card__details {
                    .card__tag {
                        a {
                            @include anchor-inline(
                                $color-gray4,
                                $color-gray4,
                                $color-gray11,
                                $color-outline--dark
                            );
                        }
                    }
                }

                .card--contact {
                    .card__details {
                        .card__tag {
                            .icon {
                                color: $color-purewhite;
                            }
                        }
                    }
                }

                li {
                    padding-right: $measure--small2;
                    width: unset;

                    & + li {
                        @include media('<tablet') {
                            border-left: 0;
                            border-top-color: $color-pink70;
                            border-top-style: $border-style-solid;
                            border-top-width: $border-width-hairline;
                            padding-left: 0;
                            padding-top: $measure--small2;
                        }

                        @include media('>=tablet') {
                            border-left-color: $color-pink70;
                            border-left-style: $border-style-solid;
                            border-left-width: $border-width-hairline;
                            border-top: 0;
                            padding-left: $measure--small2;
                            padding-top: 0;
                        }
                    }
                }
            }
        }
    }
}
