@mixin featured-profiles-block(
    $text-color,
    $link-color,
    $link-color-engaged,
    $profile-bg-color,
    $profile-bg-color-engaged,
    $profile-link-color-engaged,
    $outline-color: $color-nearblack
) {
    color: $color-gray4;

    a {
        color: $link-color;
        background-image: none;
        border-bottom: $link-color $border-style-solid $border-width-default;
    }

    .linked-heading {
        grid-column: 2 / -2;
        border-bottom: 0;

        &:focus {
            outline-color: $link-color-engaged;
            outline-offset: $measure--half;
        }
        .linked-heading__text {
            color: $text-color;
            border-bottom-color: $text-color;
        }
    }

    .wrapper--featured-profiles__heading-intro {
        color: $text-color;
    }

    .wrapper--featured-profiles__profile {
        a.featured-profiles__profile {
            background: $profile-bg-color;
            border-bottom: 0;

            &:hover {
                @include media('hover') {
                    background: $profile-bg-color-engaged;

                    .featured-profiles__profile__label {
                        h3 {
                            color: $profile-link-color-engaged;
                            border-bottom-color: $profile-link-color-engaged;
                        }

                        p {
                            color: $profile-link-color-engaged;
                        }

                        .fa-long-arrow-right {
                            visibility: visible;
                            color: $color-red50;
                        }
                    }
                }
            }

            &:active,
            &:focus {
                outline-color: $outline-color;
            }

            .featured-profiles__profile__image {
                width: 35%;
            }

            .featured-profiles__profile__label {
                width: 100%;
                margin-left: $measure--basic;

                h3 {
                    display: inline;
                    color: $link-color;
                    border-bottom-color: $link-color;
                    background-image: none;
                    border-bottom: $link-color $border-style-solid
                        $border-width-default;
                }

                p {
                    color: $text-color;
                }

                .fa-long-arrow-right {
                    display: block;
                    visibility: hidden;
                    font-size: $font-size--epsilon;
                    font-weight: $font-weight-light;
                    margin-top: $measure--quarter;
                }
            }
        }

        .featured-profiles__profile__image
            + .featured-profiles__profile__label {
            flex-basis: calc(65% - #{$measure--basic});
        }
    }
}

.featured-profiles-block {
    @include content-grid();
    grid-column: 1 / -1;
    padding: $measure--basic 0 $measure--small4;

    @include media('<between') {
        @include default-padding();
    }
}

.featured-profiles__profile {
    display: flex;
    padding: 0 $measure--basic $measure--basic 0;
    transition: $transition-duration--long;

    @include media('<tablet') {
        margin-top: $measure--small1;

        &:first-child {
            margin-top: 0;
        }
    }
}

.wrapper--featured-profiles__profile {
    justify-content: flex-start;

    @include media('>=tablet') {
        grid-template-columns: repeat(3, 1fr);
        display: grid;
        grid-column-gap: $measure--basic;
        grid-row-gap: $measure--basic;
    }

    @include media('>between') {
        grid-column-gap: $measure--small2;
        grid-template-columns: repeat(2, 1fr);
    }

    @include media('>desktop') {
        grid-template-columns: repeat(3, 1fr);
    }
}

.featured-profiles__heading,
.wrapper--featured-profiles__heading-intro,
.wrapper--featured-profiles__profile {
    grid-column: 1 / -1;

    @include media('>between') {
        grid-column: 2 / 18;
    }

    @include media('>desktop') {
        grid-column: 2 / 18;
    }
}

.featured-profiles-block {
    &.background--purple80 {
        @include featured-profiles-block(
            $color-purewhite,
            $color-gray4,
            $color-gray4,
            $color-purple90,
            $color-purewhite,
            $color-gray10,
            $color-purewhite
        );
    }
    &.background--gray9 {
        @include featured-profiles-block(
            $color-purewhite,
            $color-gray4,
            $color-gray4,
            $color-gray10,
            $color-purewhite,
            $color-gray10,
            $color-purewhite
        );
    }
    &.background--gray2 {
        @include featured-profiles-block(
            $color-nearblack,
            $color-nearblack,
            $color-nearblack,
            $color-purewhite,
            $color-pink90,
            $color-purewhite
        );
    }
    &.background--gray1 {
        @include featured-profiles-block(
            $color-nearblack,
            $color-nearblack,
            $color-nearblack,
            $color-purewhite,
            $color-pink90,
            $color-purewhite
        );
    }
    &.background--purewhite {
        @include featured-profiles-block(
            $color-gray10,
            $color-gray10,
            $color-purple90,
            $color-gray2,
            $color-pink90,
            $color-purewhite
        );
    }
}
