.link-decorator {
    margin-right: $measure--half;
}

// Link decorator not part of an anchor
:not(a) > .link-decorator {
    color: $color-red50;
}

// Link decorator part of an, e.g. CTA
a .link-decorator {
    margin-top: calc(#{$measure--quarter} + (#{$measure--quarter / 2}));
}

.inline-external-link {
    margin-left: $measure--half;
}
