////
/// Single Colour Content
/// @group pattern
////

.sc-content {
    margin: $measure--threequarters 0;
}

.sc-content__text {
    padding: $measure--threequarters;
    margin-top: 0;
}

.sc-content__heading {
    @include betatype();

    display: inline-block;
    margin: $measure--basic 0;
    padding: $measure--threequarters $measure--threequarters 0
        $measure--threequarters;
    margin: 0;
}

.sc-content--link {
    position: relative;

    .card-link {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: z-index('above');
    }

    .sc-content__heading {
        padding: 0;
        margin-top: $measure--half;
    }

    .sc-content__icon {
        position: absolute;
        bottom: 0;
        right: 0;
        padding: $measure--threequarters;
        font-weight: $font-weight-normal;
    }

    .sc-content__text {
        margin-right: $measure--small4;
    }

    a {
        @include anchor-reset();

        display: inline-block;
        padding: 0 $measure--threequarters $measure--threequarters
            $measure--threequarters;

        &:focus,
        &:active {
            .card-link {
                @include outline-properties($color-outline);
            }
        }
    }
}

$level-colors: (
    grey: (
        $color-sc-grey-bg,
        $color-sc-grey-bg-text,
        $color-sc-grey-btn,
        $color-sc-grey-btn-text,
    ),
    purple: (
        $color-sc-purple-bg,
        $color-sc-purple-bg-text,
        $color-sc-purple-btn,
        $color-sc-purple-btn-text,
    ),
    deep-magenta: (
        $color-sc-deep-magenta-bg,
        $color-sc-deep-magenta-bg-text,
        $color-sc-deep-magenta-btn,
        $color-sc-deep-magenta-btn-text,
    ),
    teal: (
        $color-sc-teal-bg,
        $color-sc-teal-bg-text,
        $color-sc-teal-btn,
        $color-sc-teal-btn-text,
    ),
    yellow: (
        $color-sc-yellow-bg,
        $color-sc-yellow-bg-text,
        $color-sc-yellow-btn,
        $color-sc-yellow-btn-text,
    ),
    green: (
        $color-sc-green-bg,
        $color-sc-green-bg-text,
        $color-sc-green-btn,
        $color-sc-green-btn-text,
    ),
    blue: (
        $color-sc-blue-bg,
        $color-sc-blue-bg-text,
        $color-sc-blue-btn,
        $color-sc-blue-btn-text,
    ),
    red: (
        $color-sc-red-bg,
        $color-sc-red-bg-text,
        $color-sc-red-btn,
        $color-sc-red-btn-text,
    ),
    white-purple: (
        $color-purewhite,
        $color-gray10,
        $color-sc-purple-btn,
        $color-sc-purple-btn-text,
    ),
    white-grey: (
        $color-purewhite,
        $color-gray10,
        $color-sc-grey-btn,
        $color-sc-grey-btn-text,
    ),
);

$level-border-colors: (
    white-purple: (
        $color-sc-purple-btn,
        $color-sc-purple-btn-text,
    ),
    white-grey: (
        $color-sc-grey-btn,
        $color-sc-grey-btn-text,
    ),
);

@each $level, $colors in $level-colors {
    $bg: nth($colors, 1);
    $bg-text: nth($colors, 2);
    $button: nth($colors, 3);
    $button-text: nth($colors, 4);

    .sc-content--#{$level} {
        color: $button-text;
        background-color: $button;
    }

    .sc-content--#{$level}.sc-content--link {
        color: $bg-text;
        background-color: $bg;

        .sc-content__heading {
            color: $button-text;
            background-color: $button;
        }

        a {
            background-color: $button;

            &:hover,
            &:focus,
            &:active {
                background-image: linear-gradient(
                    120deg,
                    $button 0%,
                    $button 100%
                );
            }

            .sc-content__heading {
                border-bottom: $border-width-default
                    $border-style-solid
                    $button-text;
            }
        }

        .sc-content__icon {
            color: $button-text;
            background-color: $button;
        }
    }
}

@each $level, $colors in $level-border-colors {
    $button: nth($colors, 1);
    $button-text: nth($colors, 2);

    .sc-content--#{$level} {
        border: $border-width-default $border-style-solid $button;
    }
}
