// <Desktop-specific layout - secondary navigation never present
main > .school {
    @include media('<small') {
        //@include default-margin();
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .school {
            grid-column: 6 / span 14;
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .school {
                grid-column: 7 / span 18;
                @include content-grid();

                .school__top-gradient {
                    grid-column: 1 / span 18;
                }

                .school__content {
                    grid-column: 1 / -1;

                    .school__content__cta,
                    .school__content__links,
                    .promo-area__wrap,
                    .school__content__facilities,
                    .school__statistic,
                    .school__content__middle-wrapper,
                    .school__content__whats-happening {
                        @include content-grid();
                    }

                    .school__content__cta ul,
                    .school__content__facilities h2,
                    .school__content__video,
                    .school__content__whats-happening h2,
                    .school__content__whats-happening .related-content {
                        grid-column: 2/-2;
                    }

                    .school__content__links--left {
                        grid-column: 2/10;
                    }

                    .school__content__links--right {
                        grid-column: 10/-2;
                    }

                    .promo-area,
                    .swiper-viewport {
                        grid-column: 1/-1;
                    }

                    .school__content__middle-wrapper,
                    .school__content__facilities,
                    .school__content__whats-happening {
                        @include content-grid();
                        grid-column: 1/-1;
                    }

                    .school__statistic {
                        @include content-grid();
                        grid-column: 1 / span18;

                        h2,
                        .statistics-block {
                            grid-column: 3/17;
                        }
                        .school__statistic--wrapper {
                            grid-column: 1/-1;
                            @include content-grid();
                        }
                    }
                }
            }
        }
    }
}

@include media('>=tablet') {
    .school__content__pathway {
        grid-column: 2/-2;
        .pathway {
            margin-top: 0;
            padding-top: 0;
            .pathway__cards {
                grid-template-columns: repeat(3, 1fr);
            }
        }
    }
}

@include media('>=between') {
    .school__content__whats-happening {
        .related-content {
            align-items: flex-start;
            flex-direction: row;
            justify-content: flex-start;

            .card {
                margin: 0 $measure--half;
            }
        }
    }
}

@include media('<small') {
    .school__content__whats-happening,
    .school__content__cta,
    .school__content__links,
    .school__content__facilities h2,
    .school__content__video,
    .school__content__pathway {
        @include default-margin();
        padding-left: 0;
        padding-right: 0;
    }

    .school__content__links--left {
        margin-bottom: $measure--basic;
    }
}

.school__top-gradient {
    .cta-block {
        max-width: initial;
        margin-top: initial;
    }
}

.school__content__middle-wrapper {
    background-color: $color-gray1;
}

.school__content__links {
    .school__content__links--left {
        ul {
            list-style: none;
            margin-left: 0;
        }

        .school__content__links--left__all-research-centres {
            &:after {
                @include font-family('sharp');

                content: '\f061';
                margin-left: $measure--half;
            }
        }
    }

    .school__content__links--right {
        background-color: $color-blue70;
        padding: $measure--basic;
        height: fit-content;

        h2 {
            color: $color-purewhite;
            margin-top: 0;
        }

        ul {
            list-style: none;
            margin-left: 0;
            margin-top: 0;
            max-width: unset;

            @include media('<small') {
                margin-bottom: $measure--basic;
            }

            @include media('>=tablet') {
                columns: 2;
            }

            li:first-of-type {
                margin-top: 0;
            }
        }

        .school__anchor-inline--gray4--darkColor {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-nearblack,
                $color-outline--dark
            );
        }
    }
}

.school__content__cta {
    @include media('>=tablet') {
        margin-bottom: $measure--small4;

        ul {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;

            li {
                max-width: calc(33% - #{$measure--basic});
                margin-right: $measure--basic;
            }
        }

        a {
            span {
                display: flex;
                flex-direction: column;
                justify-content: center;
                min-height: $measure--medium10;
            }
        }
    }

    ul {
        list-style: none;
        margin-left: 0;
        margin-top: 0;
        max-width: 100%;

        li {
            margin-top: 0;
            text-align: center;

            @include media('<=tablet') {
                p {
                    max-width: initial;
                }
            }
        }
    }

    .school__cta-outlined-light-bg {
        @include cta-outlined-light-bg($color-gray11, $color-gray6);

        @include media('hover') {
            &:hover {
                color: $color-nearblack;
            }
        }

        @include media('<tablet') {
            display: flex;
            justify-content: center;
            text-align: center;
        }
    }
}

.school__statistic {
    position: relative;
    h2 {
        text-align: center;
        position: relative;
        padding-bottom: $measure--half;

        &:after {
            content: '';
            display: block;
            position: absolute;
            width: 10%;
            left: 45%;
            border-bottom: $border-width-thicker solid $color-red50;
            bottom: 0;
        }
    }

    .school__statistic--wrapper {
        background-color: $color-gray1;
    }

    .statistics-block {
        .ksp__items {
            margin-left: 0;
            margin-right: 0;
        }

        @include media('<tablet') {
            padding-top: 0;
        }
    }

    .ksp__items {
        background-color: $color-purewhite;
    }
}

.school__content__links {
    margin-top: $measure--small4;
    margin-bottom: $measure--small4;

    .related-links--with-images__links-block {
        .icon {
            color: $color-red50;
        }
    }
}

.school__content__whats-happening {
    padding-bottom: $measure--basic;
}

// default banner CTA

.banner__standfirst-cta {
    @include cta-basic-light-bg($color-red50);

    span {
        color: $color-purewhite;
    }
}

// School of Policy & Global Affairs
.pga {
    .banner__standfirst {
        h1 {
            color: $color-school-pga--primary;
        }
    }

    .banner__standfirst-cta {
        @include cta-basic-light-bg($color-school-pga--accent1);

        span {
            color: $color-purewhite;
        }
    }
    .school__content__links--right {
        background-color: $color-school-pga--primary;
    }

    .school__content__links--left {
        &:hover {
            .linked-heading__text {
                &:before {
                    background: $color-school-pga--accent1;
                }
            }
        }

        .linked-heading__icons {
            background: $color-school-pga--primary;
        }
    }

    .school__statistic {
        h2 {
            &:after {
                border-bottom-color: $color-school-pga--primary;
            }
        }
    }
}

