/**
  Used in UG paint layout
**/

.banner-svg {
    background: linear-gradient(to bottom, $color-gray2, $color-purewhite 75%);
}

.banner-svg--wrapper {
    @include standard-grid-wrapper();
}

.banner-svg--wrapper__svg {
    grid-column: 1/-1;
    position: relative;

    .hero-image {
        height: 14rem;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);

        img {
            width: 17.5rem;
        }
    }
}

.banner-svg--wrapper__title {
    grid-column: 1/-1;
    position: relative;
    padding-top: $measure--medium7;
    padding-bottom: $measure--small4;
    @include media('<=tablet') {
        padding-bottom: $measure--small4;
    }
    text-align: center;

    h1,
    p {
        max-width: none;
    }

    &:after {
        background-color: $color-red50;
        content: '';
        display: block;
        height: 0.25rem;
        margin-left: auto;
        margin-right: auto;
        margin-top: 1.5rem;
        width: 3rem;
    }
}

.banner-svg--wrapper__title,
.banner-svg--wrapper__svg {
    @include media('<=between') {
        grid-column: 1/-1;
    }
}
