////
/// CTA mixin
/// @group abstracts
////
@mixin cta-reset-properties() {
    background: initial;
    border-radius: $measure--quarter;
    border: none;
    color: initial;
    display: inline-flex;
    font-weight: $font-weight-medium;
    padding: $measure--half $measure--small2;
    position: relative;

    &:disabled {
        cursor: default;
    }
}

@mixin cta-reset-icon-properties($icon: '\f061') {
    content: fa-content($icon);
    font-family: 'Font Awesome 6 Pro';
    font-style: normal;
    font-weight: normal;
    opacity: 0;
    position: absolute;
    right: $measure--small2;
    text-decoration: inherit;
    transform: translateX(-$measure--threequarters);
    transition: background-color $transition-duration--long,
        transform $transition-duration--long;
}

@mixin cta-reset($icon: '\f061') {
    @include cta-reset-properties();

    @include media('reducemotion') {
        transition: none;
    }

    @include media('<=mobile') {
        display: flex;
        justify-content: center;
        text-align: center;
    }

    transition: background-color $transition-duration--long,
        transform $transition-duration--long;

    span {
        transition: transform $transition-duration--long;
    }

    &:after {
        @include cta-reset-icon-properties($icon);

        @include media('reducemotion') {
            transition: none;
        }
    }

    &:focus,
    &:active {
        background-color: transparent;
        border: $border-width-default $border-style-solid $color-red50;
        color: $color-purewhite;

        span {
            transform: translateX(-$measure--threequarters);
        }

        &:after {
            opacity: 1;
            transform: translateX($measure--threequarters);
        }
    }

    &:hover {
        @include media('hover') {
            background-color: transparent;
            border: $border-width-default $border-style-solid $color-red50;
            color: $color-purewhite;

            span {
                transform: translateX(-$measure--threequarters);
            }

            &:after {
                opacity: 1;
                transform: translateX($measure--threequarters);
            }
        }
    }

    &:disabled {
        opacity: 0.7;

        span {
            transform: none;
        }

        &:after {
            opacity: 0;
            transform: none;
        }
    }
}

/**
 *
 * Call to action Primary style on a dark background
 * To use @include cta-primary-dark-bg();
 * this will style the CTA to default primary colours
 *
 * to modify the colours pass in the background colour and colour as parameters
 * e.g @include cta-primary-dark-bg(yellow, green);
 *
 *
 */

@mixin cta-primary-icon-dark-bg(
    $bgColor: $color-brand-primary,
    $color: $color-bg,
    $icon: '\f061'
) {
    @include cta-reset($icon);
    border: $border-width-default $border-style-solid $bgColor;
    background: $bgColor;
    color: $color;

    &:visited {
        color: $color;
        border-color: $bgColor;
    }

    &:focus {
        background: $bgColor;
        color: $color;
    }

    &:active,
    &:focus {
        @include anchor-default-outline($color-outline--dark);
        border-color: $bgColor;
    }

    &:hover,
    &:active {
        @include media('hover') {
            border-color: $bgColor;
            background-image: none;
            background: transparent;
        }
    }

    &:disabled {
        $color: $color-bg;
        background: $bgColor;
        border: $border-width-default $border-style-solid $bgColor;
    }
}

@mixin cta-primary-dark-bg($bgColor: $color-brand-primary, $color: $color-bg) {
    @include cta-primary-icon-dark-bg();

    border-color: $bgColor;
    background: $bgColor;
    color: $color;

    span {
        transition: none;
    }

    &:visited {
        color: $color;
        border-color: $bgColor;
    }

    &:after {
        content: '';
    }

    &:focus {
        border-color: $bgColor;
        background: $bgColor;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            background: transparent;
            color: $bgColor;
            border-color: $bgColor;
        }
    }

    &:active {
        background: transparent;
        border-color: $bgColor;
        color: $bgColor;
    }

    &:active,
    &:focus,
    &:hover {
        span {
            transform: none;
        }
    }

    &:disabled {
        border-color: $bgColor;
        background: $bgColor;
        color: $color;
    }
}

/**
 *
 * Call to action style for a light background
 * Primary colors will be used if no parameters is pass as args
 * e.g @include cta-primary-light-bg();
 *
 * to modify the colours used pass in a backgound or colour arg
 * e.g @include cta-primary-light-bg(yellow, green);
 *
 */

@mixin cta-primary-icon-light-bg(
    $lgbgColor: $color-brand-primary,
    $lgcolor: $color-purewhite
) {
    @include cta-reset();
    @include box-shadow();
    background: $lgbgColor;
    border: $border-width-default $border-style-solid $lgbgColor;
    color: $lgcolor;

    &:visited {
        border-color: $lgbgColor;
        color: $lgcolor;
    }

    &:focus {
        @include anchor-default-outline($color-outline);
        background: $lgbgColor;
        border-color: $lgbgColor;
        color: $lgcolor;
    }

    &:hover {
        @include media('hover') {
            background: $lgcolor;
            border-color: $lgbgColor;
            color: $lgbgColor;
        }
    }

    &:active {
        @include anchor-default-outline($color-outline);
        border-color: $lgbgColor;
        box-shadow: none;
        color: $lgbgColor;
        background: transparent;
    }

    &:disabled {
        color: $lgcolor;
        background: $lgbgColor;
        border: $border-width-default $border-style-solid $lgbgColor;
    }
}

@mixin cta-primary-light-bg(
    $lgbgColor: $color-brand-primary,
    $lgcolor: $color-purewhite
) {
    @include cta-primary-icon-light-bg();
    border-color: $lgbgColor;
    background: $lgbgColor;
    color: $lgcolor;

    span {
        transition: none;
    }

    &:visited {
        border-color: $lgbgColor;
        color: $lgcolor;
    }

    &:after {
        content: '';
    }

    &:focus,
    &:active {
        color: $lgbgColor;
    }

    &:focus {
        border-color: $lgbgColor;
        background: $lgbgColor;
        color: $lgcolor;
    }

    &:hover {
        @include media('hover') {
            background: transparent;
            border-color: $lgbgColor;
            color: $lgbgColor;
        }
    }

    &:active {
        color: $lgbgColor;
        border-color: $lgbgColor;
        background: transparent;
    }

    &:active,
    &:focus,
    &:hover {
        span {
            transform: none;
        }
    }

    &:disabled {
        border-color: $lgbgColor;
        background: $lgbgColor;
        color: $lgcolor;
    }
}

/**
 *
 * Call to action outlined on a dark background
 * to use just call @include cta-outlined-dark-bg();
 *
 */

/**

     TODO:
     - Allow mixin to accept parameters to change default colours?

  */

@mixin cta-outlined-icon-dark-bg(
    $link-color: $color-purewhite,
    $color: $color-purewhite,
    $alt-color: $color-nearblack,
    $icon: '\f061'
) {
    @include cta-reset($icon);
    background: transparent;
    border: $border-width-default $border-style-solid $link-color;
    color: $color;

    &:visited {
        border-color: $link-color;
        color: $color;
    }

    &:active {
        background: $link-color;
        border-color: $link-color;
        color: $alt-color;
    }

    &:hover {
        @include media('hover') {
            background: $link-color;
            border-color: $link-color;
            color: $alt-color;
        }
    }

    &:focus {
        @include anchor-default-outline($color-outline--dark);
        border-color: $link-color;
    }

    &:active {
        @include anchor-default-outline($color-outline--dark);
        border-color: $link-color;
    }

    &:disabled {
        color: $color;
        background: transparent;
        border: $border-width-default $border-style-solid $link-color;
    }
}

@mixin cta-outlined-dark-bg(
    $link-color: $color-purewhite,
    $color: $color-purewhite,
    $alt-color: $color-nearblack
) {
    @include cta-outlined-icon-dark-bg($link-color, $color, $alt-color);

    span {
        transition: none;
    }

    &:after {
        content: '';
    }

    &:focus {
        color: $alt-color;
        background-color: $color;
    }

    &:active,
    &:focus,
    &:hover {
        span {
            transform: none;
        }
    }
}

