@mixin cta-outlined-icon-light-bg(
    $color: $color-gray11,
    $color-border: $color-gray11,
    $icon: '\f061'
) {
    @include cta-reset($icon);
    @include box-shadow();
    background: transparent;
    border: $border-width-default $border-style-solid $color-border;
    color: $color;

    &:visited {
        border-color: $color-border;
        color: $color;
    }

    &:focus {
        @include anchor-default-outline($color-outline);
        background: $color-purewhite;
        color: $color;
        border-color: $color-border;
    }

    &:hover {
        @include media('hover') {
            background: $color-border;
            border-color: $color-border;
            color: $color-purewhite;
        }
    }

    &:active {
        @include anchor-default-outline($color-outline);
        background: $color-border;
        border-color: $color-border;
        box-shadow: none;
        color: $color-purewhite;
    }

    &:disabled {
        color: $color;
        background: transparent;
        border: $border-width-default $border-style-solid $color-border;
    }
}

@mixin cta-outlined-light-bg(
    $color: $color-gray11,
    $color-border: $color-gray11
) {
    @include cta-outlined-icon-light-bg($color, $color-border);

    span {
        transition: none;
    }

    &:after {
        content: '';
    }

    &:active,
    &:focus {
        span {
            transform: none;
        }
    }

    &:hover {
        @include media('hover') {
            span {
                transform: none;
            }
        }
    }
}

/**
 *
 * CTA on a light background
 * to use with default colours
 * e.g @include cta-basic-light-bg();
 *
 * colours can be modify by passing arg to the mixin
 * e.g @include cta-basic-light-bg(yellow, green);
 * this will change the background to yellow and color to green
 *
 */

@mixin cta-basic-light-bg(
    $bg-color: $color-brand-primary,
    $color: $color-purewhite
) {
    @include cta-primary-icon-light-bg();
    background-color: $bg-color;
    border-color: $bg-color;
    color: $color;

    span {
        transition: none;
    }

    &:visited {
        border-color: $bg-color;
        color: $color;
    }

    &:after {
        content: '';
    }

    &:focus,
    &:active {
        background-color: $bg-color;
        border-color: $bg-color;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            background-color: darken($bg-color, 10%);
            border-color: darken($bg-color, 10%);
            color: $color;
        }
    }

    &:active {
        background-color: darken($bg-color, 10%);
        border-color: darken($bg-color, 10%);
    }

    &:disabled {
        color: $color;
        background: $bg-color;
        border-color: $bg-color;
    }

    &:active,
    &:focus,
    &:hover {
        span {
            transform: none;
        }
    }
}

/**
 *
 * CTA on a dark background
 * To used the default colour
 * e.g @include cta-basic-dark-bg();
 *
 * to modify the colour used you can pass in arg to the mixin
 * e.g @include cta-basic-dark-bg(yellow, blue);
 * this will modify the background and color attributes
 *
 */

@mixin cta-basic-dark-bg(
    $bg-color: $color-brand-primary,
    $color: $color-purewhite
) {
    @include cta-basic-light-bg();
    background-color: $bg-color;
    border-color: $bg-color;
    color: $color;

    &:visited {
        border-color: $bg-color;
        color: $color;
    }

    &:after {
        content: '';
    }

    &:focus,
    &:active {
        background-color: $bg-color;
        border-color: $bg-color;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            background-color: darken($bg-color, 10%);
            border-color: $bg-color;
            color: $color;
        }
    }

    &:active {
        background-color: darken($bg-color, 10%);
    }

    &:focus,
    &:active {
        @include anchor-default-outline($color-outline--dark);
    }

    &:disabled {
        color: $color;
        background: $bg-color;
        border-color: $bg-color;
    }
}
