.key-information--lifelong-learning {
    .lifelong-learning__start-date__cta {
        max-width: unset;
        .cta-block__cta {
            @include media('<=between') {
                display: flex;
                flex-direction: column;
                margin-top: $measure--small4;
            }

            a,
            button {
                @include cta-basic-light-bg($color-pink80, $color-purewhite);

                @include media('<=between') {
                    justify-content: center;
                }
            }
        }
    }

    ul {
        list-style-type: none;
        margin-left: 0;
        margin-top: 0;
    }

    > ul {
        > li {
            display: grid;
            padding-bottom: $measure--basic;

            @include media('>=tablet') {
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray6;

                &:last-of-type {
                    border-bottom: none;
                }
            }

            @include media('<=mobile') {
                background-color: $color-purewhite;
                border: $border-width-default $border-style-solid $color-gray6;
                padding: $measure--basic;

                grid-column-gap: $measure--basic;
                grid-template-columns: repeat(6, 1fr);
                grid-template-rows: repeat(4, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading'
                    'date date date date date date'
                    'location location location location location location'
                    'button button button button button button'
                    'explanation explanation explanation explanation explanation explanation';
            }

            @include media('>mobile', '<=tablet') {
                grid-column-gap: $measure--basic;
                grid-template-columns: repeat(12, 1fr);
                grid-template-rows: repeat(4, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date date date date date date date date'
                    'location location location location location location location location location location location location'
                    'button button button button button button button button button button button button'
                    'explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation';
            }

            @include media('>tablet', '<=between') {
                grid-column-gap: $measure--basic;
                grid-template-columns: repeat(12, 1fr);
                grid-template-rows: repeat(2, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date location location location location location button button'
                    'explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation';
            }

            @include media('>between') {
                grid-column-gap: $measure--small2;
                grid-template-columns: repeat(18, 1fr);
                grid-template-rows: repeat(2, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date date date location location location location location location location button button button button'
                    'explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation';
            }

            @include media('>desktop') {
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date date location location location location location location location button button button button button'
                    'explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation';
            }

            > h3 {
                grid-area: heading;
            }

            > ul {
                @include media('<tablet') {
                    margin-left: $measure--small4;
                    margin-right: $measure--small4;
                }

                &:nth-of-type(1) {
                    grid-area: date;
                }

                &:nth-of-type(2) {
                    grid-area: location;
                }

                &:nth-of-type(3) {
                    grid-area: button;
                }
            }

            .lifelong-learning__start-date__fee-explanation {
                grid-area: explanation;
            }
        }
    }
}

// Key info for research templates

.key-information--research__main {
    background: $color-purewhite;
    border: $border-style-solid $border-width-hairline $color-gray3;
    padding: $measure--basic;
}

.key-information--research__main__dates {
    @include table-reset();
    display: block;
    overflow: hidden;
    text-align: left;
    margin-bottom: $measure--small2;
    margin-top: 0;
    padding-bottom: $measure--small2;
    border-bottom: $border-style-solid $border-width-thicker $color-purple70;

    thead,
    tbody {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 1.5rem;

        @include media('<desktop') {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    tr {
        display: flex;
        grid-column: 1 / 3;
        border-bottom: solid $color-gray3 $border-width-hairline;
        padding: $border-width-default 0;

        th {
            flex: 1;
            color: $color-gray9;
            @include font-family('heading');
            @include betatype();
            margin-top: 0;
            font-weight: $font-weight-medium;

            &:nth-of-type(2) {
                margin-left: $measure--small2;
            }
        }

        td {
            flex: 1;
            line-height: 1.625;

            &:nth-of-type(2) {
                margin-left: $measure--small2;
            }
        }
    }
}

.key-information--research__main__dates .date--short {
    @include media('>=mobile') {
        display: none;
    }
}

.key-information--research__main__dates .date--long {
    @include media('<mobile') {
        display: none;
    }
}

.key-information--research__main__duration {
    display: grid;
    grid-gap: 1.5rem;
    grid-template-columns: repeat(3, 1fr);

    @include media('<between') {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0;
    }

    @include media('<mobile') {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 0;
    }
}

.key-information--research__main__duration__heading {
    color: $color-gray9;
    @include betatype();
    font-weight: $font-weight-medium;
    @include font-family('heading');
    margin-bottom: $measure--half;
}

.key-information--research__main__duration__item {
    @include alphatype();

    span {
        color: $color-purple70;
        font-weight: $font-weight-medium;
    }

    @include media('<between') {
        margin-bottom: $measure--basic;
    }
}

.key-information--research__main__duration__item__label {
    display: block;
    color: $color-gray10;
}

.js .key-information--lifelong-learning__dates--controllable {
    .key-information--lifelong-learning__dates {
        overflow: hidden;
        position: relative;

        & > li {
            &:focus {
                @include outline-properties($color-nearblack);

                outline-offset: -#{$border-width-thick};
            }

            @include media('>=between') {
                display: none;

                &[data-loadmorevisible='true'] {
                    display: grid;
                }
            }

            @include media('<between') {
                position: absolute;
                top: 0;
                transition: left $transition-duration--medium ease-in-out;

                @include media('reducemotion') {
                    transition: none;
                }

                &[data-sliderposition='-1'] {
                    left: -100%;

                    &[data-hidden='true'] {
                        visibility: hidden;
                    }
                }

                &[data-sliderposition='0'] {
                    left: 0;
                    position: relative;
                }

                &[data-sliderposition='1'] {
                    left: 100%;

                    &[data-hidden='true'] {
                        visibility: hidden;
                    }
                }
            }
        }
    }

    .key-information--lifelong-learning__controls {
        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        margin-top: $measure--basic;

        .key-information--lifelong-learning__indicator {
            @include smalltype();

            display: block;

            @include media('>=between') {
                display: none;
            }

            .key-information--lifelong-learning__indicator__current,
            .key-information--lifelong-learning__indicator__divider {
                color: $color-gray7;
            }

            .key-information--lifelong-learning__indicator__divider {
                margin-left: $measure--quarter;
                margin-right: $measure--quarter;

                .key-information--lifelong-learning__indicator__divider--sr {
                    @include sr-only();
                }
            }

            .key-information--lifelong-learning__indicator__total {
                color: $color-red50;
            }
        }

        .key-information--lifelong-learning__controls__prev,
        .key-information--lifelong-learning__controls__next,
        .key-information--lifelong-learning__controls__loadmore {
            background-color: transparent;
            border-color: $color-red50;
            border-style: $border-style-solid;
            border-width: $border-width-hairline;
            color: $color-red50;
            padding: $measure--threequarters;
            transition: background-color $transition-duration ease-in,
                border-color $transition-duration ease-in,
                color $transition-duration ease-in;

            @include media('reducemotion') {
                /* We can't completely disable the transition becasuse we action on transitionend */
                transition: background-color $transition-duration--instant
                        ease-in,
                    border-color $transition-duration--instant ease-in,
                    color $transition-duration--instant ease-in;
            }

            &:focus:not(:disabled),
            &:active:not(:disabled) {
                @include outline-properties($color-nearblack);
            }

            &:hover:not(:disabled) {
                @include media('hover') {
                    background-color: $color-red50;
                    color: $color-purewhite;
                }
            }

            &:disabled {
                border-color: transparent;
                color: $color-gray7;
                cursor: default;
            }
        }

        .key-information--lifelong-learning__controls__prev,
        .key-information--lifelong-learning__controls__next {
            @include font-family('sharp');

            font-weight: $font-weight-normal;
            display: block;

            @include media('>=between') {
                display: none;
            }

            span {
                @include sr-only();
            }
        }

        .key-information--lifelong-learning__controls__prev {
            margin-left: $measure--basic;

            &:before {
                content: '\f060';
            }

            & + .key-information--lifelong-learning__controls__next {
                margin-left: $measure--basic;
            }
        }

        .key-information--lifelong-learning__controls__next {
            &:before {
                content: '\f061';
            }
        }

        .key-information--lifelong-learning__controls__loadmore {
            border-radius: $measure--quarter;
            display: block;

            @include media('<between') {
                display: none;
            }
        }
    }
}
