// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        > .course--ug {
            @include content-grid();

            header,
            .course-info__wrapper,
            .course--ug__keyinfo__wrapper,
            .featured-profiles-block__wrap,
            .course-footer,
            .course--ug__whats-happening,
            .course--ug__tabs__wrap,
            .course--ug__related-courses__wrapper,
            .course--ug__announcement--clearing {
                grid-column: 1/-1;
            }

            .courses--ug__facilities,
            .course--ug__student {
                grid-column: 2/-2;
                .course--ug__student__intro {
                    grid-column: 1/-1;
                }
            }

            .promo-box__wrapper {
                @include x-column-grid(18);

                .promo-box {
                    grid-column: 1/-1;
                }
            }
            .course--ug__keyinfo__wrapper {
                @include x-column-grid(18);

                .course--ug__keyinfo {
                    grid-column: 2/-2;
                }
            }
        }
    }
}

// tablet / mobile layout
main {
    @include media('<small') {
        > .course--ug {
            .course--ug__keyinfo__wrapper {
                .course--ug__keyinfo {
                    margin-top: $measure--small2;
                }
            }

            .promo-box__wrapper {
                @include default-padding();
                display: block;
            }

            .course-info__wrapper,
            .course--ug__keyinfo__wrapper,
            .course-footer,
            .course--ug__whats-happening,
            .course--ug__related-courses__wrapper,
            .course--ug__tabs__wrap,
            .courses--ug__facilities,
            .course--ug__student {
                grid-column: 1/-1;
                @include default-padding();
            }

            header {
                grid-column: 1/-1;
            }

            .featured-profiles-block__wrap {
                grid-column: 1/-1;
            }

            .featured-profiles-block {
                @include default-padding();
            }
        }
    }
}

// Generic styles
.course--ug {
    .parallax {
        background-attachment: fixed;
        background-size: cover;
    }

    .standard-grid-wrapper {
        @include media('<small') {
            @include default-margin;
        }
    }

    .course--promo-cards {
        @include standard-grid-wrapper();
    }

    .course-info__wrapper {
        @include content-grid();

        @include media('<=small') {
            @include default-padding();
            padding-top: $measure--basic;
        }

        @include media('>tablet') {
            padding-bottom: $measure--basic;
            margin-top: 0;
        }

        .course-info {
            z-index: z-index('above');
            grid-column: 2/-2;
            @include media('<=desktop') {
                grid-column: 1/-1;
            }
            @include media('>desktop') {
                display: flex;
                justify-content: space-between;
            }

            .course-info__year {
                justify-content: space-between;
                grid-column: 1/8;

                @include media('<=mobile') {
                    display: block;
                }

                @include media('<=desktop') {
                    margin-bottom: $measure--small4;
                }

                .start-date {
                    @include betatype;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    @include media('<=desktop') {
                        text-align: center;
                    }
                }
            }

            .course-info__video {
                @include media('<=desktop') {
                    text-align: center;
                    span,
                    .cta-block {
                        display: block;
                    }
                }

                .cta-block {
                    display: inline-block;
                    li {
                        margin-right: 0;
                        margin-left: $measure--basic;
                    }
                }
            }
        }
    }

    .promo-box__wrapper {
        padding: 0;
        display: grid;
    }

    .promo-box__container {
        display: flex;

        @include media('<=between') {
            flex-direction: column;
        }
    }

    .course--promo-cards__ranking--first,
    .course--promo-cards__ranking--sec {
        @include media('<=mobile') {
            grid-column: 1/-1;
        }

        @include media('>mobile', '<=between') {
            grid-column: 1/13;
        }
    }

    .featured-profiles-block {
        margin: 0;
        padding: 0 0 $measure--basic 0;

        @include media('<=small') {
            @include default-padding;
        }
    }

    .accordion {
        @include media('<=tablet') {
            .accordion__body {
                background: $color-purewhite;
            }
        }
    }

    .chart-row {
        margin: 0;
        padding: 0;
    }

    .chart__columns {
        @include media('<tablet') {
            display: flex;

            .chart--piechart,
            .chart__legend {
                flex: 1;
            }

            .chart--piechart {
                margin-right: $measure--basic;
            }

            .chart__legend {
                margin-top: 0;
            }
        }
        @include media('>=tablet') {
            display: block;
        }
    }

    .modal__button {
        text-align: left;

        span {
            display: inline;
        }
    }
}

// Clearing
.course--ug__announcement {
    &.course--ug__announcement--clearing.promo-box {
        background-image: none;
        background-color: $color-yellow40;
        color: $color-nearblack;
        margin-top: $measure--basic;
        margin-bottom: -$measure--small4;
        padding: $measure--small2;

        @include media('>=small') {
            padding: $measure--small2 $measure--medium3;
        }

        @include media('>=between') {
            padding: $measure--small2 $measure--small4;
        }

        @include media('<tablet') {
            margin-top: $measure--threequarters;
        }

        a {
            @include anchor-inline(
                $color-nearblack,
                $color-nearblack,
                $color-purewhite,
                $color-nearblack
            );
        }

        .promo-box__card {
            &:first-of-type {
                margin-right: 0;
            }

            .show-more {
                .show-more__content {
                    display: flex;
                    flex-wrap: wrap;
                    max-width: unset;

                    &[data-hidden='true'] {
                        max-height: 16rem;
                        @include media('<tablet') {
                            max-height: 14rem;
                        }
                        &::after {
                            background-image: none;
                            z-index: z-index('below');
                        }
                    }

                    .promo-box__card__body__text__route {
                        flex-basis: 40%;
                        @include media('<tablet') {
                            flex-basis: 100%;
                        }
                    }
                }

                .show-more__button {
                    border-top-color: $color-nearblack;

                    button {
                        @include anchor-inline(
                            $color-nearblack,
                            $color-nearblack,
                            $color-purewhite,
                            $color-nearblack
                        );
                    }

                    .show-more__link-text {
                        border-bottom: 0;
                    }
                }
            }

            .promo-box__card__body__text {
                h2 {
                    display: inline;
                }
                .promo-box__card__body__text__route--wrapper {
                    @include media('>=tablet') {
                        display: flex;
                    }

                    .promo-box__card__body__text__route {
                        margin-right: $measure--small4;
                    }
                }
            }
        }
    }
}

.course--ug__keyinfo__cta {
    border-top: $border-width-default $border-style-solid $color-gray3;

    @include media('>mobile') {
        display: flex;
        justify-content: space-between;
    }

    ul {
        list-style-type: none;
        margin: 0;
    }

    p,
    ul {
        display: block;
        text-align: center;
        width: 100%;

        @include media('>mobile') {
            display: inline-block;
            margin-right: $measure--basic;
        }

        a {
            padding: $measure--half;
            display: block;
        }

        li {
            margin: 0;
        }

        button {
            width: 100%;

            span {
                width: 100%;
            }
        }
    }
    p:last-of-type {
        margin-right: 0;
    }

    @include media('>mobile', '<=tablet') {
        flex-wrap: wrap;
        p,
        ul {
            flex-basis: 50%;

            a {
                text-align: center;
                width: 100%;
            }
        }

        p {
            padding: 0 $measure--half;
            margin: $measure--basic 0 0 0;
        }

        ul {
            margin: 0;
            padding: 0;
        }
    }
}

// tabs styling
.js {
    .course--ug {
        .tabs {
            background: $color-purewhite;
            margin-top: $measure--medium7;

            .tabs__panel__heading {
                text-align: center;
            }

            .tabs__panel__body {
                margin-top: $measure--medium7;
            }
        }

        :not(.tabs--accordion) > .accordion > .accordion__heading {
            border-bottom-color: $color-purewhite;
        }

        .accordion > .accordion__heading[data-open='true'] + .accordion__body {
            @include media('<between') {
                p.ksp__item__overline,
                p.ksp__item__figure {
                    margin: 0;
                }
            }
        }
    }
}

.course--ug__whats-happening {
    background: $color-gray1;
    grid-template-columns: repeat(18, 1fr);
    display: grid;
    grid-column-gap: $measure--small1;
    @include media('<small') {
        @include default-padding;
    }
    h2 {
        grid-column: 2/-2;
        @include media('<small') {
            grid-column: 1/-1;
        }
        padding-top: $measure--basic;
        margin-top: 0;
    }
    .related-content {
        grid-column: 2/-2;
        @include media('<small') {
            grid-column: 1/-1;
        }
    }
}

// Slider pattern override
.js .course--ug__whats-happening {
    .slider {
        &[data-style='arrows'] + .slider__controls {
            grid-column: 1/-1;
        }
    }
}

.course--ug__related-courses__wrapper {
    @include content-grid();
    grid-column-gap: $measure--small1;
    background: linear-gradient(
        360deg,
        rgba(255, 255, 255, 1) 0%,
        rgba(242, 242, 242, 1) 100%
    );
}

.course--ug__related-courses {
    padding: $measure--basic 0;
    grid-column: 2/-2;

    .related-content {
        grid-column: 2/-2;
        .card {
            a {
                &:not(:hover) {
                    background-color: $color-purewhite;
                }
            }
        }
    }

    h2 {
        @include heading--centered($color-pink80);
        grid-column: 1/-1;
    }
}

.course--ug__related-courses__kis-widget {
    grid-column: 2/-2;
}

.course--ug__pathway-image-cards {
    display: grid;
    grid-column-gap: $measure--medium7;
    grid-row-gap: $measure--medium7;
    margin-top: $measure--small4;

    @include media('>=tablet') {
        grid-template-columns: auto auto;
    }
}

.course--ug__student {
    @include media('>small') {
        display: grid;
        @include x-column-grid(18);
    }

    @include media('<=small') {
        @include default-padding;
    }

    h2 {
        @include media('>small') {
            grid-column: 1 / -1;
        }
    }

    .course--ug__student__intro {
        grid-column: 1 / -1;
    }
}

.course--ug__student-testimonials__wrapper {
    @include media('>between') {
        grid-column: 1 / -1;
    }

    .card {
        margin-top: 0;
        .card__thumbnail {
            margin: $measure--basic 0 0;
        }
        .card__details .card__details__text {
            padding-top: 0;
            @include media('<=tablet') {
                padding: 0;
            }
        }
        .card--testimonial__quote {
            margin-top: $measure--basic;
        }
    }
}

.course--ug__student-testimonials {
    grid-column: 8/-1;
}

.course--ug__student-testimonials__half {
    &:nth-child(1) {
        grid-column: 1 / 8;
    }

    &:nth-child(2) {
        grid-column: 8 / -1;
    }

    .card--testimonial__photo {
        margin-top: $measure--quarter;
    }

    .card--testimonial .card--testimonial__text .card--testimonial__quote {
        margin-top: $measure--threequarters;
    }
}

.course--ug__student-testimonials__full-width {
    grid-column: 1 / -1;
}

.course--ug__keyinfo__intro,
.course--ug__keyinfo__details,
.course--ug__keyinfo__accordion,
.course--ug__keyinfo__cta {
    @include default-padding;
    background: $color-purewhite;
    padding-bottom: $measure--basic;
    padding-top: $measure--basic;
    border: $border-width-hairline $border-style-solid $color-gray3;

    @include media('<=mobile') {
        grid-column: 1/-1;
    }

    @include media('>mobile') {
        grid-column: 2/12;
    }

    @include media('>between') {
        grid-column: 3/17;
    }

    .select-filter {
        @include dropdown-filter-select(
            $color-gray5,
            $color-nearblack,
            $color-purewhite
        );
    }
}

.course--ug__keyinfo__accordion,
.course--ug__keyinfo__cta {
    padding-bottom: $measure--small3;
}

.course--ug__keyinfo__intro {
    padding-top: $measure--quarter;

    @include media('>between') {
        margin-top: $measure--small3;
    }
}

.course--ug__keyinfo__details {
    display: flex;
    flex-wrap: wrap;

    * {
        @extend .meta;
    }

    &--column {
        flex: 1;

        @include media('<between') {
            flex-basis: 50%;

            &:nth-child(3) {
                margin-top: $measure--basic;
            }
        }

        @include media('<tablet') {
            flex-basis: 100%;

            &:nth-child(2) {
                margin-top: $measure--basic;
            }
        }
    }

    .label {
        display: block;
        color: $color-gray7;
        margin-bottom: $measure--half;
    }

    p,
    span,
    time {
        @include font-family('heading');
        @include betatype;
        font-weight: $font-weight-medium;
    }

    span:not(.label) {
        display: block;
        color: $color-gray9;
        font-weight: $font-weight-book;
        margin-bottom: $measure--quarter;
    }

    ul,
    p:not(.label) {
        font-weight: $font-weight-book;
        @include betatype();
    }
}

.course--ug__keyinfo__wrapper__chevron {
    display: none;

    @include media('<=mobile') {
        display: flex;
        justify-content: center;
        position: relative;

        span {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            bottom: -$measure--small1;
            border-radius: 100%;
            height: $measure--medium1;
            width: $measure--medium1;
            font-size: $font-size--1080p;
            background: $color-purewhite;
            box-shadow: 0 0 $measure--quarter 0 rgba(0, 0, 0, 0.5);
        }
    }
}

.course--ug__keyinfo__wrapper {
    margin-top: $measure--small4;
    display: grid;

    // Grey background when no image present
    &.no-image {
        background-image: linear-gradient(
            to bottom,
            $color-gray1,
            $color-gray3
        ) !important;
    }
}

.course--ug__keyinfo__details__ucas-code {
    margin-top: $measure--small3;
}

.course--ug__keyinfo__details__location {
    margin-top: $measure--small3;
}

.course--ug__keyinfo__accordion {
    border-top: $border-width-default $border-style-solid $color-gray3;
}

.js {
    .course--ug {
        .dropdown-filter .data-group li {
            padding: 0 $measure--basic $measure--quarter;
        }

        .course--ug__keyinfo__accordion {
            .accordion {
                margin: 0;
                padding: 0;

                .accordion__heading {
                    background: $color-gray1;

                    button {
                        color: $color-nearblack;
                        font-weight: $font-weight-book;
                        @include alphatype();
                    }
                }

                .dropdown-filter {
                    background: $color-purewhite;
                    // Red background and arrow to connect dropdown with selected content
                    .wrapper--dropdown-filter__select {
                        @include dropdown-red-bg;
                    }
                }
            }
        }

        .featured-profiles__heading,
        .wrapper--featured-profiles__heading-intro,
        .wrapper--featured-profiles__profile {
            @include media('<small') {
                grid-column: 1/-1;
            }
        }
    }
}

.course--ug__section-wrapper--grey {
    background-color: $color-gray1;
    padding-top: $measure--basic;

    .tabs--accordion {
        grid-column: 2 / 18;

        @include media('<small') {
            grid-column: 1 / -1;
        }
    }
}

.course--ug__usp {
    grid-column: 2 / 18;
    margin-bottom: $measure--small3;

    @include media('<small') {
        grid-column: 1 / -1;
    }

    .standfirst {
        @include epsilontype();
        color: $color-pink80;
    }
}

.course--ug__section-wrapper--grey {
    background-color: $color-gray1;

    .tabs--accordion:first-of-type {
        padding-bottom: $measure--medium2;
    }
}

.course--ug {
    .tabs--accordion {
        .how-to-apply--ug--js__modal {
            list-style-type: none;
            margin-left: 0;
        }
    }
}

.course--ug__usp__stats {
    background-color: $color-purewhite;

    li {
        display: inline-block;
        list-style-type: none;
        margin-right: $measure--half;

        &:last-of-type {
            margin-right: 0;
        }
    }
}

.course--ug__usp__stats {
    background-color: $color-purewhite;
}

.course--ug__usp__video {
    margin-top: $measure--medium2;

    .embedded-video {
        padding: 0;
    }

    @include media('>tablet') {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: $measure--basic;
    }
}

.course--ug__facilities__video {
    @include media('>tablet') {
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-column-gap: $measure--basic;
    }
}

.course--ug__facilities__video__intro {
    @include media('>tablet') {
        padding: $measure--small4 0;
        p {
            margin: 0;
        }
    }
}

.embedded-video {
    @include media('<=tablet') {
        padding-top: 0;
    }
}

.course--ug__student-testimonial__videos,
.course--ug__student-coursework__videos {
    grid-column: 1/-1;
    .embedded-video {
        padding: $measure--half 0 $measure--small2;
    }
    @include media('>between') {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: $measure--basic;
    }
}

.course--ug__student-coursework {
    grid-column: 1/-1;
    .imageColLayout {
        margin-top: 0;
        figure {
            margin-top: $measure--basic;
        }
    }
}

.statistics-block--ug {
    .ksp__items--ug {
        display: flex;
        justify-content: space-between;

        @include media('<between') {
            flex-direction: column;
        }
    }

    .ksp__item--ug {
        flex: 1;
        margin-right: $measure--small2;

        @include media('<between') {
            margin: $measure--half 0 $measure--half;
        }
    }

    .ksp__item__figure {
        font-weight: $font-weight-light;
        color: $color-red50;

        @include media('<between') {
            margin-top: 0;
        }
    }

    .ksp__item__overline {
        @include font-family('default');
        font-weight: $font-weight-book;
        font-size: $font-size--alpha;
        color: $color-gray7;
    }
}

.course__text_bg {
    span {
        background: $color-purewhite;
        display: inline-block;
        &:first-of-type {
            width: 8ch;
            margin-left: $border-width-default;
        }
    }
}

.ug-text-table {
    margin-top: $measure--basic;
    border: $border-width-hairline $border-style-solid $color-gray4;
    background: $color-purewhite;
    padding: $measure--quarter;

    .ug-text-table__group {
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;
        padding: $measure--quarter 0;

        &:last-child {
            border-bottom: none;
        }
    }

    .ug-text-table__prop {
        min-width: $measure--medium11;

        span {
            @include content-style();
            font-weight: $font-weight-bold;
        }
    }

    .ug-text-table__value {
        span {
            @include content-style();
        }
    }
}

.ug-career__testimonial {
    background-color: $color-gray1;
    margin: $measure--basic 0;

    @include media('<=tablet') {
        padding: $measure--basic;
    }

    .slider {
        margin: 0rem;
    }

    .card {
        margin: 0;
    }

    .card--testimonial,
    .card--testimonial .card--testimonial__photo {
        margin-top: 0;
    }

    .card--testimonial .card__details .card--testimonial__quote {
        margin-top: 0;
        @include media('<=tablet') {
            margin-top: $measure--basic;
        }
    }

    .card .card__details .card__details__text {
        @include media('<=tablet') {
            padding: 0;
        }
    }

    .card--testimonial .card__details .card__thumbnail {
        margin: $measure--basic 0 0 0;
    }
}

.courses--ug__facilities,
.course--ug__student-coursework,
.course--ug__student {
    h2 {
        @include full-width-heading();
    }
}

.courses--ug__facilities {
    @include media('<small') {
        @include default-padding();
    }
}

.course--ug__student-testimonials__half {
    grid-column: 1/-1;

    @include media('>tablet') {
        grid-column: span 12;
    }
}

.course--ug__usp__stats {
    padding: $measure--basic $measure--small2;

    @include media('>between') {
        margin-top: $measure--small2;
    }

    .course--ug__usp__stats__heading {
        margin-top: 0;
    }

    .statistics-block {
        padding: 0 0 $measure--basic 0;

        ul.slider {
            max-width: 100%;
        }

        .ksp__items {
            img {
                margin: auto;
            }
        }

        .ksp__items {
            margin: 0;

            @include media('>tablet') {
                grid-template-columns: repeat(12, 1fr);
                grid-column-gap: 0;
            }

            @include media('>desktop') {
                grid-template-columns: repeat(18, 1fr);
            }

            &.ksp__items.ksp__items--3 {
                .ksp__item:nth-child(1) {
                    @include media('>=tablet') {
                        grid-column-gap: 0;
                        grid-column: 1 / 7;
                    }
                }

                .ksp__item:nth-child(2) {
                    @include media('>=tablet') {
                        grid-column: 7 / 13;
                    }
                }

                .ksp__item:nth-child(3) {
                    @include media('>=tablet') {
                        grid-column: 4 / -4;
                    }
                    @include media('>=desktop') {
                        grid-column: 13 / -1;
                    }
                }
            }

            &.ksp__items.ksp__items--2 {
                .ksp__item:nth-child(1) {
                    @include media('>=tablet') {
                        padding-right: $measure--small4;
                    }

                    @include media('>desktop') {
                        grid-column: 1 / 10;
                    }
                }

                .ksp__item:nth-child(2) {
                    @include media('>=tablet') {
                        grid-column: 7 / -1;
                    }

                    @include media('>desktop') {
                        grid-column: 10 / -1;
                    }
                }
            }
        }
    }
}

@include media('screen') {
    .js .course--ug .tabs__links {
        @include default-margin();
    }

    .js .course--ug .accordion {
        @include media('>tablet') {
            @include standard-box();
        }
    }

    .js .course--ug .tabs {
        @include media('<tablet') {
            @include display-tabs-content(
                $link-background: $color-purewhite,
                $panel-background: $color-gray1,
                $panel-border-color: $color-gray3
            );
        }

        margin-top: 0;

        .tabs {
            background-color: $color-gray1;

            .tabs__panel {
                background-color: $color-purewhite;
            }
        }
    }

    .js main {
        > .course--ug {
            .tabs__panel:not([hidden]) {
                padding: 0;
                .tabs__panel__content {
                    @include standard-grid();

                    grid-template-rows: repeat(2, auto);

                    @include media('<=mobile') {
                        @include default-padding();
                    }

                    .tabs__panel__heading {
                        grid-row: 1;
                    }

                    .tabs__panel__body {
                        grid-row: 2;
                        margin-top: $measure--small4;
                    }

                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 1 / span 24;

                        @include media('>mobile', '<=between') {
                            grid-column: 5 / span 16;
                        }

                        @include media('>between') {
                            grid-column: 7 / span 12;
                        }
                    }
                }

                .tabs .tabs {
                    .tabs__panel:not([hidden]) {
                        padding: $measure--small1 0;
                    }

                    margin: 0;
                    padding: 0;

                    .tabs__panel__content {
                        display: block;
                        padding: 0 $measure--basic;
                    }

                    .tabs__panel__body {
                        margin-top: $measure--small1;
                    }
                }
            }
        }

        > .navigation--secondary + .course--ug {
            .tabs__panel:not([hidden]) {
                padding: $measure--small1 0;

                > .tabs__panel__content {
                    @include media('>=small') {
                        @include content-grid();

                        .tabs__panel__heading,
                        .tabs__panel__body {
                            grid-column: 4 / span 12;
                        }
                    }
                }
            }

            .tabs .tabs {
                margin: 0;
                padding: 0;

                .tabs__panel:not([hidden]) {
                    padding: $measure--small1 0;
                }

                .tabs__panel__content {
                    display: block;
                    padding: 0 $measure--basic;
                }

                .tabs__panel__body {
                    margin-top: $measure--small1;
                }
            }
        }
    }
}

.course--ug__tabs__panel__inner--during {
    grid-column: 2/-1;
}

.course--ug__student-testimonials__half--unibuddy {
    @include media('>small') {
        display: grid;
        @include x-column-grid(18);
    }

    .course--ug__student-testimonials__unibuddy {
        grid-column: 1/8;
        margin-bottom: $measure--small4;
    }

    .unibuddy-card {
        margin-top: $measure--basic;
    }
}

.course--ug__related-links {
    grid-column: 1/-1;
    .related-links--with-images__wrapper {
        @include media('<small') {
            @include default-padding();
        }

        @include media('>=small') {
            padding-left: 0;
            padding-right: 0;
        }
    }

    .related-links__image-container {
        display: none;

        @include media('>=tablet') {
            display: block;
            flex: 0 0 40%;
        }
    }

    .related-links--with-images {
        background: $color-pink80;
        padding: $measure--small4;

        @include media('>=tablet') {
            display: flex;
        }

        .underline-transition__title {
            @include h2style;
            text-align: left;
            margin-top: 0;
            color: $color-purewhite;
        }

        a {
            @include anchor-default-outline($color-purewhite);
            border-bottom-color: $color-gray4;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
        }

        a,
        span {
            color: $color-gray4;
        }
    }

    .related-links--with-images__links {
        @include media('>=tablet') {
            margin-left: $measure--small4;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-purewhite,
                $color-outline--dark
            );
            &:hover {
                color: $color-nearblack;
            }
        }
    }
}

// 'Show more' background colour gradient inside tabs
.js .course--ug .accordion .show-more__content[data-hidden='true']:after {
    background-image: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 0),
        rgba(255, 255, 255, 1) 90%
    );
}

.js
    .course--ug__student-testimonials
    .related-content[data-style='arrows']:not(.slider),
.js .ug-career__testimonial .related-content[data-style='arrows']:not(.slider) {
    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}

// Modal - how to apply
.course--ug + .dialog {
    .dialog__inner {
        background-color: $color-purewhite;
        box-shadow: 0 $measure--half $measure--small1 0 rgba(0, 0, 0, 0.3);
        color: $color-nearblack;
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: $measure--small2;
        max-height: calc(100% - #{2 * $measure--small1});
        overflow-y: scroll;
        position: relative;
        text-align: left;
        width: 90%;
        overflow: scroll;

        @include media('>=tablet') {
            margin-top: 15vh;
            max-height: calc(100% - 30vh);
        }

        @include media('>=small') {
            width: 70%;
        }

        @include media('>=desktop') {
            width: 60%;
        }

        .dialog__body-copy {
            margin-left: auto;
            margin-right: auto;
            max-width: $max-line-length;
        }
    }

    .how-to-apply--ug--js__modal__options {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        list-style: none;
        margin-left: 0;
        max-width: unset;

        // Remove button/link top margin when multiple locations exist. Ensures these selections stay
        // close to relevant heading, helping user identify grouped content.
        &.how-to-apply--pgt--js__options--multiple-locations {
            margin-top: 0;
        }

        li {
            margin-bottom: $measure--half;
            margin-right: $measure--half;
        }

        button,
        a {
            @include cta--border(
                $background: $color-purewhite,
                $color: $color-nearblack,
                $border: $color-gray4,
                $hover-color: $color-nearblack,
                $hover-border: $color-gray4,
                $outline: $color-nearblack,
                $corners: square
            );
        }

        button {
            padding: 0;
            span {
                display: inline-block;
                padding: $measure--half $measure--small2;
            }
        }
    }
}
