////
/// Anchor mixins
/// @group abstracts
////
@import 'reset';
@import 'outline';
@import 'hover';
@import 'ghost-animation';
@import 'inline';
@import 'img';
@import 'img-linked-pattern';
@import 'pagination';
@import 'discreet';
@import 'underline-basic';
@import 'underline-transition';
@import 'icon';
@import 'navigation';
@import 'cta--basic';
@import 'cta--border';
@import 'menu';
@import 'pathway-card';
@import 'card/card';

@mixin cta-like-anchors($background: $color-purewhite, $color: $color-red50) {
    @include cta-primary-icon-light-bg($background, $color);
    @include h5style();

    border-radius: 0;
    border: $border-width-default $border-style-solid $color;
    box-shadow: none;
    height: 100%;
    margin-top: 0;
    width: 100%;

    &:after {
        align-items: center;
        display: flex;
        position: relative;
        right: $measure--half;
    }

    span {
        color: $color-gray11;
        margin: auto 0;
        width: 100%;
        &:active {
            color: $background;
        }
    }

    .cta-like-anchor__text {
        span:nth-child(2) {
            @include content-style();
        }
    }

    &:visited {
        border: $border-width-default $border-style-solid $color;
    }

    &:focus {
        border: $border-width-default $border-style-solid $color;
        span {
            color: $color-gray11;
        }
        &:after {
            color: $color-gray11;
        }
    }

    &:active {
        background: $color;
        // color: red !important;
    }

    &:hover {
        @include media('hover') {
            span {
                color: $background;
            }
            &:after {
                color: $background;
            }
        }
    }
}

@mixin anchor-span() {
    @include anchor-reset();
    @include anchor-hover-background();
    color: $color-inline-link;

    span {
        border-bottom-color: $color-inline-link;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }

    &:focus {
        color: $color-inline-link;

        span {
            outline-color: $color-outline;
            outline-offset: $border-width-thick;
            outline-style: $border-style-dotted;
            outline-width: $border-width-default;
        }
    }

    &:hover {
        @include media('hover') {
            color: $color-text--dark;

            span {
                outline: none;
            }
        }
    }

    &:active {
        background-color: $color-inline-link;
        color: $color-text--dark;

        span {
            outline-color: $color-outline--dark;
            outline-offset: $border-width-thick;
            outline-style: $border-style-dotted;
            outline-width: $border-width-default;
        }
    }
}

@mixin anchor-button() {
    @include anchor-reset();
    color: $color-inline-link;
    border: none;

    &:focus {
        color: $color-text;
        outline-color: $color-outline;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
    }

    &:hover {
        @include media('hover') {
            background-image: none;
            color: $color-text;
        }
    }

    &:active {
        outline-color: $color-outline;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }

    &[disabled] {
        color: $color-text;
        &:focus,
        &:hover,
        &:active {
            outline: none;
        }
    }
}

article {
    a {
        @include anchor-inline();
    }
}
