// <Desktop-specific layout - secondary navigation never present
main > .event {
    @include media('<small') {
        .wrapper--event__content,
        .event__formassembly__form {
            @include default-margin();
        }

        .event__content,
        .event__contact {
            @include default-padding();
        }
    }

    @include media('>=tablet', '<small') {
        .wrapper--event__content {
            @include standard-grid();

            margin-left: 0;
            margin-right: 0;

            .event__content {
                grid-column: 4 / span 18;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    @include media('<tablet') {
        .event__content {
            padding: 0;
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .event {
            grid-column: span 24;

            .wrapper--event__content,
            .event__contact {
                @include standard-grid();
            }

            .event__content {
                grid-column: 6 / span 14;
            }

            .event__contact {
                @include standard-grid();

                .event__contact__details {
                    grid-column: 2 / span 9;
                }

                .event__contact__location {
                    grid-column: 11 / span 13;
                }

                .event__contact__details:only-child,
                .event__contact__location:only-child {
                    grid-column: 2 / span 22;
                }
            }
        }
    }

    @include media('>=desktop') {
        > .event {
            .event__content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .event {
                grid-column: 7 / span 18;

                .wrapper--event__content {
                    @include content-grid();
                }

                .event__content {
                    grid-column: 4 / span 12;
                }

                .event__contact {
                    @include content-grid();

                    .event__contact__details {
                        grid-column: 2 / span 7;
                    }

                    .event__contact__location {
                        grid-column: 9 / span 9;
                    }

                    .event__contact__details:only-child,
                    .event__contact__location:only-child {
                        grid-column: 2 / span 16;
                    }
                }
            }
        }
    }
}

.event {
    .embedded-video,
    .carousel-gallery-default {
        margin: $measure--threequarters 0;
    }

    .related-content {
        li:only-child {
            grid-column: 1/-1;
        }
    }

    .footer-cards {
        grid-column: 1 / -1;
        color: $color-purewhite;

        h2 {
            border-bottom: $border-width-hairline solid $color-red30;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-gray11,
                $color-outline--dark
            );
        }
    }

    .pathway .pathway__cards {
        padding-left: 0;
    }
}

// standfirst

.event__standfirst {
    @include gammatype();
    @include font-family('heading');
    font-weight: $font-weight-light;
}

//event video

.event {
    .embedded-video {
        padding: 0;
    }
}

//event social card

.event__social-cards {
    margin-top: 0;
    margin-left: 0;
    max-width: unset;
}

//event agenda card

.event__agenda {
    list-style-type: none;
    margin-top: 0;
    margin-left: 0;
    max-width: unset;
}

.event__agenda--card {
    background-color: $color-purewhite;
    padding: $measure--basic;
    display: flex;
    @include box-shadow();
}

.event__agenda--card__icon {
    min-width: $measure--medium3;

    span {
        background-color: $color-red50;
        color: $color-purewhite;
        padding: $measure--half;
        @include smalltype();
    }
}

.event__agenda--card__details--time {
    color: $color-gray7;
    @include smalltype();
}

//speaker
.event__speakers {
    list-style-type: none;
    margin-top: 0;
    margin-left: 0;

    li:after {
        content: '';
        display: inline-block;
        width: 100%;
        height: $measure--basic;
        border-bottom: $border-width-default dotted $color-gray7;
    }
}

.event__speakers__name {
    @include gammatype();
}

.event__speakers__name,
.event__speakers__description {
    display: block;
}

.event__speakers__description {
    color: $color-gray7;
}

.event__speakers__details {
    display: flex;
    @include smalltype();
    span {
        @include alphatype();
        margin-right: $measure--half;
    }
}

//sponsor

.event__sponsors {
    margin-top: 0;
    margin-left: 0;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: space-between;

    li {
        margin-top: $measure--half;
        width: 40%;
    }
    @include linked-img();
}

//event tags

.event__tags {
    h2 {
        font-family: $font-family-plex-sans;
        color: $color-gray7;
        margin-bottom: $measure--basic;
        @include smalltype();
    }

    .cta-block__cta {
        margin: 0 $measure--small2 $measure--threequarters 0;
    }
}

//event guide

.event__guide {
    margin: $measure--basic 0;
}

.event_related-events {
    grid-column: 1/ -1;
    background-color: $color-gray1;

    .event_related-events__wrapper {
        padding: $measure--basic 0;
        @include default-margin();
    }

    h2 {
        text-align: center;
    }

    .related-content {
        flex-wrap: wrap;

        @include media('<small') {
            @include default-margin;
        }

        @include media('>mobile') {
            display: flex;
            justify-content: space-between;
        }
    }

    li {
        @include media('>small') {
            width: 32%;
        }
        @include media('>=mobile', '<=small') {
            width: 47%;
        }
    }
}

.wrapper--event__social-icons {
    margin-bottom: $measure--small4;
    margin-left: 0;
    margin-right: 0;
    margin-top: $measure--small4;
}

.event__social-icons {
    border-bottom: $border-width-hairline $border-style-solid $color-gray2;
    border-top: $border-width-hairline $border-style-solid $color-gray2;
    position: relative;
}

.wrapper--event__contact {
    margin-top: $measure--basic;
    padding-bottom: $measure--small4;
    padding-top: $measure--basic;

    .event__contact__heading {
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-thicker;
        padding-bottom: $measure--half;
        text-align: left;
    }

    .event__contact {
        .related-content {
            align-items: flex-start;
            display: inline-flex;
            flex-direction: column;

            .card--contact {
                background: initial;
                box-shadow: initial;

                .card__details__text {
                    padding-left: 0;
                    padding-right: 0;
                }
            }

            li + li {
                @include media('<small') {
                    border-top-style: $border-style-solid;
                    border-top-width: $border-width-hairline;
                    padding-top: $measure--small4;
                }
            }
        }
    }
}

.wrapper--event__contact {
    .event__contact__heading {
        border-bottom-color: $color-red50;
    }

    .event__contact {
        .related-content {
            li + li {
                border-top-color: $color-red70;
            }
        }
    }
}

// Pattern overrides
.js .event .slider[data-style='arrows'] + .slider__controls,
.js .event .slider[data-style='responsive'] + .slider__controls {
    @include media('<tablet') {
        margin-right: $measure--basic;
    }
}

/// new banner
///
///

.banner-standfirst-label--event {
    @include media('<tablet') {
        h1,
        .evnt__info {
            text-align: left;
        }
    }
}

main {
    header {
        .evnt__info {
            padding-top: $measure--small4;
            p {
                @include content-style();
            }
        }

        .evnt__info__status {
            p {
                line-height: $line-height-ratio--override--gamma;
            }
        }

        .evnt__formassembly__form {
            @include default-margin();
        }

        .event__formassembly__button {
            @include cta-basic-light-bg();
        }

        .event__formassembly__form {
            margin-top: $measure--basic;
        }
    }
}

.evnt__info__details {
    margin-bottom: $measure--basic;
    text-align: left;

    @include media('>small') {
        text-align: center;
    }

    .icon {
        margin-right: $measure--half;
        @include smalltype();
        color: $color-red50;
        font-weight: $font-weight-normal;
    }

    p {
        margin-top: 0;
        text-align: left;
    }

    ul {
        margin-top: 0;
        margin-left: 0;
        list-style: none;
        max-width: initial;
    }

    ul.evnt__info__details--block {
        @include media('>small') {
            display: grid;
            @include x-column-grid(2);
        }
    }

    ul.evnt__info__details--inline {
        @include media('>small') {
            display: inline-flex;
            flex-wrap: wrap;
        }
    }

    li {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-top: 0;

        @include media('>=tablet') {
            margin-bottom: $measure--half;
            margin-right: $measure--half;
        }
    }
}

.evnt__info__status {
    @include media('>tablet') {
        margin-bottom: $measure--basic;
    }

    @include media('<=tablet') {
        margin-top: $measure--basic;
        margin-left: $measure--half;
    }

    p {
        display: inline;
        background-color: $color-gray12;
        box-shadow: $measure--half 0 0 $color-gray12,
            -$measure--half 0 0 $color-gray12;
        padding: $measure--half;
        color: $color-purewhite;
    }
}

.evnt__info__status.evnt__cancel-postponed {
    p {
        background-color: $color-red50;
        box-shadow: $measure--half 0 0 $color-red50,
            -$measure--half 0 0 $color-red50;
    }
}

.evnt__info__status.evnt__past {
    p {
        background-color: $color-nearblack;
        box-shadow: $measure--half 0 0 $color-nearblack,
            -$measure--half 0 0 $color-nearblack;
    }
}

.evnt__info__status + .evnt__info__cta {
    @include media('<tablet') {
        margin-top: $measure--basic;
    }
}

.evnt__info__cta {
    grid-column: 11 / span 6;

    ul {
        list-style: none;
        margin-top: 0;
        margin-left: 0;
        max-width: initial;
    }

    li {
        display: inline;
        margin-top: 0;

        @include media('>small') {
            margin-right: $measure--basic;
        }

        @include media('>mobile', '<small') {
            display: inline;
            margin-right: $measure--basic;
        }

        @include media('<=mobile') {
            display: block;
            padding-bottom: $measure--threequarters;
        }
    }
}

.evnt__info__text {
    margin: $measure--threequarters 0;
    border-top: $border-width-hairline dotted $color-gray6;
    border-bottom: $border-width-hairline dotted $color-gray6;

    .evnt__info__text--recurring {
        padding: $measure--threequarters 0;

        .fa-redo {
            margin-right: $measure--half;
        }

        p {
            max-width: initial;
            margin-top: 0;
        }

        .evnt__info__text--recurring--text {
            color: $color-gray8;
        }
    }
}
