// <Desktop-specific layout - secondary navigation never present
main {
    @include media('<tablet') {
        > .standard-content {
            .banner {
                .banner__block-color {
                    display: none;
                }
            }

            .pathway[data-fullwidth='yes'] {
                margin: 0 $measure--small2;
            }
        }
    }

    @include media('>=tablet', '<small') {
        > .standard-content {
            @include standard-grid();
            grid-column: span 24;

            .pathway[data-fullwidth='yes'] {
                @include standard-grid();
                grid-column: span 24;
            }

            .banner {
                grid-column: span 24;
            }

            .content {
                grid-column: 4 / span 18;
            }
        }
    }

    .standard-content {
        .content {
            overflow: hidden;
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .standard-content {
            @include standard-grid();

            grid-column: span 24;

            .banner {
                grid-column: span 24;
                .banner__standfirst {
                    grid-column: 2/-2;
                }
            }

            .content {
                grid-column: 6 / span 14;
            }
        }
    }

    @include media('>=desktop') {
        > .standard-content {
            .content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .standard-content {
                @include content-grid();

                grid-column: 7 / span 18;

                .banner {
                    grid-column: 1 / span 18;
                    .banner__standfirst {
                        grid-column: 2/-2;
                    }
                }

                .content {
                    grid-column: 4/-4;
                    overflow: hidden;
                }

                .pathway,
                .related-links--with-images {
                    &[data-fullwidth='yes'] {
                        @include media('>=small') {
                            @include content-grid();
                        }
                    }
                }
            }
        }
    }
}

// Generic styles
.standard-content {
    padding-bottom: $measure--small2;

    .content {
        @include media('>=tablet') {
            margin: 0;
        }
    }

    /** Start: SCP full-width pattern customisation **/
    .pathway {
        .pathway__cards {
            grid-template-columns: repeat(2, 1fr);
        }
        &[data-fullwidth='yes'] {
            grid-column: 1/-1;
            .pathway__cards {
                grid-column: 2/-2;
                grid-template-columns: repeat(3, 1fr);
            }
            @include media('<tablet') {
                @include default-margin();
            }
            .pathway__heading {
                @include media('>=tablet') {
                    grid-column: 2/-2;
                }
            }
        }
    }

    .related-links--with-images {
        &[data-fullwidth='yes'] {
            grid-column: 2/-2;
            @include media('<tablet') {
                @include default-margin();
                padding: 0 $measure--half;
            }
            .related-links--with-images__cards {
                grid-column: 2/-2;
                grid-template-columns: repeat(3, 1fr);
            }
            .related-links--with-images__heading {
                @include media('>=tablet') {
                    grid-column: 2/-2;
                }
            }
        }
    }
    /** End: SCP full-width pattern customisation **/

    .wrapper--tabs-accordion-full-width {
        grid-column: 2/-2;
        @include media('<tablet') {
            grid-column: 1/-1;
            @include default-margin();
        }
    }
    /** End: full-width patterns **/
}

.js .standard-content .related-content:not(.slider) {
    li:only-child {
        grid-column: 1/-1;
        max-width: unset;
    }
}
