// <Desktop-specific layout - secondary navigation never present
main > .landing--informational {
    @include media('>=small') {
        @include standard-grid();
        .landing__standfirst,
        .landing__promotional,
        .landing__profiles,
        .landing__related {
            @include standard-grid();
        }
        .landing__related__content,
        .landing__related__latest,
        .landing__profiles__flexible {
            grid-column: 2/-2;
        }
    }

    @include media('<small') {
        .landing__pathway .pathway {
            padding-left: $measure--basic;
            padding-right: $measure--basic;
        }

        .landing__related {
            .related-content {
                li + li {
                    margin-top: $measure--small2;
                }
            }
        }
    }

    @include media('>=between', '<small') {
        .landing__pathway .pathway {
            padding-left: $measure--small2;
            padding-right: $measure--small2;
        }
    }

    @include media('>=tablet', '<small') {
        .landing--informational__content__wrap {
            @include standard-grid();

            .landing__standfirst > .standfirst,
            .landing__promotional__flexible,
            .landing__promotional__heading,
            .landing__promotional__content,
            .landing__promotional__cta,
            .landing__pathway__content {
                grid-column: 6 / span 14;
                margin-left: 0;
                margin-right: 0;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    @include media('>=tablet') {
        .landing__pathway .pathway .pathway__cards {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-grid();
        > .landing--informational {
            .landing__pathway {
                grid-column: 2/-2;
                display: grid;
                @include x-column-grid(22);
                .pathway {
                    grid-column: 1/-1;
                }
            }

            .landing__standfirst > .standfirst,
            .landing__promotional__flexible,
            .landing__promotional__heading,
            .landing__promotional__content {
                grid-column: 7 / span 12;
            }

            .landing__pathway__content {
                grid-column: 6 / span 13;
            }

            .landing__promotional__cta {
                grid-column: 5 / span 16;

                .cta-block {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    justify-content: center;

                    li {
                        &:not(:last-of-type) {
                            margin-right: $measure--small2;
                        }
                    }
                }
            }

            .landing__related {
                .wrapper--landing__contact {
                    grid-column: 1/-1;
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .landing--informational {
                .landing__standfirst,
                .landing__promotional,
                .landing__related,
                .landing__pathway,
                .landing__profiles {
                    @include content-grid();
                }

                .landing__related__content,
                .landing__profiles__flexible {
                    grid-column: 2/-2;
                }
                .landing__pathway {
                    grid-column: 1/-1;
                    .pathway {
                        grid-column: 1/-1;
                        @include x-column-grid(16);
                        display: grid;
                        .pathway__heading,
                        .pathway__cards {
                            grid-column: 2/-2;
                        }
                    }
                }

                .landing__standfirst > .standfirst,
                .landing__promotional__flexible,
                .landing__promotional__heading,
                .landing__promotional__content,
                .landing__promotional__cta,
                .landing__profiles__flexible,
                .landing__pathway__content {
                    grid-column: 4 / span 12;
                }
            }
        }
    }
}

// Generic styles
.landing--informational {
    .landing__standfirst,
    .landing__promotional,
    .landing__pathway,
    .landing__profiles,
    .landing__profiles__profiles,
    .landing__related {
        grid-column: 1/-1;
    }

    .landing__pathway__content {
        padding-bottom: $measure--medium1;
    }

    @include media('<small') {
        .landing__promotional__flexible,
        .landing__promotional__heading,
        .landing__promotional__content,
        .landing__promotional__cta,
        .landing__pathway__content,
        .landing__profiles__flexible,
        .landing__related__content,
        .landing__related__latest {
            @include default-margin();
            grid-column: 1/-1;
            padding-left: 0;
            padding-right: 0;
        }
    }

    .landing__promotional__flexible {
        margin-bottom: $measure--basic;

        h2:first-of-type {
            text-align: center;
        }
    }

    .landing__related {
        .wrapper--landing__contact {
            .landing__contact__heading {
                text-align: left;
            }
        }

        .landing__related__content__heading {
            margin-bottom: $measure--basic;
        }
    }

    .landing__pathway {
        margin-top: $measure--small2;
    }

    .landing__promotional__cta {
        .cta-block {
            margin-bottom: $measure--small2;
            margin-top: $measure--small2;
            max-width: unset;
        }
    }

    .wrapper--landing__contact {
        background-color: $color-gray1;
    }
}
