// Element where SVG def rules are defined
.svg-defs {
    display: block;
}

$chart-themes: (
    'blue': (
        $color-blue40,
        $color-blue100,
        url(#striped),
        $color-blue70,
    ),
    'red': (
        $color-red40,
        $color-red100,
        url(#striped),
        $color-red70,
    ),
    'pink': (
        $color-pink50,
        $color-pink100,
        url(#striped),
        $color-pink70,
    ),
);

@each $color-key, $color-values in $chart-themes {
    @each $color in $color-values {
        .chart--#{$color-key} {
            $i: index($color-values, $color);

            path {
                &:nth-child(#{$i}) {
                    &:not(.segment-empty) {
                        stroke: $color;
                    }

                    // Fill hairline gap with appropriate colour when first segment empty
                    &[segment-empty-first='true'] {
                        stroke: nth($color-values, 2);
                    }

                    // Fill hairline gap with appropriate colour when second segment empty
                    &[segment-empty-second='true'] {
                        stroke: nth($color-values, 1);
                    }
                }
            }

            .chart__legend__item:nth-child(#{$i}) {
                // If no 100/0 split, loop through theme colours and apply to each legend item
                &:not([segment-full]) {
                    .fa.fa-circle {
                        color: $color;
                    }
                }

                // If 100/0 split and second segment is full, apply second theme colour theme
                &[segment-full='second'] {
                    .fa.fa-circle {
                        color: nth($color-values, 2);
                    }
                }
            }
        }
    }
}

.pie-chart-block {
    .chart__legend__item:nth-child(3) {
        .fa.fa-circle::before {
            display: none;
        }

        svg {
            display: block;
        }
    }

    .chart__legend__item:nth-child(4) {
        svg {
            display: none;
        }
    }
}

.chart--bar--horizontal.chart--pink {
    .chart--bar--horizontal__value {
        background: $color-pink50;
    }

    .chart--bar--horizontal__collection {
        background: $color-pink80;
    }
}

.chart-row {
    margin-top: $measure--medium7;
    @include standard-box;
    // Hide individual chart title when one chart visible
    &.chart-row--columns1 {
        .chart__title {
            display: none;
        }
    }
    @include media('>=tablet') {
        display: flex;
        &.chart-row--columns1 {
            display: block;
            .chart {
                margin-top: $measure--small4;
                .chart__columns {
                    display: flex;
                    .chart--piechart {
                        width: 33%;
                        margin-right: 0;
                        .chart--pie-chart__svg-wrapper {
                            margin-right: $measure--medium4;
                        }
                    }
                    .chart__legend {
                        margin-top: $measure--half;
                        width: 60%;
                    }
                }
            }
        }
        &:not(.chart-row--columns1) {
            flex-wrap: wrap;
            .chart {
                width: 33%;
                &:nth-of-type(3) {
                    margin-right: 0;
                }
                .chart-row__group-heading,
                .chart-row__group-intro {
                    flex-basis: 100%;
                }
            }
        }
    }
    .chart {
        margin-bottom: $measure--small4;
        .chart__legend {
            margin-right: $measure--small1;
        }
    }
}

.chart-row__group-heading,
.chart__title {
    width: 100%;
}

.chart--piechart {
    margin-right: $measure--medium4;
    svg {
        width: 100%;
    }
}

.chart__legend {
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.chart__legend__item__icon {
    font-size: $measure--small1;
    margin-right: $measure--threequarters;
}

.chart__legend {
    margin-top: $measure--small2;
}

.chart__title,
.chart-row__group-heading {
    margin-bottom: $measure--small2;
}

.chart__legend__item {
    margin-bottom: $measure--basic;
    align-items: center;
    display: flex;
    line-height: 1.5;
}

.chart--piechart__value {
    @include svg-path-animation;
}

.chart--bar--horizontal {
    margin-top: $content-element-margin;
}

.chart--bar--horizontal__collection {
    display: flex;
    margin-bottom: $measure--basic;
    position: relative;
    align-items: center;
    padding: $measure--small2 $measure--half;
    color: $color-purewhite;

    @for $i from 0 through 100 {
        &[data-bar-width='#{$i}'] {
            width: round(percentage($i/100));
        }
    }
}

.chart--bar--horizontal__category {
    position: absolute;
    right: $measure--basic;

    // Prevent text being obscured on narrower viewports
    margin-left: $measure--medium7;
    text-align: center;
}

.chart--bar--horizontal__value {
    padding: $measure--basic $measure--half;
    position: absolute;
    top: 0;
    left: 0;
    align-items: center;
    height: 100%;
    display: flex;

    @for $i from 0 through 100 {
        &[data-segment-width='#{$i}'] {
            width: round(percentage($i/100));
        }
    }

    &:after {
        content: '';
        position: absolute;
        right: -$measure--half;
        width: $measure--half;
        height: 0px;
        border-top: $measure--half $border-style-solid transparent;
        border-bottom: $measure--half $border-style-solid transparent;
        border-left: $measure--half $border-style-solid $color-pink50;
    }

    span {
        &[data-hidden='true'] {
            @include media('<large') {
                display: none;
            }
        }
    }
}
