@mixin timetable($color: $color-pink80) {
    padding-bottom: $measure--small2;
    margin-top: $measure--small2;

    .timetable-heading {
        color: $color-nearblack;
        display: table;
        height: 100%;
        padding: $measure--small2 $measure--basic $measure--small2
            $measure--basic;
        width: 100%;

        @include media('>tablet') {
            border-left: $border-width-thick dotted $color;
            padding: 0;

            span {
                display: table-cell;
                text-align: center;
                vertical-align: middle;
            }
        }

        span {
            font-weight: $font-weight-bold;
        }
    }

    .timetable-row {
        background-color: $color-purewhite;
        margin-bottom: $border-width-default;
        padding: $measure--quarter $measure--basic;

        &:last-child {
            margin-bottom: 0;
        }

        span {
            line-height: $line-height-ratio--body;
            padding: $measure--quarter 0;

            @include media('>tablet') {
                padding: 0;
                display: inline-block;
            }

            &.separator {
                color: $color-gray4;
                padding: 0;
            }
        }

        &:last-of-type {
            .icon {
                color: $color;
                margin-right: $measure--quarter;
            }
        }
    }
}

@mixin timetable-terms($color: $color-pink80) {
    @include media('>tablet') {
        display: grid;
        grid-template-columns: $measure--huge1 auto;
    }

    .timetable-column:first-child {
        border-top: $border-width-thick dotted $color;

        @include media('>tablet') {
            border: none;
        }
    }
}

.timetable {
    @include timetable();
}

.timetable-terms {
    @include timetable();
    @include timetable-terms();
}
