////
/// Infographic
/// @group patterns
////

$colors: (
    blue50: (
        $color-blue50,
        $color-purple50,
    ),
    purple50: (
        $color-purple50,
        $color-blue50,
    ),
    red50: (
        $color-red50,
        $color-purple50,
    ),
);

.infographic {
    @include media('<tablet') {
        .infographic__panel {
            padding: $measure--small4 $measure--basic;
            &.infographic__panel__panel1.background--purple50 {
                padding-bottom: 0;
            }
        }
    }

    @include media('>=tablet') {
        display: flex;
        flex-wrap: wrap;
        .infographic__panel {
            flex: 1;
            display: flex;
            justify-content: center;
            flex-direction: column;
            padding: $measure--small4;
        }
    }

    // Panel background and text highlight
    @each $name, $color in $colors {
        $color-primary: nth($color, 1);
        $color-secondary: nth($color, 2);
        &[data-theme='#{$name}'] {
            .infographic__panel {
                .infographic__description__text__highlight {
                    @include text-highlight($color-primary);
                }
                &[data-theme-background='true'] {
                    .infographic__description__text__highlight {
                        @include text-highlight($color-secondary);
                    }
                }
            }
        }
    }

    .infographic__panel {
        &[data-type='image'] {
            text-align: center;
        }
        &[data-type='text'] {
            .infographic__description__text {
                margin: 0;
                color: $color-purewhite;
            }
            span:not(.infographic__description__text__highlight) {
                &:not(last-of-type) {
                    margin-right: $measure--threequarters;
                }
            }
            span.infographic__description__text__highlight {
                font-weight: $font-weight-medium;
                line-height: calc(
                    #{$measure--small4} + #{$measure--quarter}/ 2
                );
                margin: 0 $measure--threequarters 0 0;
            }
        }
    }

    .infographic__caption {
        flex-basis: 100%;
        .infographic__caption__source {
            display: flex;

            a {
                margin-left: $measure--quarter;
                margin-right: $measure--quarter;
            }
        }
    }
}
