/* Provide a full-width heading area, where the bottom border is applied to give
the impression for a full-width underline on larger screens. As this extends beyond the regular grid,
the standard grid wrapper mixin is applied to the child element with class
'full-width-heading'.

Example HTML:
<div class="wrapper--course--pgt__profiles__heading">
    <div class="course--pgt__profiles__heading full-width-heading">
        <h2>Our academics</h2>
    </div>
</div>

Assign mixin:
.wrapper--course--pgt__profiles__heading {
    @include full-width-heading();
}
*/

@mixin full-width-heading($border-color: $color-gray4) {
    border-bottom: $border-width-hairline $border-style-solid $border-color;
    padding-bottom: $measure--half;
    .full-width-heading {
        grid-column: 1/-1;
        @include media('>tablet') {
            &:first-child {
                grid-column: 2/-2;
            }
        }
    }
}
