// General
.course--pgt__our-students_heading {
    grid-column: 1/-1;
    @include full-width-heading();
}

@include media('>=small') {
    .course--pgt,
    .course--pgt__section-wrapper--gray,
    .course--pgt__profiles,
    .wrapper--course--pgt__student-life,
    .wrapper--course--pgt__related-courses {
        @include content-grid();
    }

    main > .course--pgt {
        .section--wrapper--course--pgt_introduction,
        .course--pgt__tabs,
        .course--pgt__section-wrapper--gray,
        .course--pgt__profiles,
        .course--pgt__our-students,
        .wrapper--course--pgt__student-life,
        .wrapper--course--pgt__whats-happening,
        .wrapper--course--pgt__related-courses,
        .course-footer {
            grid-column: 1/-1;
        }

        .course--pgt__profiles_heading,
        .course--pgt__keyinfo,
        .wrapper--course--pgt__usp,
        .featured-profiles-block,
        .course--pgt__our-students,
        .course--pgt__student-life,
        .course--pgt__related-courses {
            grid-column: 2/-2;
        }
    }
}

@include media('<small') {
    .course--pgt__section-wrapper--gray {
        @include default-padding();
        .course--pgt__keyinfo,
        .wrapper--course--pgt__usp,
        .course--pgt__tabs {
            padding-left: 0;
            padding-right: 0;
        }
    }
}

// Banner
.course--pgt {
    @include banner($color: $color-pink80);

    .banner__overlay {
        .banner__overlay__heading {
            grid-column: 1/-1;
            @include media('>small') {
                grid-column: 2/-2;
            }
            padding: 0;
            margin: 0;
        }
    }

    .wrapper--course--pgt__introduction {
        @include content-grid();
        @include media('<=small') {
            @include default-padding();
        }
        @include media('>tablet') {
            padding-bottom: $measure--basic;
            margin-top: 0;
        }

        a,
        button {
            background-image: none;

            &:focus,
            &:active {
                @include outline-properties($color-purewhite);
            }
            &:active {
                background-color: $color-purewhite;
            }
        }

        @include media('<=tablet') {
            button {
                margin: 0 auto;
            }
        }

        .course--pgt__introduction {
            color: $color-purewhite;
            grid-column: 1/-1;
            @include media('>small') {
                grid-column: 2/-2;
            }

            .course--pgt__introduction__year-select {
                grid-column: 3 / 10;

                @include media('<=tablet') {
                    grid-column: 1 / -1;
                }

                p {
                    font-weight: $font-weight-book;
                    font-size: $font-size--beta;

                    @include media('<=tablet') {
                        max-width: unset;
                    }

                    &.course--pgt__introduction__year-select__other {
                        margin-top: 0;
                    }
                    a {
                        @include anchor-inline(
                            $color-gray4,
                            $color-gray4,
                            $color-gray11,
                            $color-outline--dark
                        );
                    }
                }
            }

            .course--pgt__introduction__ctas {
                grid-column: 1 / -1;
                padding: $measure--basic 0;
                text-align: center;

                @include media('>tablet') {
                    grid-column: 3/-2;
                    display: flex;
                    justify-content: space-between;
                }

                @include media('<=tablet') {
                    > .course--pgt__introduction__ctas--apply {
                        > .cta-block {
                            > .cta-block__cta {
                                a {
                                    display: block;
                                }
                            }
                        }
                    }

                    > .course--pgt__introduction__ctas--apply,
                    > .course--pgt__introduction__ctas--video {
                        > .cta-block {
                            max-width: unset;

                            > .cta-block__cta,
                            li {
                                margin-left: auto;
                                margin-right: auto;
                                max-width: unset;
                            }
                        }
                    }
                    > .how-to-apply--pgt--js {
                        .cta-block__cta {
                            width: auto;
                            .primary-cta-arrow--bright {
                                width: 100%;
                                text-align: center;
                                span {
                                    margin: 0 auto;
                                }
                            }
                        }
                    }
                }

                @include media('<=mobile') {
                    > .how-to-apply--pgt--js {
                        .cta-block__cta {
                            width: 100%;
                        }
                    }
                }

                .course--pgt__introduction__ctas--video {
                    @include media('<=tablet') {
                        margin-top: $measure--small2;
                    }

                    .dialog__inner {
                        text-align: left;
                        color: $color-gray10;
                        width: 90%;
                        @include media('>tablet') {
                            width: 60%;
                        }
                        .dialog__close {
                            outline-offset: 0;
                            &:focus {
                                outline-color: $color-outline;
                            }
                        }
                    }

                    span {
                        display: block;
                        @include media('>tablet') {
                            display: inline-block;
                        }
                    }

                    .cta-block {
                        @include media('>tablet') {
                            margin-left: $measure--basic;
                            display: inline-block;
                        }

                        .cta-block__cta {
                            margin-right: 0;
                            @include media('<=tablet') {
                                margin: $measure--small2 auto;
                            }
                        }
                    }
                }
            }
        }
    }
}

// Grey top section
.course--pgt__section-wrapper--gray {
    padding-bottom: $measure--basic;
}

// Promo content
.course--pgt__promo__content {
    @include content-grid();
    grid-column: 2/-2;
    .promo-box {
        grid-column: 1/-1;
        margin-top: $measure--basic;
    }
}

.wrapper--course--pgt__profiles__heading,
.wrapper--course--pgt__facilities__heading {
    @include content-grid();
    @include full-width-heading();
    .full-width-heading:first-child {
        grid-column: 1/-1;
    }
    @include media('<small') {
        padding-left: 0;
        padding-right: 0;
        margin: $measure--basic $measure--small2 0 $measure--small2;
    }
}

// Profiles
.course--pgt__profiles {
    grid-column: 1/-1;
    padding-bottom: $measure--small4;
    @include media('<small') {
        @include default-padding();
    }
    .featured-profiles-block {
        margin: 0;
        padding: 0;
    }
    .wrapper--featured-profiles__heading-intro,
    .wrapper--featured-profiles__profile {
        margin-top: $measure--basic;
        grid-column: 1/-1;
    }
    .wrapper--featured-profiles__heading-intro {
        margin-top: 0;
    }
}

// Key information
.course--pgt__keyinfo {
    padding-top: $measure--basic;
    @include content-grid();
    .course--pgt__keyinfo__content {
        grid-column: 1/-1;
        .course--pgt__keyinfo__content__column__item {
            * {
                @extend .meta;
            }
            margin-bottom: $measure--basic;
            :not(span) {
                font-weight: $font-weight-medium;
                @include betatype();
            }
        }
        span {
            display: block;
            margin-bottom: $measure--quarter;
            color: $color-gray9;
            font-weight: $font-weight-book;
            @include font-family('default');
            &.label {
                @include font-family('heading');
                color: $color-gray7;
                font-weight: $font-weight-normal;
                &.color--pink80 {
                    color: $color-pink80;
                    font-weight: $font-weight-medium;
                }
            }
        }

        // By section
        .course--pgt__keyinfo__content__standfirst {
            padding: $measure--basic $measure--small2;
            background: $color-purewhite;
            margin: 0 0 $measure--basic;
            .course--pgt__keyinfo__content__heading {
                margin-top: 0;
            }
        }

        .wrapper--course--pgt__keyinfo__content__date-location {
            background: $color-purewhite;
            padding: $measure--basic $measure--small2 $measure--basic;
            .course--pgt__keyinfo__content__date-location {
                background: $color-purewhite;
                padding-top: $measure--threequarters;
                padding-bottom: $measure--basic;
                border-bottom: $border-width-default $border-style-solid
                    $color-gray3;
                @include media('<=tablet') {
                    padding-bottom: $measure--half;
                    margin-bottom: $measure--small1;
                }
            }
        }

        .wrapper--course--pgt__keyinfo__content__duration {
            background: $color-purewhite;
            padding: 0 $measure--small3 $measure--basic;
            .course--pgt__keyinfo__content__duration {
                display: grid;
                grid-column-gap: $measure--small2;
                grid-template-columns: repeat(14, 1fr);
                @include media('>tablet') {
                    border-bottom: $border-width-default $border-style-solid
                        $color-gray3;
                }
                .course--pgt__keyinfo__content__duration__qualification-type {
                    margin-bottom: $measure--basic;
                    grid-column: 1/-1;
                    @include media('>tablet') {
                        // Larger screens: group first qualification type in first column; others in second
                        &:nth-child(1) {
                            grid-column: 1/7;
                        }
                        &:nth-child(n + 2) {
                            grid-column: 7/-1;
                        }
                    }
                }
            }
        }

        .course--pgt__keyinfo__content__accreditation {
            background: $color-purewhite;
            padding-bottom: $measure--small1;
            @include media('>tablet') {
                display: flex;
                padding-top: $measure--small1;
                border-bottom: $border-width-default $border-style-solid
                    $color-gray3;
            }
            .course--pgt__keyinfo__content__column {
                &:nth-child(1) {
                    flex-basis: 33%;
                }
                &:nth-child(2) {
                    flex-basis: 66%;
                }
            }
        }

        // CTAs
        ul.course--pgt__keyinfo__content__ctas {
            grid-column: 1 / -1;
            margin: $measure--basic 0;
            max-width: none;

            @include media('>tablet') {
                grid-column: 3 / -3;
                display: flex;
            }

            li {
                list-style-type: none;

                @include media('>tablet') {
                    &:not(:last-of-type) {
                        margin-right: $measure--basic;
                    }
                }

                a {
                    width: 100%;
                    height: 100%;
                    box-shadow: none;
                    &:hover {
                        span {
                            color: $color-purewhite;
                        }
                    }
                    span {
                        margin: auto; // Centre CTA text
                    }
                }
            }
        }
    }
}

// Our students
.course--pgt__our-students {
    grid-column: 2/-2;

    .wrapper--course--pgt__our-students__heading {
        @include content-grid();
        @include full-width-heading();
        grid-column: 1/-1;
        @include media('<small') {
            @include default-padding();
        }
    }

    .course--pgt__student-work {
        grid-column: 1/-1;
        .imageColLayout {
            margin: 0;
        }

        .wrapper--course--pgt__student-work__videos {
            display: flex;
            margin-top: $measure--basic;
            @include media('<=tablet') {
                flex-direction: column;
            }
            .wrapper--course--pgt__student-work__video {
                flex: 1;
                &:nth-of-type(2) {
                    margin: 0 0 0 $measure--basic;
                }
                @include media('<=tablet') {
                    &:nth-of-type(2) {
                        margin: $measure--basic 0 0 0;
                    }
                }
                .embedded-video {
                    @include media('>between') {
                        padding-top: 0;
                        padding-bottom: 0;
                        figure.youtube-video {
                            margin: 0;
                        }
                    }
                }
            }
        }

        .course--pgt__student-work__heading,
        .wrapper--course--pgt__student-work__video,
        .course--pgt__student-work__introduction,
        .imageColLayout-wrap__section {
            grid-column: 1/-1;
            .imageColLayout--card {
                @include media('>between') {
                    padding-right: 0;
                }
            }
        }

        .course--pgt__student-work__heading {
            margin-bottom: $measure--threequarters;
        }
    }
}

// Our students
.course--pgt__our-students {
    @include content-grid();
    @include media('<small') {
        @include default-padding();
    }
    h2 {
        grid-column: 1/-1;
    }
    .course--pgt__testimonials {
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 8/-1;
        }
        // If no Unibuddy card exists, make Testimonials span full-width of container
        &.course--pgt__testimonials--full-width {
            grid-column: 1/-1;
        }

        .slider {
            grid-column: 1/-1;
            display: block;
            margin-bottom: $measure--basic;

            li {
                margin-top: 0;
                picture {
                    margin-left: 0;
                }
            }
        }

        .card--testimonial__photo {
            margin-top: $measure--threequarters;
            margin-left: 0;
        }

        .card--testimonial__quote {
            margin-top: $measure--basic;
        }

        .card--testimonial .card__details .card__thumbnail {
            @include media('>tablet') {
                margin-left: 0;
            }
        }

        ul.related-content {
            margin-top: 0;
        }
    }

    .course--pgt__unibuddy {
        grid-column: 1/-1;
        margin-bottom: $measure--basic;
        @include media('>tablet') {
            grid-column: 1/8;
        }
        height: fit-content;

        .unibuddy-card {
            @include media('<tablet') {
                margin-top: $measure--threequarters;
            }
            margin-top: $measure--small4;
        }
    }
}

.js
    .course--pgt__testimonials
    .related-content[data-style='arrows']:not(.slider) {
    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}

// Student life
.wrapper--course--pgt__student-life {
    margin: $measure--basic 0 0;
    padding: 0 0 $measure--small4;
    .course--pgt__student-life {
        @include content-grid();
        @include media('<small') {
            @include default-padding();
        }
        .pathway {
            grid-column: 1/-1;
            padding: 0;
        }
    }
}

// What's happening cards
.wrapper--course--pgt__whats-happening {
    @include content-grid();

    @include media('<small') {
        @include default-padding();
    }

    .course--pgt__whats-happening {
        grid-column: 1/-1;
        @include sixteen-grid();

        @include media('>=small') {
            grid-column: 2/-2;
        }

        .related-content {
            grid-column: 1/-1;

            @include media('>=tablet') {
                @include content-grid();
            }

            @include media('<tablet') {
                margin: $measure--basic 0;
            }
        }
        .related-content__heading {
            grid-column: 1/-1;
        }
        .related-content.slider-responsive {
            grid-column: 1/-1;
            @include media('>=tablet') {
                display: flex;
                flex-wrap: wrap;
            }
            .slider__slide {
                flex: 1;
                height: fit-content;
                margin-top: $measure--half;
                @include media('>tablet') {
                    &:not(:last-of-type) {
                        margin-right: $measure--basic;
                    }
                }
            }
        }
    }
}

// Related courses
.course--pgt__related-courses {
    @include media('<=small') {
        @include default-margin();
    }
    padding: $measure--basic 0 $measure--basic;

    h2.related-content__heading {
        @include heading--centered($color-pink90);
    }
}

.course--pgt {
    .wrapper--dropdown-filter__select {
        &:after {
            content: '';
            width: 0;
            border-left: $measure--basic solid transparent;
            border-right: $measure--basic solid transparent;
            border-top: $measure--basic solid $color-red60;
            margin: auto;
            display: block;
            top: $measure--basic;
            position: relative;
        }
        background-color: $color-red60;
        color: $color-purewhite;
        padding: $measure--basic $measure--basic 0;
    }
    .dropdown-filter__select {
        &:focus {
            outline-color: $color-purewhite;
        }
    }
    // Indent selected drodpdown content to demonstrate hierarchy between select element and outputted content
    .dropdown-filter .data-group li {
        padding: 0 $measure--basic;
        @include media('<tiny') {
            padding: 0;
        }
    }
}

.course--pgt__tabs {
    // Pre-chart heading made up of number and text
    .chart-heading {
        display: flex;
        color: $color-pink90;

        .chart-heading__number {
            flex: 1 1 auto;
            margin: $measure--threequarters $measure--half 0 0;
            font-weight: $font-weight-light;
            @include etatype();
        }

        .chart-heading__text {
            flex: 1 1 auto;
            @include standfirst();
        }
    }
}

.course--pgt {
    .tabs__panel__heading {
        text-align: center;
    }

    .wrapper--alumni-testimonials {
        display: flex;
        flex-direction: column;

        .card--testimonial .card--testimonial__text .card--testimonial__quote,
        .card--testimonial .card--testimonial__photo {
            margin-top: 0;
        }
        .card--testimonial .card--testimonial__photo {
            margin: 0 $measure--basic 0 0;
        }
    }

    .wrapper--tabs__panel__body__fees__qualification-type__method-of-study {
        @include media('>mobile') {
            display: flex;
            margin-bottom: $measure--medium1;
        }

        .tabs__panel__body__fees__qualification-type__method-of-study {
            @include media('>mobile') {
                flex: 1 0 33.33%;
                font-weight: $font-weight-book;
                color: $color-gray7;
            }
        }

        p.tabs__panel__body__domicile__fees {
            color: $color-pink80;
            @include h5style;
            font-weight: $font-weight-book;
        }

        .tabs__panel__body__domicile {
            margin-right: $measure--small1;

            h5 {
                color: $color-gray7;
            }

            &:last-child {
                margin-right: 0;
            }
        }

        .tabs__panel__body__qualification-type {
            margin: $measure--small5 0;
        }
    }

    .chart-row {
        display: block; // Required for chart columns flex display
        margin: $measure--small4 0 0 0;
        padding: 0;
        .chart {
            margin-right: 0;
            .chart__columns {
                display: flex;
                .chart--piechart {
                    flex-basis: 40%;
                    margin-top: 0;
                }
                .chart__legend {
                    flex-basis: 60%;
                }
            }
        }
    }

    .modal__button {
        text-align: left;

        span {
            display: inline;
        }
    }
}

// Accordion-tabs styles when JS is enabled
.js .course--pgt .accordion {
    padding-bottom: $measure--small4;

    & > .accordion__heading + .accordion__body {
        background-color: $color-gray1;
    }

    &.accordion--nested {
        padding-bottom: 0;
        .accordion__body {
            background-color: $color-purewhite;
        }
    }

    & > .accordion__heading {
        background-color: $color-purewhite;
        border-bottom-color: $color-gray3;
    }
}
// Tab styles
.js .course--pgt .tabs {
    padding-bottom: $measure--basic;

    .tabs__panel {
        padding: $measure--small1 0;
        .tabs__panel__content {
            @include standard-grid();

            grid-template-rows: repeat(2, auto);

            .tabs__panel__heading {
                grid-row: 1;
            }

            .tabs__panel__body {
                grid-row: 2;
            }

            .tabs__panel__heading,
            .tabs__panel__body {
                grid-column: 1 / span 24;

                @include media('>mobile', '<=between') {
                    grid-column: 5 / span 16;
                }

                @include media('>between') {
                    grid-column: 7 / span 12;
                }
            }

            @include media('>=small') {
                @include content-grid();

                .tabs__panel__heading,
                .tabs__panel__body {
                    grid-column: 4 / span 12;
                }
            }

            @include media('<=mobile') {
                @include default-padding();
            }
        }
    }

    .tabs__links {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

// 'Show more' background colour gradient inside tabs
.js .course--pgt .tabs .show-more__content[data-hidden='true']:after {
    background-image: linear-gradient(
        to bottom,
        rgba(242, 242, 242, 0),
        #f2f2f2 90%
    );
}
.js .course--pgt .tabs--accordion .accordion {
    &:not(.accordion--nested) {
        @include media('<=tablet') {
            @include default-padding();
        }
    }
}

// Testimonials slider
.js .wrapper--alumni-testimonials {
    .card--testimonial .card__details .card--testimonial__quote {
        @include media('>tablet') {
            margin-top: $measure--basic;
        }
    }
    .slider {
        padding: 0;
        .card {
            &:last-of-type {
                margin-bottom: $measure--basic;
            }
            &.card--testimonial .card__details {
                .card__testimonial__quote {
                    margin-top: $measure--basic;
                }
            }
            &.slide {
                margin: 0;
                .card__details {
                    .card__thumbnail {
                        margin-top: $measure--basic;
                    }
                }
            }
        }
    }
}

// What's happening card align (JS-enabled)
.js .course--pgt__whats-happening .slider {
    & + .slider__controls {
        grid-column: 1/-1;
    }
}

.chart--bar--horizontal__collection[data-label='course--pgt__msc'] {
    width: 100%;
}

.chart--bar--horizontal__collection[data-label='course--pgt__pgdip'] {
    width: 66%;
}

.chart--bar--horizontal__collection[data-label='course--pgt__pgcert'] {
    width: 33%;
}

.course--pgt .pathway {
    a {
        @include pathway-card(
            $color-purewhite,
            $color-purewhite,
            $color-pink60,
            $color-purewhite,
            $color-purewhite,
            $color-pink60,
            $color-purewhite
        );
    }

    h2 {
        text-align: left;
        padding: 0;
    }

    .pathway__cards {
        padding: 0;
        margin-top: $measure--basic;
        @include media('>=tablet') {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

.course--pgt__facilities {
    grid-column: 1/-1;

    @include content-grid();
    @include media('<small') {
        @include default-padding();
    }

    .wrapper--course--pgt__facilities__video {
        grid-column: 1/-1;
        margin-top: $measure--half;
        @include content-grid();

        .course--pgt__facilities__video__introduction {
            grid-column: 1/-1;

            @include media('>=tablet') {
                grid-column: 1/8;
            }

            @include media('>=small') {
                grid-column: 2/8;
            }
        }
        .course--pgt__facilities__video__embed {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 9/-2;
            }
            .embedded-video {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    .course--pgt__facilities__heading {
        margin: $measure--basic 0 0;
        @include full-width-heading();
        @include media('>=small') {
            grid-column: 2/-2;
        }
        grid-column: 1/-1;
    }

    .wrapper--course--pgt__facilities__gallery {
        grid-column: 1/-1;
        @include media('>=small') {
            @include content-grid();
        }

        .course--pgt__facilities__gallery__introduction {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 1/-1;
            }

            @include media('>=small') {
                grid-column: 2/-2;
            }
        }

        .swiper-viewport {
            @include content-grid();

            @include media('>=small') {
                grid-column: 2/-2;
            }

            grid-column: 1/-1;
            .swiper-container {
                grid-column: 1/-1;
            }
        }
    }
}

// Awards: Duration, credits, modules asset listings
.course--pgt__tabs #structure-content {
    .wrapper--course--pgt__awards__content__duration__qualification-type {
        border: $border-width-hairline $border-style-solid $color-gray3;
        background-color: $color-purewhite;
        padding: $measure--half $measure--basic;
        @include media('>=tablet') {
            margin: $measure--threequarters 0;
        }
    }

    .course--pgt__awards__content__duration__qualification-type,
    .wrapper--course--pgt__awards__content__duration__method-of-study,
    .course--pgt__awards__content__credits {
        border-bottom: $border-width-hairline $border-style-solid $color-gray3;
        padding-bottom: $measure--threequarters;
    }

    .course--pgt__awards__content__duration__method-of-study,
    .wrapper--course--pgt__awards__content__modules ul {
        margin: $measure--quarter 0 $measure--threequarters $measure--medium2;
    }

    .course--pgt__awards__content__credits p {
        @include media('<=tablet') {
            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }
}

/** No JS **/
.no-js .course--pgt {
    // CTA video modal
    .wrapper--course--pgt__introduction {
        .course--pgt__introduction {
            @include media('>tablet') {
                grid-column: 3/10;
            }
            .course--pgt__introduction__ctas {
                display: block; // Appear underneath 'Apply now' CTA
                .course--pgt__introduction__ctas--video {
                    span {
                        display: none;
                    }
                    .cta-block {
                        margin: $measure--small4 0 0 0;
                        li {
                            figcaption {
                                text-align: left;
                                color: $color-purewhite;
                                span {
                                    display: block;
                                }
                                time {
                                    display: flex;
                                    .icon {
                                        margin: $measure--quarter $measure--half
                                            0 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
