.card--event {
    .card__anchor {
        @include box-shadow();

        background-color: $color-purewhite;
        position: relative;

        &,
        &:focus,
        &:active,
        &:visited {
            border: none;
        }

        &:hover {
            @include media('hover') {
                @include box-shadow-darker();

                border: none;
            }
        }

        .card--event__label--event {
            @include smalltype();

            color: $color-gray7;
            font-style: italic;
            margin-left: $measure--basic;
            margin-top: $measure--basic;
        }

        .card--event__label--event + .card__thumbnail {
            margin-top: $content-element-margin;
        }

        .card--event__label--shortdate {
            @include smalltype();

            align-self: flex-start;
            background-color: $color-red50;
            color: $color-purewhite;
            display: inline-block;
            font-weight: $font-weight-bold;
            left: 0;
            margin-top: 0;
            position: absolute;
            padding: $measure--half;
            top: $measure--medium1;
            transition: background-color $transition-duration ease-in;

            @include media('reducemotion') {
                transition: none;
            }

            span {
                font-weight: $font-weight-text;
            }

            // When no Event label, position short date at top of card
            &:first-child {
                top: 0;
            }

            &:first-child + .card__details__text {
                margin-top: $measure--medium1;
            }
        }

        &:hover {
            .card--event__label--shortdate {
                @include media('hover') {
                    background-color: $color-red70;
                }
            }
        }

        .card--event__label--event + .card--event__label--shortdate,
        .card--event__label--shortdate + .card__thumbnail {
            margin-top: $content-element-margin;
        }

        .card__details__text {
            margin-top: $measure--medium4;
            padding-bottom: $measure--basic;
            padding-top: $heading-margin;
        }

        .card--event__label--shortdate
            .card--event__label--event
            + .card--event__label--shortdate {
            top: $measure--medium1;
        }

        .card--event__label--event
            + .card__thumbnail
            + .card--event__label--shortdate {
            top: $measure--medium7;
        }

        .card__thumbnail ~ .card__details__text {
            margin-top: 0;
        }

        .card__type {
            @include smalltype();
            margin-top: $content-element-margin;
            color: $color-gray7;
        }

        .card--event__divider {
            color: $color-red50;
        }

        &:after {
            content: unset;
            display: none;
        }
    }

    &.card--landscape {
        @include landscape-card();

        .card--event__label--event
            + .card--event__label--shortdate
            + .card__details__text {
            margin-top: $measure--large4;
        }
    }

    @include media('>mobile', '<small') {
        @include landscape-card();

        .card--event__label--event
            + .card--event__label--shortdate
            + .card__details__text {
            margin-top: $measure--large4;
        }
    }
}
