.show-more {
    .show-more__button__container {
        padding-top: $measure--small2;
        text-align: center;
    }

    .show-more__content {
        @include content-style();

        overflow: hidden;
        transition: max-height $transition-duration--long;
    }

    .show-more__content[data-hidden='true'] {
        max-height: $measure--huge1;
        position: relative;

        &:after {
            background-image: linear-gradient(
                to bottom,
                rgba(255, 255, 255, 0),
                rgba(255, 255, 255, 1) 90%
            );

            bottom: 0;
            content: '';
            height: 90%;
            left: 0;
            position: absolute;
            width: 100%;
            z-index: z-index('above');
        }
    }

    &[data-open='true'] {
        .show-more__button {
            .icon {
                &:after {
                    content: '\f056';
                }
            }
        }

        .show-more__content {
            max-height: 100%;
        }
    }

    .show-more__button {
        border-top-color: $color-linkblue--dark;
        border-top-style: $border-style-solid;
        border-top-width: $border-width-default;
        padding-top: $measure--half;

        .icon {
            margin-right: $measure--quarter;
            border-bottom: $border-width-default $border-style-solid transparent;

            &:after {
                content: '\f055';
            }
        }

        .show-more__link-text {
            border-bottom: $border-width-default $border-style-solid
                $color-linkblue;
        }

        button {
            @include anchor-inline(
                $color-inline-link,
                $color-inline-link,
                $color-text--dark,
                $color-outline
            );

            border-bottom: none;

            &:hover,
            &:active {
                border-bottom: none;

                .icon {
                    border-bottom: $border-width-default $border-style-solid
                        transparent;
                }

                .show-more__link-text {
                    border-bottom: $border-width-default $border-style-solid
                        transparent;
                }
            }

            &:focus {
                border-bottom: none;

                .icon {
                    border-bottom: $border-width-default $border-style-solid
                        transparent;
                }

                .show-more__link-text {
                    border-bottom: $border-width-default $border-style-solid
                        $color-linkblue;
                }
            }
        }
    }
}

// Blend background linear gradient to match tabs' light grey background
.tabs .show-more .show-more__content[data-hidden='true']:after,
.accordion .show-more .show-more__content[data-hidden='true']:after {
    background-image: linear-gradient(
        to bottom,
        rgba(242, 242, 242, 0),
        rgba(242, 242, 242, 1) 90%
    );
}
