// <Desktop-specific layout - secondary navigation never present
main > .landing--informational {
    @include media('<small') {
        .landing__contact {
            @include default-padding();
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include standard-grid();

        > .landing {
            grid-column: span 24;

            .landing__standfirst {
                p {
                    grid-column: 7 / 19;
                }
            }

            .landing__contact {
                @include standard-grid();

                .landing__contact__details {
                    grid-column: 2 / span 9;
                }

                .landing__contact__location {
                    grid-column: 11 / span 13;
                }

                .landing__contact__details:only-child,
                .landing__contact__location:only-child {
                    grid-column: 2 / span 22;
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .landing {
                grid-column: 7 / span 18;

                .landing__contact {
                    @include content-grid();

                    .landing__contact__details {
                        grid-column: 2 / span 7;
                    }

                    .landing__contact__location {
                        grid-column: 9 / span 9;
                    }

                    .landing__contact__details:only-child,
                    .landing__contact__location:only-child {
                        grid-column: 2 / span 16;
                    }
                }
            }
        }
    }
}

// Generic styles
.landing {
    .banner {
        @include banner($color: $color-gray9);
    }

    .landing__pathway {
        .pathway__heading,
        .pathway__cards {
            padding-left: 0;
            padding-right: 0;
        }
    }

    .swiper-viewport {
        @include media('>between') {
            grid-template-areas: '. . . . . . . slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container';
        }
        @include media('<=between') {
            grid-template-areas: intial;

            .swiper-container {
                grid-column: 1 / -1;
            }

            .swiper-buttons-wrap {
                right: $measure--basic;
                bottom: $measure--half;
            }
            .swiper-indicator {
                right: $measure--huge3;
                bottom: $measure--basic;
            }
        }
    }

    .wrapper--landing__contact {
        margin-top: $measure--basic;
        padding-bottom: $measure--small4;
        padding-top: $measure--basic;

        .landing__contact__heading {
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-thicker;
            padding-bottom: $measure--half;
            text-align: left;
        }

        .landing__contact {
            .related-content {
                align-items: flex-start;
                display: inline-flex;
                flex-direction: column;

                .card--contact {
                    background: initial;
                    box-shadow: initial;

                    .card__details__text {
                        padding-left: 0;
                        padding-right: 0;
                    }
                }

                li + li {
                    @include media('<small') {
                        border-top-style: $border-style-solid;
                        border-top-width: $border-width-hairline;
                        padding-top: $measure--small4;
                    }
                }
            }
        }

        .landing__contact__location {
            .card--findus {
                a {
                    box-shadow: none;
                }

                .card--findus__content {
                    padding-top: 0;
                }

                .card--findus__content__header {
                    display: none;
                }
            }
        }
    }
}

@import 'landing--informational';
@import 'landing--functional';
@import 'landing--latest';
@import 'finder--mini';
