article .social-icon {
    @include anchor-reset();

    @include zoom-in-icon();
}

.copyLink {
    display: flex;
    justify-content: space-evenly;
    margin-right: 0;
}

.tooltip {
    @include font-family('default');
    color: $color-gray7;
    border: $border-width-hairline solid $color-gray2;
    border-radius: 0.1rem;
    padding: $measure--quarter $measure--quarter;
    background-color: $color-gray2;
    transition: all $transition-duration--long;
    font-size: 0.8rem;
    font-weight: bold;
    margin-left: $measure--basic;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include media('<mobile') {
        font-size: 0.5rem;
        width: 9ch;
        margin-left: $measure--half;
    }
}

article .link-copy {
    position: absolute;
    left: -30000px;
    opacity: 0;
}

.tooltip.successful {
    border-color: $color-gray10;
    background-color: $color-gray10;
    color: $color-purewhite;
}
