@import 'finder-filters';

main {
    @include media('>=tablet') {
        > article {
            .finder {
                .finder__results__list {
                    @include content-grid();

                    grid-auto-rows: auto;

                    .card {
                        grid-column: 3 / -3;
                    }

                    &.finder__results__list--three-up {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: wrap;

                        .card {
                            grid-column: unset;
                            padding: $measure--quarter;
                            width: calc(33% - #{$measure--basic});
                        }
                    }
                }
            }
        }
    }
}

main {
    > article {
        .finder {
            .finder__results__list {
                &.finder__results__list--three-up {
                    margin-left: 0;
                    margin-right: 0;
                    max-width: unset;

                    @include media('>mobile') {
                        @include default-margin();
                    }
                }
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > article {
            .finder {
                .banner-text .banner-text__content {
                    @include x-column-grid(24);

                    display: grid;
                    margin: 0;
                    padding: 0;

                    h1,
                    p {
                        grid-column: 3 / -3;
                    }
                }
            }
        }
    }

    @include media('>=desktop') {
        > article {
            .finder {
                .banner-text .banner-text__content {
                    h1,
                    p {
                        grid-column: 4 / -4;
                    }
                }

                .finder__query,
                .wrapper--finder__filters,
                .finder__results {
                    grid-column: 4 / -4;
                }

                .finder__results__list {
                    @include content-grid();

                    .card {
                        grid-column: 3 / -3;
                    }

                    &.finder__results__list--three-up {
                        display: flex;

                        .card {
                            grid-column: unset;
                            padding: $measure--quarter;
                            width: calc(33% - #{$measure--basic});
                        }
                    }
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + article {
                .finder {
                    .banner-text .banner-text__content {
                        @include standard-box();
                        display: block;
                    }
                }
            }
        }
    }
}

.finder__query {
    background-color: $color-gray11;
    padding: $measure--small2;
    position: relative;

    & > div {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: 100%;
    }

    @include media('>=tablet') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

.finder__query__input {
    @include font-family('default');
    @include body-font-features();
    @include alphatype();

    @include media('>=tablet') {
        @include gammatype();
    }

    background-color: $color-purewhite;
    display: flex;
    flex-grow: 1;
    font-weight: $font-weight-text;
    padding: 0;
    position: relative;

    &:focus-within {
        @include outline-properties($color-purewhite);
    }

    input {
        @include font-family('default');
        @include body-font-features();
        @include gammatype();

        border: $border-width-hairline solid transparent;
        font-style: normal;
        height: 100%;
        min-width: 0; // Constrains width in Firefox
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--medium5;
        padding-top: $measure--half;
        width: 100%;

        &:focus {
            color: $color-gray10;
            font-style: normal;
            font-weight: $font-weight-medium;
            outline: 0;
        }

        &::placeholder {
            font-style: italic;
        }

        &:placeholder-shown {
            color: $color-gray7;
        }
    }

    .finder__clear {
        @include epsilontype();

        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: $measure--small5;
        justify-content: center;
        position: absolute;
        right: $measure--half;
        top: $measure--quarter;
        width: $measure--small5;

        &:focus {
            @include outline-properties();

            outline-offset: -#{$border-width-thicker};
        }

        .finder__clear__text {
            @include sr-only();
        }

        .icon {
            /*
            temp overide with xmark-temp-styling below
            
            --fa-primary-color: #{$color-gray7};
            --fa-primary-opacity: 1;
            --fa-secondary-color: #{$color-gray1};
            --fa-secondary-opacity: 1;
            */
            @include xmark-temp-styling;

            letter-spacing: 0;
            line-height: 1;
        }

        &:hover {
            background-color: transparent;

            .icon {
                /*
                temp overide with xmark-temp-styling below

                --fa-primary-color: #{$color-gray1};
                --fa-secondary-color: #{$color-gray7};
                */
                @include xmark-temp-styling-hover;
            }
        }
    }
}

.finder__query__icon--wrapper {
    background-color: $color-purewhite;
    padding-bottom: $measure--half;
    padding-left: $measure--half;
    padding-right: $measure--basic;
    padding-top: #{$measure--quarter + $border-width-hairline};

    @include media('<tablet') {
        display: none;
    }
}

.finder__query__submit {
    @include gammatype();
    @include cta--basic(
        $color-purewhite,
        $color-red50,
        $color-purewhite,
        $color-red70,
        $color-purewhite,
        round
    );

    flex-shrink: 0;
    font-weight: $font-weight-book;
    margin-left: $measure--basic;

    &:hover {
        cursor: pointer;
    }

    @include media('>=tablet') {
        margin-left: $measure--small4;
    }

    @include media('<tablet') {
        padding: $measure--basic $measure--small1;

        &:hover {
            padding: $measure--basic $measure--small1;
        }
    }

    span {
        margin: auto;
    }

    .finder__query__submit__text {
        @include media('<tablet') {
            @include screenreader-only();
        }
    }

    .finder__query__submit__icon {
        font-weight: $font-weight-medium;
        padding: 0 $measure--half 0 0;

        @include media('<tablet') {
            display: block;
            padding: 0;
        }
    }
}
.finder__filter__submit {
    margin-bottom: $measure--threequarters;
    margin-right: $measure--basic;
    margin-top: $measure--quarter;
}

.finder__submit {
    @include alphatype();
    margin-left: initial;
}

.finder__didyoumean__button {
    @include anchor-inline(
        $color-inline-link,
        $color-inline-link,
        $color-text--dark,
        $color-outline
    );
}

.finder__filter {
    legend {
        @include h3style();
        position: relative;
        display: block;
        width: 100%;
        background-color: $color-purewhite;
        padding: 0;

        button {
            background-image: none;
            border-bottom-color: $color-purewhite;
            padding: $measure--threequarters $measure--small2;
            width: 100%;

            &:active,
            &:focus {
                outline-offset: -$measure--quarter;
            }

            &:hover {
                .icon {
                    color: $color-red50;
                    transition: color $transition-duration--long;
                }
            }

            & > span {
                color: $color-nearblack;
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: space-between;
            }
        }

        button[aria-expanded='true'] {
            .icon {
                color: $color-red50;
            }
        }
    }

    label {
        cursor: pointer;
        width: 100%;
    }

    input:not(:disabled) {
        cursor: pointer;
    }

    input[type='radio'] {
        align-self: flex-start;
        display: block;
        margin-right: $measure--basic;
        margin-top: $measure--half;
    }

    legend[data-open='true'] {
        &:after {
            border-top: $border-width-hairline solid $color-gray3;
            bottom: 0;
            content: '';
            left: $measure--small2;
            position: absolute;
            width: calc(100% - #{$measure--medium3});
        }
    }

    legend[data-open='true'] + ul {
        display: block;
    }

    legend[data-open='false'] + ul {
        display: none;
    }

    ul {
        background-color: $color-purewhite;
        list-style-type: none;
        margin-left: 0;
        margin-top: 0;
        max-width: unset;
        padding: $measure--threequarters $measure--small2;
        position: relative;
    }

    li {
        align-items: center;
        display: flex;
        flex-direction: row;
    }
}

.finder__query__suggestions {
    background-color: $color-purewhite;
    border-radius: 0 0 $measure--half $measure--half;
    border-top: 0;
    border: $border-width-hairline $border-style-solid $color-gray2;
    box-shadow: rgba(0, 0, 0, 0.25) 0 $measure--basic $measure--medium3 -$measure--threequarters;
    color: $color-purewhite;
    font-weight: $font-weight-bold;
    left: 0;
    list-style: none;
    margin-left: 0;
    max-width: unset;
    position: absolute;
    top: $measure--medium1;
    width: 100%;
    z-index: z-index('above');

    li {
        font-weight: $font-weight-text;
        margin-top: 0;

        button {
            color: $color-text;
            height: 100%;
            padding: $measure--half $measure--small4 $measure--half
                $measure--small4;
            text-align: left;
            width: 100%;

            &:focus {
                outline-color: $color-nearblack;
                outline-offset: (-$border-width-default);
                outline-style: $border-style-dotted;
                outline-width: $border-width-default;
                text-decoration: underline;
            }

            &:active {
                @include outline-properties();

                outline-offset: 0;
            }

            &:hover {
                background-color: $color-linkblue;
                color: $color-purewhite;
                text-decoration: underline;
            }

            .finder__suggestion {
                font-weight: $font-weight-bold;
            }
        }
    }
}

.finder__query__suggestions.hide {
    display: none;
}

.finder__results__announcement {
    padding: $measure--basic;
    background-color: $color-bg--dark;
    color: $color-purewhite;

    p {
        max-width: initial;
        margin-top: initial;
    }
}

.finder__results__summary__heading {
    @include h3style();

    text-align: left;

    @include media('<=mobile') {
        @include betatype();
    }

    span {
        @include content-style();

        span {
            color: $color-red50;
            font-weight: $font-weight-bold;

            @include media('<=mobile') {
                font-weight: $font-weight-medium;
            }
        }
    }
}

.finder__results__summary {
    border-bottom: $border-width-hairline $border-style-solid $color-gray4;
    padding-bottom: $measure--quarter;

    &:not(.finder__results__summary--noresults) {
        @include media('>=tablet') {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
        }
    }

    &.finder__results__summary--noresults {
        border-bottom: none;
    }

    button {
        @include anchor-inline();
    }
}

.finder .finder__results {
    @include media('>=tablet') {
        margin-left: 0;
        margin-right: 0;
    }

    .finder__results__summary,
    .finder__results__updating,
    .finder__results__didyoumean,
    .pagination__wrapper {
        @include media('>=tablet') {
            margin-left: $measure--small2;
            margin-right: $measure--small2;
        }
    }

    .pagination__wrapper {
        border-top: $border-width-hairline $border-style-solid $color-gray4;
        max-width: unset;
    }

    .pagination__controls {
        justify-content: center;
    }
}

.finder__results__list {
    list-style-type: none;
    margin-left: 0;
    max-width: unset;
}

.finder .highlightText {
    background-color: $color-red10;
}

.finder__results__updating {
    @include gammatype();

    margin-bottom: $heading-margin;
    margin-top: $heading-margin;

    .icon {
        color: $color-red50;
    }
}

.card {
    margin-top: $measure--small4;

    &:last-of-type {
        margin-bottom: $measure--small4;
    }
}

.finder__results__bestbet {
    a .finder__results__card__description {
        color: $color-text;
    }
}

.finder__input-text__wrapper {
    background-color: $color-purewhite;
    display: flex;
    flex: 1;
    padding: $measure--half $measure--threequarters $measure--quarter
        $measure--quarter;
}

.finder__input-text__label {
    display: none;
}

.finder__icon {
    background-color: $color-purewhite;
    color: $color-gray7;
    font-weight: $font-weight-text;
    margin-bottom: $measure--half;
    margin-top: $measure--threequarters;
}

.edge {
    .finder__query__input {
        input {
            &:focus {
                @include outline-properties();
            }
        }

        .finder__clear {
            &:focus {
                @include outline-properties();
            }
        }
    }

    .finder__select select {
        &:focus {
            @include outline-properties();
        }
    }
}

.wrapper--finder__select--sort--desktop {
    display: none;

    @include media('>=tablet') {
        display: block;

        .finder__select--sort {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: flex-end;
            margin-top: 0;

            label {
                flex-shrink: 0;
                margin-right: $measure--half;
                margin-bottom: $measure--half;
                padding-bottom: 0;
            }
        }
    }
}

.wrapper--finder__select--sort--mobile {
    display: block;

    @include media('>=tablet') {
        display: none;
    }
}

.finder--nofilters {
    .finder__results {
        margin-top: $measure--small2;
    }
}

.no-js {
    .wrapper--finder__select--sort--desktop {
        display: block;
    }
}
