.figure--map {
    a {
        @include anchor-reset-properties;
        overflow: hidden;
        background-image: none;
        background-size: 0;
        transition: none;
        display: block;

        &:hover {
            @include media('hover') {
                @include anchor-reset-properties;
                background-image: none;
                background-size: 0;
                transition: none;

                picture {
                    transform: scale(1.04);

                    &:before {
                        background-color: rgba($color-gray11, 0.1);
                    }
                }
            }
        }

        &:focus,
        &:active {
            @include anchor-reset-properties;
            @include outline-properties();
        }

        &:focus {
            picture {
                transform: scale(1.04);
            }
        }
    }

    picture {
        position: relative;
        display: block;
        transition: all 0.3s ease-in-out;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background: transparent;
            transition: background-color 0.3s ease-in-out;
        }
    }
}
