.pathway {
    margin-top: $measure--small2;
    padding-top: $measure--basic;

    .pathway__heading {
        margin-top: 0;
        padding-left: $measure--half;
        padding-right: $measure--half;
    }

    .pathway__cards {
        @include media('>tablet') {
            display: grid;
            grid-column: 2/-2;
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: $measure--small2;
        }

        list-style: none;
        margin: 0;
        max-width: unset;
        padding-bottom: $measure--small1;
        padding-left: $measure--half;
        padding-right: $measure--half;

        .card--pathway {
            margin-top: $measure--basic;

            a {
                @include pathway-card();
            }

            a.card--pathway__content--border {
                border-top: $measure--half solid $color-red50;
            }

            .card--pathway__content {
                overflow: hidden;

                img {
                    display: block;
                    width: 100%;
                }

                @include media('>mobile', '<=tablet') {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;

                    picture {
                        max-width: $measure--huge5;
                    }
                }
            }

            .card--pathway__content__text {
                padding-bottom: $measure--basic;
                padding-left: $measure--basic;
                padding-right: $measure--basic;
            }

            .card--pathway__content__heading {
                @include betatype();

                .icon {
                    margin-left: $measure--quarter;
                }
            }
        }
    }

    &.background--pink90 {
        .pathway__heading {
            color: $color-purewhite;
        }

        .pathway__cards {
            .card--pathway {
                a {
                    @include pathway-card(
                        $card-text-color: $color-purewhite,
                        $card-text-hover-color: $color-purewhite,
                        $card-background-color: transparent,
                        $card-background-hover-color: $color-pink80,
                        $card-heading-color: $color-purewhite,
                        $section-title-color: $color-purewhite,
                        $border-color: $color-pink80,
                        $outline-color: $color-purewhite
                    );
                }
            }
        }
    }
}
