@mixin related-content-properties() {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    li {
        width: 100%;
    }

    @include media('>=small') {
        &[data-rowsize='1'] {
            li {
                width: 100%;
            }
        }

        &[data-rowsize='2'] {
            li {
                margin-right: $measure--small2;
                width: calc(50% - #{$measure--small2});
            }
        }

        &[data-rowsize='3'] {
            li {
                margin-right: $measure--small2;
                width: calc(33% - #{$measure--small2});
            }
        }
    }
}

.related-content {
    margin: 0;
    max-width: unset;

    &:not(.slider) {
        @include related-content-properties();
    }
}

.no-js {
    .related-content {
        &.slider {
            @include related-content-properties();
        }
    }
}

.latest-content {
    margin-top: $measure--small4;
}
