// < Desktop-specific layout - secondary navigation never present
main > .documentation {
    @include media('>=small') {
        .content {
            grid-column: 4/-4;
        }
    }
}

// > Desktop (secondary navigation present)
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .documentation {
                .content {
                    grid-column: 2/-2;
                }
            }
        }
    }
}

// Generic styles
.documentation {
    @include media('<small') {
        .content {
            grid-column: 1/-1;
            @include default-margin();
        }
    }

    .content {
        margin-top: $measure--small2;
        margin-bottom: $measure--small2;
        padding: 0;
    }

    .slider.doc-slider {
        list-style: none;
        margin: 0 auto;

        li {
            height: $measure--large2;
            width: 100%;
            border: $border-width-default $border-style-solid $color-nearblack;
        }
    }
}
