// pattern used in paint layouts
.promo-box {
    background-color: $color-red50;
    background-image: linear-gradient(to right, $color-red50, $color-pink80);
    color: $color-purewhite;
    padding: 0 $measure--small2 $measure--basic;

    a {
        border-bottom-color: $color-purewhite;
        color: $color-purewhite;
    }
}

.promo-box__wrapper {
    display: block;
    padding-top: $measure--small1;

    &:nth-of-type(2) {
        margin-top: $measure--small5;
    }

    @include media('>tablet') {
        display: flex;
    }
}

.promo-box__card {
    grid-column: 1/-1;
    flex: 1;
    &:nth-of-type(2) {
        margin-top: $measure--small5;
    }

    @include media('>tablet') {
        &:nth-of-type(2) {
            margin-top: 0;
        }
    }

    &:first-of-type {
        margin-right: $measure--small4;
    }
}

.promo-box__card__body {
    display: flex;

    img {
        min-width: $measure--medium4;
        max-width: $measure--medium4;
        min-height: $measure--medium4;
        max-height: $measure--medium4;
        display: block;
        margin-right: $measure--small1;
    }
}
