.module-selector-wrapper {
    margin-top: $measure--small4;
}

.module-selector {
    @include standard-box();
    @include gradient-border($color-red50, $color-pink60);
    position: relative;
    padding: $measure--basic;
    background-color: $color-red5;
    overflow: hidden;
    text-align: center;

    @include media('<tablet') {
        margin: 0;
    }
    @include media('>=tablet', '<small') {
        margin: 0 $measure--basic;
    }
    @include media('>=small', '<large') {
        margin: 0;
    }

    @include media('>=between') {
        display: flex;
        flex-direction: row;
        padding: $measure--small4;
        text-align: left;
    }
}

.module-selector__heading {
    @include h3style();
}

.module-selector__content {
    .module-selector__content__text {
        max-width: 100%;
    }
}

.module-selector__heading,
.module-selector__content,
.module-selector__button {
    @include media('>=between') {
        width: calc(33% - #{$measure--small2});
        margin-right: $measure--small2;
    }
}

.module-selector__button {
    margin: $measure--small4 0 0 0;

    @include media('>=between') {
        margin: 0;
    }

    @include media('<tablet') {
        display: flex;
        flex-direction: column;

        a {
            display: block;
        }
    }

    a {
        @include alphatype();
    }
}

.module-selector__content,
.module-selector__heading {
    margin-bottom: $measure--basic;

    @include media('>between') {
        margin-bottom: 0;
    }
}

.module-selector__content,
.module-selector__heading,
.module-selector__button {
    align-self: center;
    position: relative;
}

.module-selector__content,
.module-selector__button {
    text-align: center;
}

.module-selector__heading-text {
    color: $color-gray7;
}

.module-selector__background {
    display: block;
    right: 0;
    position: absolute;
    right: -#{$measure--large2};
    height: 100%;
    top: 0;

    @include media('>=tablet') {
        right: 0;
    }
}

.no-js {
    .module-selector__button {
        margin: $measure--basic 0 0 0;
    }

    .module-selector {
        @include media('>=tablet') {
            display: block;
        }

        .modal__content {
            background: transparent;
        }
    }

    .module-selector__button {
        text-align: left;
    }
}
