@keyframes slide-left-in {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: none;
    }
}

@keyframes slide-right-out {
    from {
        transform: none;
    }

    to {
        transform: translateX(100%);
    }
}

@mixin ghost-out() {
    animation: slide-right-out $transition-duration ease 0s both normal;
    background-color: $color-gray3;
    bottom: 0;
    content: '';
    height: $measure--half;
    left: 0;
    opacity: 0;
    position: absolute;
    transform: translateX(105%);
    transition: opacity $transition-duration ease $transition-duration;
    width: 100%;
    z-index: z-index('default');

    @include media('reducemotion') {
        animation: none;
        transition: none;
    }
}

@mixin ghost-in() {
    animation: slide-left-in $transition-duration ease 0s both normal;
    opacity: 1;
    transform: translateX(-105%);
    transition: none;

    @include media('reducemotion') {
        animation: none;
        transition: none;
    }
}
