////
/// CMS editor warning
/// @group patterns
////
.cms-editor-warning {
    display: none;
}

@mixin cms-warning() {
    background-color: $color-red80;
    color: $color-text--dark;
    padding-left: $measure--half;
    padding-right: $measure--half;
}

.cms-editor {
    div.cms-editor-warning {
        display: flex;
        grid-column: 1 / 24;
        justify-content: center;

        p {
            @include cms-warning();
            @include alphatype();
            margin-bottom: $measure--half;
            max-width: $max-line-length;
            padding-top: $measure--half;
            padding-bottom: $measure--medium3;
            position: relative;

            &:after {
                @include extrasmalltype();
                bottom: $measure--half;
                content: 'CMS editor warning';
                font-weight: $font-weight-light;
                position: absolute;
                right: $measure--half;
            }
        }
    }

    span.cms-editor-warning {
        @include cms-warning();

        display: inline;
    }

    .wrapper--cms-editor-warning-remover {
        @include content-style();
        @include standard-box();

        margin-bottom: $measure--small2;
        margin-top: $measure--small2;
        text-align: center;
    }

    .cms-editor-warning-remover {
        @include cta--basic(
            $color-purewhite,
            $color-red80,
            $color-purewhite,
            $color-red100,
            $color-nearblack,
            round
        );
    }
}
