main {
    @include media('>=small') {
        > .error {
            .content {
                grid-column: 4/-4;
                padding: 0;
            }
        }
    }

    @include media('<small') {
        > .error {
            grid-column: 1/-1;
            @include default-margin();
        }
    }
}

// Generic styles
main {
    > .error {
        padding: $measure--small2 0;
    }
}
