.card--news {
    .card__anchor {
        @include box-shadow();

        background-color: $color-purewhite;

        &,
        &:focus,
        &:active,
        &:visited {
            border: none;
        }

        &:hover {
            @include media('hover') {
                @include box-shadow-darker();

                border: none;
            }
        }

        .card--news_f_label--news + .card__details__text {
            @include media('>mobile', '<small') {
                padding-top: $measure--medium3;
            }
        }

        .card--news__label--news {
            @include smalltype();

            color: $color-gray7;
            font-style: italic;
            margin-left: $measure--basic;
            margin-top: $measure--basic;
        }

        .card--news__label--news + .card__thumbnail {
            margin-top: $content-element-margin;
        }

        .card--news__label--news + .card__details__text {
            @include media('>=mobile', '<tablet') {
                margin-top: $measure--medium1;
            }
        }

        .card__details__text {
            padding-bottom: $measure--basic;
            padding-top: $heading-margin;
        }

        .card__type {
            margin-top: 0;
            @include smalltype();
            color: $color-gray7;
        }

        &:after {
            content: unset;
            display: none;
        }
    }

    &.card--landscape {
        @include landscape-card();
    }

    @include media('>=mobile', '<tablet') {
        @include landscape-card();
    }
}
