.related-content {
    .card--profile {
        .card__details {
            display: flex;
            flex-wrap: nowrap;

            @include media('<tablet') {
                flex-direction: column;
            }

            @include media('>=tablet') {
                flex-direction: row;
            }
        }

        .card__thumbnail {
            padding-left: $measure--basic;
            padding-top: $measure--basic;
        }
    }
}

.finder__results,
.search__results {
    .card--profile {
        .card__details {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
        }
    }
}

.card--profile {
    .card__details {
        .card__thumbnail {
            flex-shrink: 0;
            height: $measure--huge5;
            width: $measure--huge5;
        }

        .card__type {
            color: $color-red50;
        }
    }
}
