////
/// Breadcrumbs
/// @group patterns
////
.breadcrumbs {
    background-color: $color-gray1;
    color: $color-nearblack;

    ol {
        @include media('<small') {
            @include default-margin();
        }

        @include media('>=small') {
            @include standard-box();
        }

        display: flex;
        flex-wrap: wrap;
        list-style: none;
        padding-bottom: $measure--half;
        padding-top: $measure--half;

        li {
            @include content-style();
            @include smalltype();

            font-weight: $font-weight-text;
            line-height: $measure--small1;
            max-width: unset;

            &:not(:last-of-type) {
                margin-right: $measure--half;
                margin-bottom: $measure--quarter;

                &:after {
                    @include font-family('sharp');
                    font-size: $font-size--icon;
                    content: '\f105';
                    font-weight: $font-weight-light;
                    margin-left: $measure--quarter;
                }
            }

            a {
                @include anchor-discreet--notransition(
                    $color-gray8,
                    $color-gray8,
                    $color-nearblack,
                    $color-nearblack,
                    $color-red50
                );
            }
        }
    }

    @include media('print') {
        display: none;
    }
}
