////
/// Footer - This is for the reusable footer element on course pages paint layouts asset id 503750
/// @group patterns
////

.course-footer {
    @include default-padding();
    padding-top: $heading-margin;
    padding-bottom: $measure--small2;

    h2 {
        border-bottom: $border-width-thicker $border-style-solid $color-red50;
        padding-bottom: $measure--basic;
        margin-top: $measure--small4;
    }

    h2:nth-child(2) {
        margin-top: $measure--medium3;
    }

    .related-content {
        justify-content: left;

        .card.card--contact {
            padding-top: 0;
            padding-bottom: 0;
            margin-top: $measure--basic;

            &:not(:first-child) {
                .card__details {
                    .card__details__text {
                        padding-top: 0;
                    }
                }
            }

            .card__details__text {
                padding-left: 0;
            }
            & + .card--contact {
                padding-top: 0;
                border-top: 0;
            }
        }
    }

    .ul:first-of-type {
        margin-top: $measure--basic;
    }

    .related-content .card.card--contact + .card--contact {
        border-top: none;
    }

    &.background--pink80 {
        color: $color-purewhite;

        h2 {
            border-bottom: $border-width-hairline $border-style-solid
                $color-pink70;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-gray11,
                $color-outline--dark
            );
        }

        .card .card__details .card__tag .icon {
            color: $color-purewhite;
        }
    }

    .card .card__details .card__details__text {
        @include media('>tablet') {
            padding-top: 0;
        }
    }

    .related-content:not(.slider) {
        @include media('>tablet') {
            flex-wrap: nowrap;
        }
    }
}
