// <Desktop-specific layout - secondary navigation never present
main > .department {
    @include media('<small') {
        //@include default-margin();
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .department {
            grid-column: 6 / span 14;
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=tablet') {
        .wrapper--department__tabs {
            @include content-grid();
            .department__tabs {
                grid-column: 1/-1;
                .tabs__panel:not([hidden]) .tabs__panel__content {
                    @include content-grid();
                    h2,
                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 4 / span 12;
                    }
                }
            }
        }
    }
    @include media('>=small') {
        .navigation--secondary {
            & + .department {
                .department__additional-info {
                    h2,
                    .department__additional-info__content {
                        grid-column: 2/-2;
                    }
                }

                .department__facilities,
                .department__whats-happening,
                .department__contact-us,
                .department__multimedia,
                .department__additional-info {
                    @include content-grid();
                }

                .department__tabs,
                .department__whats-happening h2,
                .department__whats-happening .related-content,
                .department__multimedia .embedded-video,
                .department__facilities h2 {
                    grid-column: 2/-2;
                }

                .department__contact-us__contacts {
                    grid-column: 2/10;
                }

                .department__contact-us__footer__find-us {
                    grid-column: 10/-2;
                }
            }
        }
    }
}

.dep__gallery__wrapper {
    grid-column: 1/-1;
    @include media('>=small') {
        @include content-grid();
    }

    .swiper-viewport {
        @include content-grid();

        @include media('>=small') {
            grid-column: 2/-2;
        }

        grid-column: 1/-1;
        .swiper-container {
            grid-column: 1/-1;
        }
    }
}

@include media('>tablet', '<small') {
    .department__additional-info {
        .tabs__panel:not([hidden]) .tabs__panel__content {
            @include standard-grid();

            h2,
            .department__additional-info__content,
            .tabs__panel__heading,
            .tabs__panel__body {
                grid-column: 4 / span 18;
            }
        }
    }
}

@include media('<small') {
    .department__tabs,
    .department__multimedia,
    .department__additional-info,
    .department__facilities,
    .department__whats-happening,
    .department__social-media,
    .department__contact-us {
        @include default-margin();
    }
}

@include media('<=mobile') {
    .department__additional-info {
        padding: 0;
    }
}
.department__additional-info h2,
.department__tabs h2 {
    text-align: center;
}

.promo-area__wrap {
    grid-column: 1/-1;
}

.department__usp {
    background-image: linear-gradient(to top, $color-purewhite, $color-gray1);
}

.department {
    .department__heading--underlined {
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;
        padding-bottom: $measure--half;
        grid-column: 2/-2;
    }

    .department__contact-us {
        display: flex;

        .department__contact-us__contacts {
            .related-content__heading {
                @include media('>=tablet') {
                    margin-right: $measure--basic;
                }
            }
        }

        .related-content__heading {
            border-bottom: $border-width-hairline $border-style-solid
                $color-gray4;
        }

        @include media('<tablet') {
            flex-direction: column;
        }

        .department__contact-us__contacts {
            .related-content {
                display: flex;
                flex-direction: column;

                .card.card--contact {
                    flex: 1;
                    padding-bottom: 0;

                    .card__details__text {
                        padding-left: 0;
                    }

                    + .card.card--contact {
                        border-top: none;
                        padding-top: 0;
                    }
                }
            }
        }

        .department__contact-us__contacts--2columns {
            flex: 2;
        }

        .department__contact-us__contacts--1column {
            flex: 1;
        }

        .department__contact-us__footer__find-us {
            flex: 1;
        }
    }

    .tabs__panel:not([hidden]) {
        padding: 0;
        .tabs__panel__content {
            .tabs__panel__body {
                margin-top: $measure--small4;
            }
        }
    }

    .tabs--accordion,
    .tabs {
        .show-more .show-more__content[data-hidden='true']::after {
            background-image: linear-gradient(
                to bottom,
                rgba(242, 242, 242, 0),
                rgba(242, 242, 242, 1) 90%
            );
        }

        .card__anchor {
            border-color: $color-gray3;

            @include media('<between') {
                background: $color-purewhite;
            }

            &:hover {
                @include media('hover') {
                    background-color: $color-purewhite;
                    border-color: $color-purewhite;
                }
            }
        }
    }
}

.department__featured-staff {
    .related-content[data-style='arrows']:not(.slider) {
        grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
    }
}

// Pattern override
.js
    .department
    .department__contact-us__footer__find-us
    .related-content:not(.slider) {
    li:only-child {
        grid-column: 1/-1;
    }
}
