// <Desktop-specific layout - secondary navigation never present
main > .case-study {
    @include media('<small') {
        .banner-standfirst-label {
            .banner__image {
                grid-column: 1 / span 24;
            }
        }
    }
}

// Desktop-specific layout - for finder pages
main > .listing {
    @include media('>=small') {
        .banner-text {
            @include default-padding();
        }
    }
}

// Desktop-specific layout
main > article {
    @include media('>=small') {
        .banner-standfirst-label {
            display: grid;
            .banner__image {
                grid-column: 1 / span 24;
            }

            .banner__standfirst {
                grid-column: 3 / 23;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + article {
                .banner-standfirst-label {
                    .banner__image {
                        grid-column: 1 / span 18;
                    }

                    .banner__standfirst {
                        grid-column: 3 / 17;
                    }
                }
            }
        }
    }
}

// Generic styles
.banner-standfirst-label {
    .banner__image {
        overflow: hidden;
        &:not(.banner__image--svg) {
            &:after {
                background-image: none;
            }
        }
    }

    .banner__block-color {
        background-color: $color-brown30;
        grid-column: 1 / -1;
        height: $measure--huge1;

        @include media('<tablet') {
            height: $measure--large1;
        }
    }

    .banner__standfirst {
        background: $color-purewhite;
        margin-top: 0;
        color: $color-nearblack;
        grid-column: 1 / -1;
        opacity: 1;
        text-align: center;

        @include media('>=tablet') {
            margin-top: -$measure--medium9;
            grid-column: 2 / 24;
        }

        @include media('>small') {
            grid-column: 2 / 18;
        }

        @include media('>desktop') {
            grid-column: 3 / 17;
        }

        .outline-cta--bright {
            margin-top: $measure--threequarters;
            @include alphatype();
        }
    }

    .labels {
        @include smalltype();

        color: $color-gray7;
        font-weight: $font-weight-bold;
        margin-bottom: $measure--half;
    }

    .standfirst {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}
