@mixin anchor-pagination(
    $accent: $color-red50,
    $background: $color-purewhite,
    $color: $color-nearblack,
    $hover: $color-purewhite
) {
    @include anchor-reset();

    background-image: linear-gradient(120deg, $background 0%, $background 100%);
    background-position: 0 100%;
    background-repeat: no-repeat;
    background-size: 100% 0;
    border-bottom-color: $accent;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;
    border-left-color: $color-gray4;
    border-left-style: $border-style-solid;
    border-left-width: $border-width-hairline;
    border-right-color: $color-gray4;
    border-right-style: $border-style-solid;
    border-right-width: $border-width-hairline;
    border-top-color: $color-gray4;
    border-top-style: $border-style-solid;
    border-top-width: $border-width-hairline;
    color: $color;
    font-weight: $font-weight-text;
    transition: background-size $transition-duration $transition-function;

    @include media('reducemotion') {
        transition: none;
    }

    &:focus {
        @include outline-properties();

        background-image: linear-gradient(
            120deg,
            $background 0%,
            $background 100%
        );
        border-bottom-color: $accent;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $color;
        font-weight: $font-weight-bold;
    }

    &:hover {
        @include media('hover') {
            background-image: linear-gradient(120deg, $accent 0%, $accent 100%);
            background-size: 100% 100%;
            color: $hover;
            font-weight: $font-weight-text;
        }
    }

    &:active {
        @include outline-properties();

        background-image: linear-gradient(120deg, $accent 0%, $accent 100%);
        background-size: 100% 100%;
        border-bottom-color: $accent;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $hover;
        font-weight: $font-weight-bold;
    }

    &:disabled {
        background-size: 100% 0;
        border-bottom-color: $color-gray4;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-hairline;
        color: $color-gray7;
        cursor: default;
        font-weight: $font-weight-text;

        span {
            display: inline-block;
            padding-bottom: $border-width-hairline;
        }
    }

    &[aria-current] {
        border-bottom-color: $color-gray4;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-hairline;
        color: $color-gray7;
        cursor: default;
        font-weight: $font-weight-bold;
        outline-color: transparent;
        outline-offset: 0;
        outline-style: none;
        outline-width: 0;
    }
}
