.social-icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    margin-bottom: $measure--small2;
    margin-left: 0;
    margin-right: 0;
    margin-top: $measure--small2;
    max-width: none;

    li {
        margin-right: $measure--threequarters;
        margin-top: 0;

        &.copyLink {
            margin-right: 0;
        }
    }
}
