@mixin landscape-card() {
    .card__details {
        flex-direction: row;

        .card__thumbnail {
            width: $measure--huge5;
        }

        .card--news__label--news,
        .card--event__label--event {
            position: absolute;

            & + .card__thumbnail {
                margin-top: $measure--medium3;

                & + .card__details__text {
                    margin-top: $measure--medium3;
                    padding-top: 0;
                }
            }
        }
    }
}

@mixin card-anchor-colors(
    $background: transparent,
    $color: $color-nearblack,
    $background-hover: $color-gray1,
    $color-hover: $color-nearblack,
    $color-outline: $color-nearblack,
    $icon: $color-red50,
    $arrow-background: $color-nearblack,
    $arrow-color: $color-purewhite,
    $arrow-background-hover: $color-red50,
    $arrow-color-hover: $color-purewhite,
    $border: $color-gray1
) {
    background-color: $background;
    color: $color;

    &:focus,
    &:active {
        @include outline-properties($color-outline);
    }

    &,
    &:focus,
    &:active,
    &:visited {
        border-color: $border;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            background-color: $background-hover;
            border-color: $border;
            color: $color;

            &:after {
                background-color: $arrow-background-hover;
                color: $arrow-color-hover;
            }
        }
    }

    &:after {
        background-color: $arrow-background;
        color: $arrow-color;
    }

    @include media('<=between') {
        background-color: $background-hover;

        &:after {
            background-color: $arrow-background-hover;
        }
    }

    .card__type {
        color: $icon;
    }

    .card__tag {
        .icon {
            color: $icon;
        }
    }
}

.card {
    list-style-type: none;
    max-width: unset;

    .card__anchor {
        @include anchor-reset();
        @include card-anchor-colors();

        display: flex;
        flex-direction: column;
        overflow: hidden;
        position: relative;
        transition: background-color $transition-duration ease-in;

        @include media('reducemotion') {
            transition: none;
        }

        &,
        &:focus,
        &:active,
        &:visited {
            border-width: $border-width-hairline;
            border-style: $border-style-solid;
        }

        &:hover {
            @include media('hover') {
                border-style: $border-style-solid;
                border-width: $border-width-hairline;

                .card__thumbnail {
                    transform: scale(1.02);
                }
            }
        }

        &:after {
            @include font-family('sharp');

            align-items: center;
            bottom: 0;
            content: '\f178';
            display: flex;
            font-weight: $font-weight-light;
            height: $measure--small4;
            justify-content: center;
            position: absolute;
            right: 0;
            text-align: center;
            transition: background-color $transition-duration ease-in;
            width: $measure--small4;

            @include media('reducemotion') {
                transition: none;
            }
        }

        .card__thumbnail {
            align-self: flex-start;
            transition: transform $transition-duration ease-in-out;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }

        .card__heading {
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;

            .icon {
                margin-left: $measure--half;
            }
        }
    }

    .card__details {
        .card__details__text {
            padding-bottom: $measure--medium3;
            padding-left: $measure--basic;
            padding-right: $measure--basic;
            padding-top: $measure--basic;
        }

        .card__thumbnail {
            & + .card__details__text {
                padding-top: $heading-margin;
            }
        }

        .card__heading {
            @include h5style();

            display: inline;
            max-width: unset;
        }

        .card__type {
            @include alphatype();

            margin-top: 0;
            max-width: unset;
        }

        .card__description {
            @include content-style();
        }

        .card__tag {
            @include smalltype();

            max-width: unset;

            & + .card__tag {
                margin-top: 0;
            }

            .icon {
                margin-right: $measure--half;
            }
        }
    }
}

// Contact and research cards share many similarities. Mixin for base rules.
@mixin search-contact-research-card() {
    .card__details {
        border-color: $color-gray1;
        border-style: $border-style-solid;
        border-width: $border-width-hairline;
        display: block;

        @include media('<=between') {
            background-color: $color-gray1;
        }

        .card__details__text {
            padding-bottom: $measure--basic;
        }

        .card__type {
            color: $color-red50;
        }
    }
}

@mixin contact-research-card() {
    .card__details {
        .card__tag {
            .icon {
                color: $color-red50;
            }
        }
    }

    .wrapper--card--contact__address {
        @include content-style();

        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-top: $content-element-margin;

        address {
            .card--contact__addressline {
                display: block;
            }
        }
    }
}

