.pagination__wrapper {
    @include media('print') {
        .pagination__controls,
        .pagination__summary {
            display: none;
        }
    }

    @include media('screen') {
        .pagination__controls {
            display: flex;
            flex-wrap: nowrap;
            justify-content: flex-end;

            @include media('<tablet') {
                align-items: stretch;
                flex-wrap: wrap;
                justify-content: center;
            }

            a,
            button {
                @include anchor-pagination();

                span {
                    display: inline-block;
                    padding-left: $measure--half;
                    padding-right: $measure--half;
                }
            }

            .pagination__controls__ellipsis {
                align-items: center;
                display: flex;
                justify-content: center;

                span {
                    display: inline-block;
                    padding-bottom: $border-width-default;
                }
            }

            a,
            button,
            .pagination__controls__ellipsis {
                margin-right: $measure--half;
                margin-top: $measure--threequarters;
                min-height: $minimum-control-height;
                padding-bottom: $measure--quarter;

                :before,
                span {
                    align-self: center;
                }
            }

            [data-page] {
                display: none;

                &[aria-expanded='true'],
                &[data-page='1'],
                &.pagination__controls__button--last {
                    display: flex;
                }

                &[data-proximity='1'],
                &[data-proximity='-1'] {
                    @include media('>=tablet') {
                        display: flex;
                    }
                }

                &[data-proximity='2'],
                &[data-proximity='-2'] {
                    @include media('>=desktop') {
                        display: flex;
                    }
                }
            }

            .pagination__controls__ellipsis {
                display: none;
            }

            &:not([data-pagecount='2']):not([data-pagecount='3']):not([data-pagecount='4']):not([data-pagecount='5']) {
                [data-page='1'] {
                    &:not([aria-expanded='true']) {
                        &:not([data-proximity='1']):not([data-proximity='-1']) {
                            & + .pagination__controls__ellipsis {
                                @include media('<tablet') {
                                    display: flex;
                                }
                            }
                        }

                        &:not([data-proximity='1']):not([data-proximity='-1']):not([data-proximity='2']):not([data-proximity='-2']) {
                            & + .pagination__controls__ellipsis {
                                @include media('>=tablet', '<desktop') {
                                    display: flex;
                                }
                            }
                        }

                        &:not([data-proximity='1']):not([data-proximity='-1']):not([data-proximity='2']):not([data-proximity='-2']):not([data-proximity='3']):not([data-proximity='-3']) {
                            & + .pagination__controls__ellipsis {
                                @include media('>=desktop') {
                                    display: flex;
                                }
                            }
                        }
                    }
                }

                .pagination__controls__button--penultimate {
                    &:not([aria-expanded='true']) {
                        &:not([data-proximity='-1']) {
                            & + .pagination__controls__ellipsis {
                                @include media('<tablet') {
                                    display: flex;
                                }
                            }
                        }

                        &:not([data-proximity='1']):not([data-proximity='-1']) {
                            & + .pagination__controls__ellipsis {
                                @include media('>=tablet', '<desktop') {
                                    display: flex;
                                }
                            }
                        }

                        &:not([data-proximity='1']):not([data-proximity='-1']):not([data-proximity='2']):not([data-proximity='-2']) {
                            & + .pagination__controls__ellipsis {
                                @include media('>=desktop') {
                                    display: flex;
                                }
                            }
                        }
                    }
                }
            }

            .pagination__controls__button--next,
            .pagination__controls__button--prev {
                display: flex;

                span {
                    @include screenreader-only();
                }

                &:before {
                    @include font-family('sharp');

                    align-self: center;
                    font-size: $font-size--icon;
                    font-weight: $font-weight-normal;
                    padding-left: $measure--half;
                    padding-right: $measure--half;
                }
            }

            .pagination__controls__button--next {
                margin-right: 0;

                &:before {
                    content: '\f061';
                    font-size: $font-size--icon;
                    font-weight: $font-weight-normal;
                }
            }

            .pagination__controls__button--prev {
                &:before {
                    content: '\f060';
                    font-size: $font-size--icon;
                    font-weight: $font-weight-normal;
                }
            }

            &[data-pagecount='2'],
            &[data-pagecount='3'],
            &[data-pagecount='4'],
            &[data-pagecount='5'] {
                .pagination__controls__button {
                    display: flex;
                }

                .pagination__controls__ellipsis {
                    display: none;
                }
            }
        }

        .pagination__summary {
            @include smalltype();
            margin-top: 0;
            max-width: unset;
            text-align: right;
        }
    }
}

.content .pagination__wrapper {
    @include media('screen') {
        max-width: $max-line-length;
    }
}
