// <Desktop-specific layout - secondary navigation never present
main > .case-study--business {
    @include media('<small') {
        .case-study--business__overview__main__wrapper,
        .case-study--business__cta {
            @include default-margin();

            .case-study--business__overview__main,
            .case-study--business__overview__multimedia-banner {
                @include default-padding();
            }
        }
        .case-study--business__related-case-studies {
            padding-bottom: calc(#{$measure--basic} / 2);
        }
    }

    @include media('>=tablet', '<small') {
        .case-study--business__content__wrapper {
            @include x-column-grid(24);
            display: grid;
            @include default-margin();
        }
    }

    .case-study--business__content {
        padding: 0;
        @include media('>=tablet', '<small') {
            grid-column: 4/-4;
        }

        @include media('<tablet') {
            margin: 0;
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);

        > .case-study--business {
            grid-column: 1 / span 24;

            .case-study--business__overview__main__wrapper,
            .case-study--business__overview__multimedia-banner,
            .case-study--business__global-details__wrapper,
            .case-study--business__content__wrapper {
                @include x-column-grid(24);
                display: grid;

                .case-study--business__overview__main {
                    @include x-column-grid(22);
                    display: grid;
                    grid-column: 2 / -2;

                    .case-study--business__overview__main__details {
                        grid-column: 3 / -3;
                    }
                }

                .case-study--business__overview__multimedia-banner__video,
                .case-study--business__overview__multimedia-banner__image {
                    grid-column: 6 / -6;

                    .embedded-video {
                        margin-top: $measure--small2;

                        padding-bottom: 0;
                        padding-left: 0;
                        padding-right: 0;
                        padding-top: 0;
                    }

                    .youtube-video {
                        width: 100%;
                    }
                }

                .case-study--business__content {
                    grid-column: 7 / span 12;
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .case-study--business {
                grid-column: 7 / span 18;

                .case-study--business__overview__main__wrapper,
                .case-study--business__overview__multimedia-banner,
                .case-study--business__content__wrapper {
                    @include content-grid();

                    .case-study--business__overview__main {
                        @include x-column-grid(16);

                        display: grid;
                        grid-column: 2 / -2;

                        .case-study--business__overview__main__details {
                            grid-column: 3 / -3;
                        }
                    }

                    .case-study--business__overview__multimedia-banner__video,
                    .case-study--business__overview__multimedia-banner__image {
                        grid-column: 3 / -3;

                        .embedded-video {
                            margin-top: $measure--small2;
                            padding-bottom: 0;
                            padding-left: 0;
                            padding-right: 0;
                            padding-top: 0;
                        }

                        .youtube-video {
                            width: 100%;
                        }
                    }

                    .case-study--business__content {
                        grid-column: 4 / span 12;
                    }
                }
            }
        }
    }
}

// Generic styles
.case-study--business {
    .case-study--business__overview__main {
        background-color: $color-gray13;
        padding-bottom: $measure--basic;
        padding-top: $measure--basic;

        @include media('>between') {
            padding-top: $measure--small4;
        }

        .case-study--business__overview__main__details {
            .label {
                font-weight: $font-weight-medium;
                margin-right: $measure--half;
            }

            .case-study--business__overview__main__details__company-cards {
                @include media('>=tablet') {
                    display: flex;
                    margin-bottom: $measure--basic;
                }
            }

            .case-study--business__overview__main__details__result p {
                display: inline;
            }
        }
    }

    .case-study--business__content__wrapper {
        @include default-margin;
    }

    .case-study--business__company-card {
        background-color: $color-purewhite;
        border-bottom: $border-width-hairline $border-style-solid $color-gray3;
        border: $border-width-hairline $border-style-solid $color-gray3;
        color: $color-gray7;
        display: flex;
        margin-bottom: $measure--basic;
        max-height: $measure--large3;
        min-height: $measure--large3;
        transition: none;
        width: 100%;

        @include media('>tablet') {
            margin-bottom: 0;
            width: 20rem;
        }

        &:first-child {
            margin-right: $measure--basic;
        }

        &:focus,
        &:visited {
            color: $color-nearblack;
            background-image: none;
            background-color: $color-purewhite;
            border-bottom: $border-width-hairline $border-style-solid
                $color-gray3;
            @include box-shadow;

            .company-card__name {
                &:after {
                    border-bottom-color: $color-nearblack;
                    border-bottom-style: $border-style-solid;
                    border-bottom-width: $border-width-thick;
                }
            }
        }

        &:hover {
            @include media('hover') {
                color: $color-nearblack;
                background-image: none;
                background-color: $color-purewhite;
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray3;
                @include box-shadow;

                .company-card__name {
                    &:after {
                        border-bottom-color: $color-nearblack;
                        border-bottom-style: $border-style-solid;
                        border-bottom-width: $border-width-thick;
                    }
                }
            }
        }

        figure {
            margin-top: 0;
            overflow: hidden;
        }

        .figure--image,
        .figure--svg {
            align-items: center;
            display: flex;
            justify-content: center;
            padding: $measure--basic;
            position: relative;
            width: $measure--medium11;

            picture {
                height: 100%;
                text-align: center;

                img {
                    height: auto;
                    max-height: 100%;
                    max-width: 100%;
                    width: auto;
                }
            }

            img {
                //SVG specific without picture wrapping element
                height: auto;
                max-height: 100%;
                max-width: 100%;
                width: auto;

                @include media('<=mobile') {
                    padding-bottom: 0;
                }
            }

            @include media('>tablet') {
                width: $measure--large4;
            }

            &:after {
                background-color: $color-gray2;
                content: '';
                height: $measure--medium9;
                position: absolute;
                right: 0;
                top: $measure--basic;
                width: 1px;
            }
        }
    }

    .company-card__name {
        padding-bottom: $measure--quarter;
        position: relative;

        &:after {
            bottom: 0;
            content: '';
            height: $border-width-thick;
            left: 0;
            position: absolute;
            width: 100%;
        }

        .fa-external-link {
            margin-left: 0;
        }
    }

    .company-card__name-title {
        @include font-family('heading');

        margin-bottom: $measure--half;
    }

    .case-study--business__company-card__details {
        align-items: center;
        display: flex;
        padding-left: $measure--basic;
    }

    .case-study--business__content {
        margin-bottom: $measure--basic;

        h2 {
            margin-top: $measure--small2;
        }
    }

    .case-study--business__content__share-icons__wrap {
        margin-bottom: $measure--small4;
        margin-left: 0;
        margin-right: 0;
        margin-top: $measure--small4;
    }

    .case-study--business__content__share-icons {
        border-bottom: $border-width-hairline $border-style-solid $color-gray2;
        border-top: $border-width-hairline $border-style-solid $color-gray2;
        position: relative;
    }

    .case-study--business__content__share-icons__icons {
        ul {
            list-style-type: none;
            margin: $measure--small2 0;
            display: flex;
            max-width: none;
        }

        li {
            margin-right: $measure--threequarters;
            margin-top: 0;
        }

        li.copyLink {
            margin-right: 0;
        }
    }

    .case-study--business__global-details__container {
        padding-bottom: $measure--small5;
        grid-column: 2/-2;

        .contact-cards {
            .related-content__heading {
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray4;
                margin-top: $measure--small4;
            }

            .related-content {
                margin-top: $measure--threequarters;

                @include media('<tablet') {
                    flex-direction: column;
                }

                .card--contact {
                    flex: 1;
                    padding-top: 0;
                    padding-bottom: 0;
                    border-top: none;

                    @include media('>=tablet') {
                        &:nth-child(2) {
                            margin-left: $measure--basic;
                        }
                    }

                    @include media('<tablet') {
                        &:nth-child(2) {
                            margin-top: $measure--basic;
                        }
                    }
                }
            }
        }
    }

    .case-study--business__related-events {
        margin-top: $measure--basic;
        display: flex;
        flex-direction: column;

        @include media('>=tablet') {
            padding-bottom: $measure--small2;
        }
    }

    .case-study--business__related-case-studies__wrapper {
        .case-study--business__related-case-studies {
            @include default-margin();
        }

        h2,
        h3 {
            text-align: center;
        }

        .related-content__heading {
            margin-top: $measure--small4;
        }
    }
}
