.card--casestudy {
    &:not(.card--casestudy--alternative) {
        .card__details {
            display: flex;
            flex-wrap: nowrap;

            @include media('<tablet') {
                flex-direction: column;
            }

            @include media('>=tablet') {
                flex-direction: row;
            }
        }

        .card__thumbnail + .card__details__text {
            @include media('<=mobile') {
                padding-left: 0;
                padding-right: 0;
            }

            @include media('>=small') {
                padding-top: 0;
            }
        }

        .card__details__text {
            padding-top: $measure--half;
        }

        .card__thumbnail {
            overflow: hidden;

            @include media('<small') {
                padding-left: $measure--basic;
            }

            @include media('<between') {
                height: $measure--huge1;
                width: $measure--huge1;
                padding-left: $measure--basic;
                padding-top: $measure--basic;
            }

            @include media('>=between') {
                height: $measure--huge3;
                width: $measure--huge3;
            }

            picture,
            img {
                height: 100%;
                max-width: unset;
                width: auto;
            }
        }

        .card__description {
            margin-top: 0;
        }
    }

    &.card--casestudy--alternative {
        padding: $measure--half;

        .card__anchor {
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;

            @include media('>between') {
                flex-direction: row;
            }

            &:after {
                display: none;
            }

            &,
            &:focus,
            &:active,
            &:visited {
                border: none;
            }

            &:hover {
                @include media('hover') {
                    background-color: transparent;
                    border: none;

                    .card__details__text {
                        background-color: $color-red80;
                    }
                }
            }

            @include media('<=between') {
                background-color: transparent;
            }

            .wrapper--card__thumbnail {
                align-self: center;
                overflow: hidden;

                @include media('>between') {
                    width: 45%;
                }
            }

            .card__thumbnail {
                @include media('<=between') {
                    max-height: 15rem;
                }

                @include media('>between') {
                    margin-bottom: $measure--small2;
                    margin-top: $measure--small2;
                }

                img {
                    display: flex;

                    @include media('<=between') {
                        height: auto;
                        width: 100%;
                    }
                }
            }

            .card__details__text {
                @include box-shadow();

                background-color: $color-red70;
                color: $color-purewhite;
                transition: background-color $transition-duration ease-in;

                @include media('>between') {
                    width: 55%;
                }

                @include media('reducemotion') {
                    transition: none;
                }

                .card__type {
                    @include extrasmalltype();

                    color: $color-gray2;
                    font-style: italic;
                    margin-bottom: $measure--basic;
                }
            }
        }
    }
}
