.event__formassembly__button {
    @include cta-basic-light-bg();

    @include media('>=small') {
        display: block;
        margin-left: auto;
        margin-right: 0;
    }

    text-align: center;
}

.event__formassembly__form {
    @include default-padding();

    border: $border-width-default $border-style-solid $color-gray3;

    .wFormHeader {
        display: none;
    }

    .wFormContainer .wForm {
        padding-top: 0;
    }

    &:focus {
        @include outline-properties($color-nearblack);

        outline-offset: 0;
    }

    .wrapper--event__formassembly__form__heading {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
    }
}

.event__formassembly__form__close-button {
    @include cta-primary-light-bg($color-gray3, $color-nearblack);

    display: block;
    margin-bottom: $measure--basic;
    margin-left: auto;
    margin-right: 0;
    margin-top: $measure--basic;

    &:hover,
    &:active {
        background-color: $color-gray5;
        color: $color-nearblack;
    }
}

.js .event__formassembly__form {
    overflow-y: hidden;
    transition-duration: $transition-duration--long;
    transition-property: height;
    transition-timing-function: $transition-function;

    &[data-open='false'] {
        display: none;
    }
}
