.card--testimonial {
    .card__details {
        display: flex;
        flex-direction: column;
        position: relative;

        @include media('>tablet') {
            flex-direction: row;
        }

        .card__thumbnail {
            margin-top: $measure--small4;

            @include media('<=tablet') {
                position: absolute;
                right: 0;
                bottom: -$measure--half;
                margin-top: -$measure--medium3;
            }

            @include media('>tablet') {
                align-self: start;
                margin-left: $measure--basic;
                margin-right: $measure--small4;
            }

            img {
                width: $measure--large4;
                max-width: none;
                @include media('<=tablet') {
                    width: $measure--medium7;
                }
            }
        }

        .card__details__text {
            padding-bottom: $measure--basic;
        }

        .card--testimonial__author {
            .card__tag {
                @include smalltype();

                color: $color-gray7;
                font-weight: $font-weight-bold;
            }
        }

        .card--testimonial__quote {
            margin-top: $measure--small4;
            position: relative;
            padding-left: $measure--threequarters;

            p {
                @include standfirst();
                /**
               * Increasing the font size of the quotation marks means that the
               * lines containing them are slightly taller, resulting in uneven
               * spacing.
               *
               * Increase the spacing throughout to make space and so it’s at
               * equal throughout.
               **/
                line-height: $line-height-ratio--override--zeta;
                color: $color-gray10;

                &:first-child {
                    margin-top: 0;
                }
            }

            span {
                position: relative;
            }

            &:before,
            &:after {
                @include standfirst();
                @include zetatype();
                color: $color-pink90;
                display: inline;
                /**
               * Quotation mark line height equal to other lines, even though
               * the font size is greater.
               **/
                line-height: $line-height-ratio--override--alpha;
            }

            &:before {
                content: '“';
                left: 0;
                position: absolute;
                top: #{-$measure--half};
            }

            &:after {
                content: '”';
                right: auto;
                position: absolute;
                bottom: 0;
            }
        }
    }
}
