.js .paginated-list {
    margin-left: 0;

    .page {
        list-style-type: none;
        max-width: unset;
    }

    @include media('screen') {
        .page {
            display: none;
            margin-top: 0;

            &[data-open='true'] {
                display: list-item;
                margin-top: $measure--basic;

                &:focus {
                    @include outline-properties($color-nearblack);
                }
            }

            ul {
                list-style: disc;

                ul {
                    list-style: circle;
                }
            }
        }
    }
}
