.accordion {
    &.accordion--timeline {
        .timeline__content {
            position: relative;
            &.expanded {
                &::before {
                    /* Vertical line */
                    content: '';
                    position: absolute;
                    left: 18px;
                    height: 100%;
                    width: 4px;
                    background-color: $color-nearblack;
                    z-index: z-index('default');
                    @include media('>=small') {
                        left: 50%;
                    }
                }
            }
        }

        .timeline__content__block {
            padding-bottom: $measure--basic;
            position: relative;
            &:nth-child(1) {
                padding-top: $measure--basic;
            }
            &:first-of-type {
                top: $measure--quarter;
                @include media('>=small') {
                    .timeline__content__block__item__connector {
                        top: $measure--small5;
                    }
                }
            }

            @include media('>=small') {
                margin-top: -5%;
                &:nth-child(even) {
                    animation: timeline-bounce-2 $transition-duration--long;
                    .timeline__content__block__item {
                        position: relative;
                        left: 55%;
                        .timeline__content__block__item__connector {
                            left: -11%;
                            span {
                                left: 80px;
                            }
                        }
                    }
                }
                &:nth-child(odd) {
                    animation: timeline-bounce-2-inverse
                        $transition-duration--long;
                }
            }
        }

        .timeline__content__block__item {
            border: $border-width-hairline $border-style-solid $color-gray3;
            padding: $measure--basic;
            margin: 0 20px 0 45px;
            background-color: $color-purewhite;

            @include media('>=small') {
                margin: 0 45px 0 0;
                width: 45%;
            }

            .timeline__content__block__item__text {
                &:last-child {
                    margin-bottom: 0;
                }
                img {
                    display: block;
                    margin-bottom: $measure--basic;
                }
            }
        }

        .timeline__content__block__item__connector {
            border-top: $border-width-default $border-style-solid
                $color-nearblack;
            position: relative;
            left: -42px;
            width: 50px;

            @include media('>=small') {
                position: absolute;
                width: 80px;
                left: auto;
                right: 50%;
            }

            span {
                width: 7px;
                height: 7px;
                border: $border-width-hairline $border-style-solid
                    $color-nearblack;
                border-radius: 50%;
                display: block;
                color: transparent;
                position: relative;
                top: -4px;
                left: 50px;

                @include media('>=tablet') {
                    width: 10px;
                    height: 10px;
                    border: $border-width-default $border-style-solid
                        $color-nearblack;
                    top: -6px;
                    left: 50px;
                }
                @include media('>=small') {
                    left: auto;
                    right: 10px;
                }
            }
        }
    }
}

@keyframes timeline-bounce-2 {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }

    60% {
        opacity: 1;
        transform: translateX(20px);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes timeline-bounce-2-inverse {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }

    60% {
        opacity: 1;
        transform: translateX(-20px);
    }

    100% {
        transform: translateX(0);
    }
}

// Override default accordion body rule
.js
    .accordion
    > .accordion__heading[data-open='true']
    + .accordion__body
    > .timeline__content.expanded {
    top: $measure--quarter;
    @include media('>=small') {
        top: $measure--small5;
        margin-bottom: $measure--medium4;
    }
}

.no-js {
    .accordion {
        &.accordion--timeline {
            margin-top: $measure--medium4;
            .collapsed {
                display: block;
            }
            .accordion__heading {
                height: 0;
                margin: 0 0 0 $measure--medium3;
                @include media('>=small') {
                    margin: 0;
                }
            }
            .timeline__content__block:first-of-type {
                margin: 0;
                padding: 0 0 $measure--medium5 0;
                top: $measure--medium1;
            }
        }
    }
}
