.finder__results__list,
.search__results {
    .card--research {
        @include search-contact-research-card();
    }
}

.card--research {
    @include contact-research-card();
}

.related-content {
    .card {
        &.card--research {
            background-color: $color-purewhite;
            .card__details__text {
                padding: $measure--basic;
                .card--research__title-link {
                    @include h4style();
                    margin-top: 0;
                }
                .card__tag {
                    display: flex;
                    align-items: baseline;
                    .icon {
                        min-width: $measure--small2;
                    }
                }
            }
        }
    }
}
