// Desktop-specific layout
main {
    @include media('>=small') {
        > .listing {
            grid-column: 1 / span 24;

            .banner-text {
                @include standard-grid();
                padding-left: $measure--half;
                padding-right: $measure--half;

                .banner-text__content {
                    grid-column: 3/-3;
                    margin-right: 0;
                    margin-left: 0;
                    padding-left: 0;
                    padding-right: 0;
                }
            }

            .finder {
                @include x-column-grid(24);
                display: grid;

                .finder__query,
                .wrapper--finder__filters,
                .finder__results {
                    grid-column: 3 / -3;
                }

                .finder__query {
                    margin-left: 0;
                    margin-right: 0;
                }

                .wrapper--finder__filters .finder__filters__heading,
                .finder__results__summary {
                    margin: 0;
                    padding-left: 0;
                    padding-right: 0;
                }

                .wrapper--finder__filters .wrapper--finder_filters--filters {
                    margin: 0;
                }

                .finder__results__list {
                    @include x-column-grid(20);

                    .finder__results__card {
                        grid-column: 3 / -3;
                    }

                    &.finder__results__list--three-up {
                        display: flex;

                        .finder__results__card {
                            grid-column: unset;
                            width: calc(50% - #{$measure--basic});
                        }
                    }
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .listing {
                grid-column: 7 / span 18;

                .banner-text {
                    display: inherit;

                    .banner-text__content {
                        margin-left: $measure--small2;
                        margin-right: $measure--small2;
                    }
                }

                .finder {
                    display: block;

                    .finder__query {
                        margin-left: $measure--small2;
                        margin-right: $measure--small2;
                    }

                    .wrapper--finder__filters .finder__filters__heading,
                    .finder__results__summary,
                    .finder__results__announcement {
                        @include default-margin();
                    }

                    .wrapper--finder__filters
                        .wrapper--finder_filters--filters {
                        @include default-margin();
                    }

                    .finder__results__list {
                        @include content-grid();

                        .finder__results__card {
                            grid-column: 3 / -3;
                        }

                        &.finder__results__list--three-up {
                            display: flex;

                            .finder__results__card {
                                grid-column: unset;
                            }
                        }
                    }
                }
            }
        }
    }
}

// layout no side navigation present
main {
    @include media('<small') {
        .listing {
            .banner-text {
                .banner-text__content {
                    padding-left: $measure--half;
                    padding-right: $measure--half;
                    margin-left: $measure--small2;
                    margin-right: $measure--small2;
                }
            }
        }
    }

    @include media('<tablet') {
        .listing {
            .banner-text {
                .banner-text__content {
                    margin-left: $measure--half;
                    margin-right: $measure--half;
                }
            }
        }

        .finder {
            .finder__query {
                margin-left: $measure--half;
                margin-right: $measure--half;
            }
        }
    }
}

// Generic
.listing {
    /*** BANNER COLOURS
     *
     * Each listing has a unique theme ID attribute for banner colour styling
     ***/

    // Courses
    &[data-theme-id='494808'] {
        .banner-text {
            background-color: $color-gray666;
            color: $color-purewhite;
        }
    }

    // No search component
    &[data-theme-id='531052'],
    &[data-theme-id='592970'],
    &[data-theme-id='495139'] {
        .banner-text {
            background: $color-brown30;
            background: linear-gradient(
                180deg,
                $color-brown30 0%,
                $color-purewhite 100%
            );
            color: $color-nearblack;
        }
    }

    // Generic banner rules
    .banner-text {
        background: $color-gray666;
        color: $color-purewhite;
        padding-bottom: $measure--large1;
        padding-top: $measure--small2;
    }

    /*** END BANNER COLOURS ***/

    .content {
        margin: 0;
    }

    padding-bottom: $measure--small2;

    .finder__query {
        margin-top: -$measure--medium7;
        @include media('<tablet') {
            margin-left: 0;
            margin-right: 0;
        }
    }

    @include media('<tablet') {
        .finder__results {
            @include default-padding();
        }
    }

    .wrapper--listing__content {
        @include default-padding();

        background-color: $color-pink90;
        color: $color-purewhite;
        margin-top: $measure--small2;
        padding-bottom: $measure--small2;
        padding-top: $measure--basic;

        .card--contact {
            border-top: 0;

            .icon {
                color: $color-purewhite;
            }

            a {
                @include anchor-inline(
                    $color-gray4,
                    $color-gray4,
                    $color-gray11,
                    $color-outline--dark
                );
            }
        }
    }

    @include media('<small', '>=tablet') {
        .finder__results__list--three-up {
            .card--news a {
                flex-direction: column;

                img {
                    width: 100%;
                }
            }
        }
    }

    @include media('<tablet') {
        .wrapper--finder__filters--mobile[data-open='false'] {
            margin-top: -#{$measure--medium7};
        }

        .finder__query ~ .wrapper--finder__filters--mobile[data-open='false'] {
            margin-top: unset;
        }
    }

    @include media('>=tablet') {
        .wrapper--finder__filters {
            margin-top: -#{$measure--medium7};
        }

        .finder__query ~ .wrapper--finder__filters {
            margin-top: unset;
        }
    }
}
