$colors: (
    blue70: $color-gray4,
    pink90: $color-gray4,
    red70: $color-gray4,
);

@each $class, $color in $colors {
    $textColor: nth($color, 1);
    .related-links--with-images__cards {
        .card--related-links--with-images {
            &:not(.background--purewhite):not(.background--gray2) {
                &.background--#{$class} {
                    color: $color-purewhite;
                    .link-decorator {
                        color: $color-purewhite;
                    }
                    a {
                        @include anchor-inline(
                            $textColor,
                            $textColor,
                            $textColor,
                            $textColor
                        );
                        &:hover {
                            @include media('hover') {
                                color: $color-gray11;
                            }
                        }
                    }
                }
            }
        }
    }
}

.related-links--with-images__cards {
    @include media('>=tablet') {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: $measure--small2;
    }

    @include media('<tablet') {
        .card--related-links--with-images {
            &:not([data-image='true']) {
                .card--related-links--with-images__links__content {
                    padding-top: $measure--quarter;
                }
            }
        }
    }

    list-style: none;
    margin: 0;
    max-width: unset;
    padding-bottom: $measure--small1;

    .card--related-links--with-images {
        height: max-content;
        &.background--purewhite,
        &.background--gray2 {
            border: $border-width-hairline $border-style-solid $color-gray1;
            &:not([data-image='true']) {
                border-top: $measure--half $border-style-solid $color-red50;
            }
        }
        .card--related-links--with-images__links__content {
            padding: 0 $measure--basic $measure--basic $measure--basic;
            .card--related-links--with-images__links {
                p {
                    display: table;
                    span {
                        display: table-cell;
                        padding-right: $measure--half;
                    }
                }
            }
        }
    }
}
