// <Desktop-specific layout - secondary navigation never present
main > .profile {
    @include media('<small') {
        .standfirst,
        .profile--global__date {
            @include default-margin();
        }
    }

    @include media('<=mobile') {
        .profile__banner__content {
            display: grid;
            grid-gap: $measure--basic;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: repeat(2, auto);
            padding-bottom: $measure--small4;
            padding-top: $measure--small2;

            h1 {
                align-self: flex-end;
                grid-column: span 3;
                grid-row: 1;
            }

            .profile__banner__content__details {
                grid-column: span 3;
                grid-row: 2;
            }

            picture {
                grid-column: 1;
                grid-row: 1;

                ~ h1 {
                    grid-column: span 2;
                }
            }
        }
    }

    @include media('>mobile', '<small') {
        .profile__banner__content {
            display: grid;
            grid-column-gap: $measure--small2;
            grid-template-columns: repeat(3, auto);
            grid-template-rows: repeat(2, auto);
            padding-bottom: $measure--large2;

            h1 {
                grid-column: span 3;
                grid-row: 1;
                padding-top: $measure--medium1;
            }

            .profile__banner__content__details {
                grid-column: span 3;
                grid-row: 2;
            }

            picture {
                grid-column: 1;
                grid-row: span 2;

                ~ h1,
                ~ .profile__banner__content__details {
                    grid-column: 2 / span 2;
                }
            }
        }
    }

    @include media('>=tablet', '<small') {
        .profile__banner--with-contact {
            .profile__banner__content {
                padding-bottom: $measure--large3;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);

        > .profile {
            @include standard-grid();
            grid-auto-rows: min-content;
            grid-column: 1 / span 24;

            .profile__banner,
            .wrapper--profile__contact {
                grid-column: 1 / span 24;
            }

            .profile__banner__content {
                display: grid;
                grid-column-gap: $measure--small2;
                grid-template-columns: repeat(3, auto);
                grid-template-rows: repeat(2, auto);
                padding-bottom: $measure--large2;

                h1 {
                    grid-column: span 3;
                    grid-row: 1;
                    padding-top: $measure--medium1;
                }

                .profile__banner__content__details {
                    grid-column: span 3;
                    grid-row: 2;
                }

                picture {
                    grid-column: 1;
                    grid-row: span 2;

                    ~ h1,
                    ~ .profile__banner__content__details {
                        grid-column: 2 / span 2;
                    }
                }
            }

            .profile__banner--with-contact {
                .profile__banner__content {
                    padding-bottom: $measure--large3;
                }
            }

            .standfirst,
            .profile--global__about,
            .profile--global__date {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .profile {
                @include content-grid();

                grid-column: 7 / span 18;

                .profile__banner,
                .wrapper--profile__contact {
                    grid-column: 1 / span 18;
                }

                .profile__banner__content {
                    grid-template-columns: 1fr 2fr;

                    h1 {
                        align-self: flex-end;
                        grid-column: 1 / span 2;
                    }

                    .profile__banner__content__details {
                        grid-column: 1 / span 2;
                    }

                    picture {
                        ~ h1,
                        ~ .profile__banner__content__details {
                            grid-column: 2;
                        }
                    }
                }

                .standfirst,
                .profile--global__about,
                .profile--global__date,
                .section__wrapper {
                    grid-column: 4 / span 12;
                }
            }
        }
    }
}

// Generic styles

.profile {
    padding-bottom: $measure--small4;
    .standfirst {
        margin-top: $measure--basic;
    }

    .standfirst,
    .profile--global__about,
    .profile--global__about-heading {
        @include media('<small') {
            @include standard-box;
        }

        @include media('>=small') {
            grid-column: 4 / span 12;
        }
    }

    .wrapper--profile__contact {
        @include default-margin();

        position: relative;

        .profile__contact {
            @include default-padding();

            background-color: $color-purewhite;

            @include media('>=tablet') {
                margin-top: -$measure--medium7;
            }
        }

        .profile__contact__content {
            padding-bottom: $measure--basic;
            padding-top: $measure--basic;
        }

        .profile__contact__content__heading {
            @include content-style();

            border-bottom: $border-style-solid $border-width-default
                $color-gray2;
            color: $color-gray7;
            font-weight: $font-weight-medium;
            margin-top: 0;
            max-width: unset;

            .icon {
                color: $color-gray7;
                margin-right: $measure--half;
            }
        }

        .profile__contact__content__list {
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            margin-left: 0;
            max-width: unset;

            li {
                list-style: none;
            }

            @include media('>=between') {
                flex-direction: row;

                li {
                    width: 50%;
                }
            }

            span {
                color: $color-red50;
                margin-right: $measure--basic;
            }

            .fa-phone {
                @include fa-icon-rotate(90deg, 1);
            }
        }
    }

    .wrapper--profile__banner__content {
        @include default-margin();
        @include default-padding();
        color: $color-purewhite;
    }

    .profile__banner__content {
        picture {
            img {
                max-height: $measure--huge5;
            }

            padding-top: $measure--small2;

            @include media('>=tablet') {
                padding-top: $measure--medium1;
            }
        }
    }

    .tabs--accordion {
        grid-column: 2 / 18;

        @include media('<=tablet') {
            @include standard-box();
            margin-top: $measure--small5;
        }
    }

    .tabs__panel__content {
        margin: auto;
        max-width: $max-line-length;
    }

    .tabs--accordion
        > .accordion
        .accordion__heading
        button
        .accordion__heading__text {
        background: $color-gray10;
    }

    .tabs .tabs__links li a[aria-selected='true'] span {
        border-bottom-color: $color-gray10;
    }

    .profile__banner {
        @include corner-gradient-background(
            $main-color: $color-gray10,
            $corner-color: $color-nearblack
        );
    }

    .profile__banner--student,
    .profile__banner--honorary,
    .profile__banner--alumni,
    .profile__banner--ambassador {
        @include corner-gradient-background(
            $main-color: $color-pink90,
            $corner-color: $color-pink95
        );
    }

    .profile__banner--press {
        @include corner-gradient-background(
            $main-color: $color-red60,
            $corner-color: $color-red70
        );
    }

    .profile__banner--staff {
        @include corner-gradient-background(
            $main-color: $color-blue90,
            $corner-color: $color-blue100
        );
    }

    .profile__banner__content__details {
        align-self: flex-start;
    }

    .profile__banner__content__details__organisation {
        @include font-family('default');
        @include alphatype();
        @include body-font-features();

        font-weight: $font-weight-text;
        margin-top: $measure--small2;

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-nearblack,
                $color-outline--dark
            );
        }

        span:not(:first-of-type):not(:last-of-type):after {
            content: '\2219';
            margin-left: $measure--half;
            margin-right: $measure--half;
        }

        span:first-of-type {
            margin-right: $measure--half;
        }
    }

    /* --- GLOBAL PROFILE STYLES --- */

    .profile--global__tags {
        margin-top: $measure--medium3;
        padding-bottom: $measure--basic;
        padding-top: $measure--basic;

        .content {
            grid-column: 1/-1;
            padding: 0;
        }

        @include media('<small') {
            @include standard-box;
        }

        @include media('>=small') {
            grid-column: 4 / span 12;
        }
    }

    .profile--global__tags--nocontact {
        margin-top: 0;
    }

    .profile--global__tags__heading {
        @include content-style();

        color: $color-gray8;
        font-weight: $font-weight-bold;
        margin-bottom: $measure--half;
        margin-top: 0;
        max-width: $max-line-length;
    }

    .profile--global__tags__labels {
        @include media('>=tablet') {
            max-width: $max-line-length;
        }

        .cta-block {
            flex-wrap: wrap;
        }

        .cta-block__cta {
            @include smalltype();

            margin: 0 $measure--small2 $measure--threequarters 0;
        }
    }

    h2.profile--global__details__heading {
        margin-top: 0;
        border-bottom: $border-style-solid $border-width-default $color-gray2;

        span:first-child {
            font-size: $font-size--basic;
        }
    }

    .profile--global__details__heading__text {
        @include content-style;
        color: $color-gray7;
        font-weight: $font-weight-bold;
        margin-left: initial;
    }

    .profile--global__details__content {
        a {
            span {
                margin-left: $measure--quarter;
            }
        }
    }

    .profile--global__personal-links,
    .profile--global__other-contact-methods {
        .profile--global__details__content {
            margin-top: $measure--threequarters;
        }
    }

    .profile--global__contact-details {
        .profile--global__details__content {
            margin-top: $measure--quarter;
        }
    }

    .profile--global__details__content__column {
        ul {
            margin-left: 0;
        }

        li {
            list-style: none;
        }

        .icon {
            color: $color-red50;
            margin-right: $measure--half;
        }

        .fa-phone {
            @include fa-icon-rotate(90deg, 1);
        }
    }

    .profile--global__personal-links {
        ul {
            margin-left: $measure--small2;
        }
    }

    .profile--global__contact-details {
        padding-top: $measure--basic;

        @include media('>=small') {
            grid-column: 2 / span 14;
        }

        @include media('>=desktop') {
            grid-column: 2 / span 6;
        }

        &:nth-child(2) {
            margin-top: $measure--small2;
        }

        .profile--global__details__content {
            @include media('>=tablet') {
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-column-gap: $measure--small4;
            }

            @include media('>desktop') {
                display: block;
            }
        }
    }

    .profile--global__other-contact-methods {
        margin-top: $measure--small4;
        display: flex;

        @include media('<tablet') {
            flex-direction: column;
        }

        @include media('>=desktop') {
            margin-top: $measure--basic;
        }

        .profile--global__details__content {
            @include content-style;
        }

        .profile--global__details {
            flex: 1;
        }

        .profile--global__personal-links {
            flex: 1;

            @include media('>tablet') {
                margin-left: $measure--small2;
            }
        }
    }

    .tabs--profile {
        grid-column: 3/-3;
        @include media('<small') {
            @include standard-box();
        }
    }

    :not(.tabs) > .tabs__links {
        display: none;
    }

    .profile--global__details__content__name {
        display: block;
        font-weight: $font-weight-bold;
    }

    .profile--global__date {
        color: $color-gray7;
        border-top: $border-style-solid $border-width-thick $color-gray7;
        padding-top: $measure--basic;
        margin-top: $measure--small4;

        @include media('>=tablet') {
            margin-top: $measure--medium3;
        }
    }

    /* --- END GLOBAL PROFILE STYLES --- */

    /* --- ACADEMIC STYLES --- */

    &.profile--academic {
        @include standard-box();
        margin-bottom: $measure--medium1;
        position: relative;

        @include media('<=mobile') {
            padding-left: $measure--threequarters;
        }

        @include media('>=tablet') {
            @include standard-grid();
            background: $color-purewhite;
            margin-top: -$measure--medium7;
        }
    }

    .profile--staff,
    .profile--academic {
        .profile--global__personal-links {
            @include media('<tablet') {
                margin-top: $measure--small4;
            }
        }
    }

    /* --- END ACADEMIC STYLES --- */

    /* --- PRESS STYLES -- */

    .wrapper--profile__contact--press {
        @include media('>=small') {
            margin: 0;
            display: grid;
            @include x-column-grid(18);
        }

        .profile__contact {
            @include media('>=small') {
                grid-column: 2 / span 16;
                padding: 0;
                display: grid;
                @include x-column-grid(16);
            }
        }

        .profile__contact__content {
            padding-top: $measure--small4;

            @include media('>=small') {
                grid-column: 3 / span 12;
            }
        }
    }

    .profile--press__contact__about {
        margin-top: $measure--small4;

        h2 {
            margin-bottom: $measure--basic;
        }
    }

    .profile--press__contact__details__content__column {
        ul {
            @include media('>=tablet') {
                display: flex;

                li {
                    margin-bottom: $measure--basic;
                    margin-right: $measure--medium11;

                    &:nth-child(2) {
                        margin-right: 0;
                    }
                }
            }
        }
    }

    /* --- END PRESS STYLES --- */

    /* --- ALUMNI STYLES --- */

    section.profile--alumni__contact {
        background: $color-purewhite;
        grid-column: 2 / span 16;
        padding-top: $measure--basic;
        padding-bottom: $measure--basic;

        @include media('>=tablet') {
            margin-top: -$measure--medium7;
        }

        @include media('<small') {
            @include standard-box();
        }
    }

    .profile--alumni__contact__inner {
        @include media('>=small') {
            display: grid;
            @include x-column-grid(16);
        }
    }

    .profile--alumni__details {
        @include media('>=tablet') {
            display: flex;
        }

        @include media('>tablet') {
            grid-column: 3 / span 12;
            padding: 0;
        }

        .profile--global__details {
            &:nth-child(2) {
                margin-top: $measure--small4;

                @include media('>=tablet') {
                    margin-top: 0;
                }
            }
        }
    }

    .profile--alumni__details__column {
        @include media('>=tablet') {
            width: 50%;
        }

        &:first-child {
            @include media('>=tablet') {
                margin-right: $measure--small4;
            }

            @include media('>desktop') {
                margin-right: $measure--medium7;
            }
        }
    }

    /* --- END ALUMNI STYLES --- */

    /* --- STUDENT/AMBASSADOR STYLES --- */

    &.profile__banner--student,
    &.profile__banner--ambassador {
        .profile__banner__content {
            @include media('>=tablet') {
                padding-bottom: $measure--small4;
            }
        }
    }

    .profile--global__tags--student {
        margin-top: 0;

        .profile--global__tags__content {
            padding-bottom: $measure--small4;
        }
    }

    /* --- END STUDENT STYLES --- */

    .profile__news-three-block {
        .related-content {
            @include media('>=tablet', '<=between') {
                // use flex to centre cards
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
            }

            .card {
                @include media('>=tablet', '<=between') {
                    // centre the cards
                    max-width: 28rem; // this to be removed
                }

                @include media('<=between') {
                    margin-bottom: $measure--basic;

                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }

    .profile__news-three-block__heading {
        grid-column: 1/-1;
        max-width: $max-line-length;
        margin-top: $measure--small4;
        margin-bottom: $measure--small4;
    }
}

/* --- STAFF/ACADEMIC STYLES --- */

.profile--staff,
.profile--academic {
    background: $color-purewhite;
    padding-left: $measure--medium3;
    padding-right: $measure--medium3;
    display: flex;
    flex-direction: row;

    @include media('<small') {
        @include standard-box();
    }

    @include media('<desktop') {
        flex-direction: column;
    }

    @include media('>=tablet') {
        margin-top: -$measure--medium7;
    }

    @include media('>=small') {
        grid-column: 2 / span 16;
    }

    .profile--global__contact-details {
        flex: 1.4;

        @include media('<desktop') {
            flex: 1;
        }
    }

    .profile--global__other-contact-methods {
        flex: 1;
        margin-left: $measure--small4;

        @include media('<desktop') {
            flex: 1;
            margin-left: 0;
        }

        &.columns-2 {
            flex: 2;
        }
    }
}
