// <Desktop-specific layout - secondary navigation never present
main > .landing--functional {
    @include media('<small') {
        .landing--functional__video {
            @include default-margin();
        }

        .landing__contact {
            @include default-padding();
        }
    }

    @include media('>=tablet', '<small') {
        .wrapper--landing--functional__content {
            @include standard-grid();

            margin-left: 0;
            margin-right: 0;

            .landing--functional__content,
            .wrapper--finder--mini,
            .landing--functional__finder__links__content {
                grid-column: 4 / span 18;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .landing--functional {
            .wrapper--landing--functional__content,
            .landing--functional__video,
            .landing__contact,
            .wrapper--landing--functional--pathway {
                @include standard-grid();
            }

            .landing--functional__content {
                grid-column: 6 / span 14;
            }

            .pathway {
                grid-column: 2/-2;
            }

            .wrapper--finder--mini,
            .landing--functional__finder__links__content {
                grid-column: 5 / span 16;
            }
        }
    }

    @include media('>=desktop') {
        > .landing--functional {
            .landing--functional__content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .landing--functional {
                .wrapper--landing--functional__content,
                .landing--functional__video {
                    @include content-grid();
                }

                .landing--functional__content {
                    grid-column: 4 / span 12;
                }

                .wrapper--finder--mini,
                .landing--functional__finder__links__content {
                    grid-column: 3 / span 14;
                }
            }
        }
    }
    @include media('>=tablet') {
        .pathway .pathway__cards {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

// Generic styles
.landing--functional {
    .landing--functional__content {
        padding-bottom: $measure--small2;
        @include media('<=tablet') {
            @include default-margin();
        }
        > h2:first-of-type {
            text-align: center;
        }
    }

    .wrapper--landing--functional--pathway {
        @include media('<=small') {
            @include default-margin;
        }

        .pathway__heading {
            padding: 0;
        }

        .pathway__cards {
            @include media('>tablet') {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-column-gap: $measure--small2;
            }
        }
    }

    .landing--functional__video {
        .embedded-video {
            @include media('>small') {
                grid-column: 2/-2;
            }
            .embedded-video-details {
                @include media('<=tablet') {
                    display: block;
                }
            }

            .embedded-video-details__heading {
                @include media('<=tablet') {
                    text-align: center;
                }
            }
        }

        .embedded-video-details__description {
            max-width: none;
        }
    }

    .wrapper--landing__contact {
        .landing__contact__heading {
            border-bottom-color: $color-red50;
        }

        .landing__contact {
            .related-content {
                .card--contact {
                    .card__details {
                        .card__heading {
                            margin-top: inherit;
                        }
                    }
                }

                li + li {
                    border-top-color: $color-pink70;
                }
            }
        }

        .landing__contact__location {
            .card--findus {
                a {
                    &:focus {
                        outline-color: $color-purewhite;
                    }
                }

                .card--findus__content__name {
                    @include anchor-inline(
                        $color-gray4,
                        $color-gray4,
                        $color-gray11,
                        $color-outline--dark
                    );
                }

                .card--findus__content__address {
                    color: $color-purewhite;

                    .icon {
                        color: $color-purewhite;
                    }
                }
            }
        }
    }
}

// Pattern override
.js .landing--functional .related-content:not(.slider) {
    li:only-child {
        grid-column: 1/-1;
    }
}
