// <Desktop-specific layout - secondary navigation never present
main > .lifelong-learning {
    // Wider 'location' template area for short courses (non-City Health) to allow for application deadline
    &.shortcourse {
        .key-information--lifelong-learning > ul > li {
            @include media('>=between') {
                grid-template-areas: 'heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading' 'date date date date date date location location location location location location location location button button button button' 'explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation';
            }
            @include media('>=tablet', '<between') {
                grid-template-areas: 'heading heading heading heading heading heading heading heading heading heading heading heading' 'date date date date location location location location location location button button' 'explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation explanation';
            }
        }
    }
    @include media('<small') {
        .key-information--lifelong-learning--bespoke {
            .key-information--lifelong-learning--bespoke__contact {
                @include standard-grid();
                @include standard-box();

                grid-template-rows: repeat(3, auto);
                justify-items: center;

                .key-information--lifelong-learning--bespoke__contact__header {
                    grid-column: 1 / span 24;
                    grid-row: 1;
                }

                .key-information--lifelong-learning--bespoke__contact__location {
                    grid-column: 1 / span 12;
                    grid-row: 2;
                }

                .key-information--lifelong-learning--bespoke__contact__details {
                    grid-column: 13 / span 12;
                    grid-row: 2;
                }

                .key-information--lifelong-learning--bespoke__contact__related-links {
                    grid-column: 1 / span 24;
                    grid-row: 3;
                }
            }
        }

        .key-information--lifelong-learning {
            @include standard-grid();
        }

        .lifelong-learning__tutors,
        .lifelong-learning__testimonials {
            @include standard-box();
        }

        .related-content--profiles {
            @include content-grid();

            .card {
                grid-column: 4 / -4;
            }
        }

        .lifelong-learning__video {
            @include default-margin();
        }

        .module-selector-wrapper {
            @include default-margin();
        }
    }

    @include media('<between') {
        .key-information--lifelong-learning--bespoke {
            .key-information--lifelong-learning--bespoke__contact {
                @include standard-grid();

                grid-template-rows: repeat(4, auto);
                justify-items: center;

                .key-information--lifelong-learning--bespoke__contact__header {
                    grid-column: 1 / span 24;
                    grid-row: 1;
                }

                .key-information--lifelong-learning--bespoke__contact__location {
                    grid-column: 1 / span 24;
                    grid-row: 2;
                }

                .key-information--lifelong-learning--bespoke__contact__details {
                    grid-column: 1 / span 24;
                    grid-row: 3;
                }

                .key-information--lifelong-learning--bespoke__contact__related-links {
                    grid-column: 1 / span 24;
                    grid-row: 4;
                }
            }
        }
    }

    @include media('<tablet') {
        .related-content--profiles {
            display: initial;
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);

        > .lifelong-learning {
            grid-column: 1 / span 24;

            .key-information--lifelong-learning--bespoke {
                .key-information--lifelong-learning--bespoke__contact {
                    @include standard-grid();
                    grid-template-rows: repeat(2, auto);

                    .key-information--lifelong-learning--bespoke__contact__header {
                        grid-column: 1 / span 24;
                        grid-row: 1;
                    }

                    .key-information--lifelong-learning--bespoke__contact__location {
                        grid-column: 1 / span 8;
                        grid-row: 2;
                    }

                    .key-information--lifelong-learning--bespoke__contact__details {
                        grid-column: 9 / span 8;
                        grid-row: 2;
                    }

                    .key-information--lifelong-learning--bespoke__contact__related-links {
                        grid-column: 17 / span 8;
                        grid-row: 2;
                    }
                }
            }

            .key-information--lifelong-learning {
                @include standard-grid();
            }

            .lifelong-learning__tutors,
            .lifelong-learning__testimonials {
                @include standard-grid();

                grid-template-rows: repeat(2, auto);

                h2 {
                    grid-column: 1 / span 24;
                }
            }

            .lifelong-learning__tutors {
                .related-content {
                    grid-column: 7 / span 12;
                }
            }

            .lifelong-learning__testimonials {
                .related-content {
                    grid-column: 1 / span 24;
                }

                .slider__controls {
                    grid-column: 1 / -1;
                }
            }

            header,
            .wrapper--lifelong-learning__introduction,
            .wrapper--lifelong-learning__announcement,
            .lifelong-learning__content,
            .wrapper--key-information--lifelong-learning,
            .lifelong-learning__tutors__wrap,
            .lifelong-learning__video,
            .lifelong-learning__contact,
            .related-content__heading,
            .lifelong-learning__testimonials,
            .related-content,
            .banner-text__content {
                grid-column: 1/-1;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .lifelong-learning {
                grid-column: 7 / span 18;
                @include content-grid();

                .module-selector-wrapper,
                .lifelong-learning__related-courses,
                .related-content {
                    grid-column: 2/-2;
                }

                .banner-text {
                    .banner-text__content {
                        @include default-padding();
                        @include default-margin();
                    }
                }

                .key-information--lifelong-learning--bespoke {
                    .key-information--lifelong-learning--bespoke__contact {
                        @include content-grid();
                        grid-template-rows: repeat(2, auto);

                        .key-information--lifelong-learning--bespoke__contact__header {
                            grid-column: 1 / span 18;
                            grid-row: 1;
                        }

                        .key-information--lifelong-learning--bespoke__contact__location {
                            grid-column: 1 / span 6;
                            grid-row: 2;
                        }

                        .key-information--lifelong-learning--bespoke__contact__details {
                            grid-column: 7 / span 6;
                            grid-row: 2;
                        }

                        .key-information--lifelong-learning--bespoke__contact__related-links {
                            grid-column: 13 / span 6;
                            grid-row: 2;
                        }
                    }
                }

                .key-information--lifelong-learning {
                    @include content-grid();
                }

                .lifelong-learning__tutors,
                .lifelong-learning__testimonials {
                    @include content-grid();

                    grid-template-rows: repeat(2, auto);

                    h2 {
                        grid-column: 1 / span 18;
                    }

                    .related-content {
                        grid-column: 4 / span 12;
                    }
                }

                .lifelong-learning__tutors {
                    .related-content {
                        grid-column: 4 / span 12;
                    }
                }

                .related-content--profiles {
                    @include content-grid();

                    .card {
                        grid-column: 4 / -4;
                    }
                }

                .lifelong-learning__testimonials {
                    .related-content {
                        grid-column: 1 / span 18;
                    }
                }
            }
        }
    }
}

// Generic styles
.lifelong-learning {
    padding-bottom: $measure--small2;
    @include banner($color-pink80);

    .related-content--profiles {
        .card {
            grid-column: 4 / -4;
            .card__details {
                justify-content: center;
            }
        }
    }

    .banner-text {
        color: $color-purewhite;
        padding-bottom: $measure--basic;
        padding-top: $measure--small4;
        @include media('print') {
            color: $color-nearblack;
        }
    }

    .wrapper--key-information--lifelong-learning {
        background-color: $color-gray1;
        border-bottom: $border-width-default $border-style-solid $color-gray3;
        padding-bottom: $measure--small4;
    }

    .wrapper--lifelong-learning__introduction,
    .wrapper--lifelong-learning__announcement {
        background-color: $color-pink80;
        color: $color-purewhite;

        @include media('print') {
            color: $color-nearblack;
        }
    }

    .wrapper--lifelong-learning__introduction {
        &:has(+ .wrapper--key-information--lifelong-learning) {
            padding-bottom: $measure--small4;
        }
    }

    .wrapper--lifelong-learning__announcement {
        padding-bottom: $measure--small4;
    }

    .wrapper--lifelong-learning__higher-right-course {
        padding: $measure--threequarters;
        background-color: $color-gray1;
        border-top: $border-width-default solid $color-gray3;

        .tabs__panel:not([hidden]) {
            background: $color-gray13;
            .tabs__panel__content {
                @include media('>tablet', '<small') {
                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 1 / -1;
                    }
                }
            }
        }

        .tabs__links {
            justify-content: left;
        }

        .tabs .tabs__panel {
            border: $border-width-default $border-style-solid $color-gray3;
        }

        .wrapper--key-information--lifelong-learning {
            background-color: initial;
            border-bottom: initial;
            h2,
            h3,
            .key-information--lifelong-learning__dates {
                grid-column: 1/-1;
            }
        }
    }

    .wrapper--key-information--lifelong-learning__register-interest {
        @include content-grid();
        margin-top: $measure--basic;

        @include media('<between') {
            @include standard-grid();
            display: none;
        }

        .key-information--lifelong-learning__register-interest {
            grid-column: 1/-1;
            @include media('>=small') {
                grid-column: 7/-2;
            }
            @include media('>=between', '<small') {
                grid-column: 9/-2;
            }
            @include media('>=tablet', '<between') {
                grid-column: 11/-2;
            }
            @include media('>=mobile', '<tablet') {
                grid-column: 2/-2;
            }
            @include media('>=mobile') {
                display: flex;
                justify-content: flex-end;
            }
            p {
                margin: auto $measure--basic;
                @include media('<mobile') {
                    text-align: center;
                    margin: $measure--basic 0 $measure--basic;
                }
            }
            .cta-block__cta {
                margin-top: 0;
                @include media('<mobile') {
                    margin: 0 $measure--basic;
                }
                a {
                    border: $border-width-default $border-style-solid
                        $color-pink90;
                    color: $color-pink90;
                    &:hover {
                        background: $color-pink90;
                        color: $color-purewhite;
                    }
                }
            }
        }

        // Controls visibility of Register interest button outside Key Information section's entry points
        &[data-onload-mobile-only='false'] {
            display: grid;
        }

        &[data-onload-mobile-only='true'] {
            display: none;
            @include media('<between') {
                display: grid;
            }
        }
    }

    .lifelong-learning__announcement__event {
        position: relative;

        .lifelong-learning__announcement__event--title {
            position: relative;
            top: $measure--small3;
            background-color: $color-nearblack;
            color: $color-purewhite;
            width: fit-content;
            padding: $measure--half $measure--half $measure--half
                $measure--basic;

            &:after {
                content: '';
                position: absolute;
                top: 0;
                right: -$measure--small4;
                width: $measure--small4;
                height: 100%;
                background: linear-gradient(
                    to top left,
                    #ffffff00 0%,
                    #ffffff00 50%,
                    $color-nearblack 50%,
                    $color-nearblack 100%
                );
            }

            h2 {
                @include h5style();
                margin-top: initial;
            }
        }
    }

    .lifelong-learning__introduction,
    .lifelong-learning__announcement {
        @include default-margin();
        @include default-padding();

        @include media('<tablet') {
            padding-left: 0;
            padding-right: 0;
        }
    }

    .lifelong-learning__introduction {
        .standfirst {
            margin-top: initial;
        }
    }

    .lifelong-learning__announcement {
        padding-bottom: $measure--basic;
    }

    .lifelong-learning__announcement__event--content {
        @include smalltype();
        width: fit-content;
        padding: $measure--medium2 $measure--basic $measure--basic
            $measure--basic;
        background-color: $color-purewhite;
        color: $color-nearblack;
        border-radius: $measure--half;

        p {
            @include smalltype();
            margin-top: initial;
        }
    }

    .lifelong-learning__selling-points__background {
        background: linear-gradient(
            180deg,
            $color-pink80 50%,
            $color-gray1 50%
        );
        background-repeat: no-repeat;
        border-bottom: $border-width-default $border-style-solid $color-gray3;
        padding-bottom: $measure--small3;
    }

    .lifelong-learning__selling-points {
        @include default-margin();
        @include default-padding();

        background-color: $color-purewhite;
        border: $border-style-solid $border-width-hairline $color-gray3;
        padding-bottom: $measure--small2;
        padding-top: $measure--small2;

        @include media('>=desktop') {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            padding-bottom: $measure--medium5;
            padding-top: $measure--medium5;
        }
    }

    .lifelong-learning__selling-points__card {
        display: flex;
        margin-bottom: $measure--small4;

        &:last-child {
            margin-bottom: 0;
        }

        @include media('>=desktop') {
            margin-bottom: 0;
            margin-right: $measure--small2;

            &:last-of-type {
                margin-right: 0;
            }
        }

        .icon {
            font-size: $measure--small5;
            min-width: $measure--medium3;
            margin-right: $measure--small4;

            @include media('>mobile') {
                min-width: $measure--medium2;
            }

            @include media('>between') {
                margin-right: $measure--basic;
            }
        }

        &:nth-child(1) {
            .icon {
                color: $color-green50;
            }
        }

        &:nth-child(2) {
            .icon {
                color: $color-teal50;
            }
        }

        &:nth-child(3) {
            .icon {
                color: $color-purple50;
            }
        }
    }

    .lifelong-learning__selling-points__title {
        font-weight: $font-weight-bold;
    }

    .key-information--lifelong-learning {
        grid-template-rows: repeat(2, auto);

        > h2,
        h3 {
            color: $color-pink80;
        }

        > h2 {
            grid-column: 2 / -2;
        }

        > h3 {
            grid-column: 1 / -1;
        }

        .key-information--lifelong-learning__icon {
            margin-right: $measure--basic;
        }

        > ul,
        > .pagination__wrapper {
            grid-column: 2 / -2;
            max-width: unset;
        }

        > .key-information--lifelong-learning__controls {
            grid-column: 2 / -2;
            justify-content: center;
        }
        > ul > li {
            background-color: $color-purewhite;
            border: $border-width-hairline $border-style-solid $color-gray3;
            padding: 0 $measure--basic $measure--basic $measure--basic;
        }
        > ul > li > ul {
            margin-left: 0;
            margin-right: 0;
        }
    }

    .lifelong-learning__start-date__cta {
        text-align: center;

        .cta-block__cta > span {
            @include smalltype();
            display: block;
        }
    }

    .related-content__heading {
        @include heading--centered($color-pink80);
    }

    .lifelong-learning__related-courses {
        padding-bottom: $measure--medium8;
        padding-top: $measure--medium8;
    }

    .lifelong-learning__tutors__wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .lifelong-learning__tutors,
    .lifelong-learning__testimonials {
        padding-bottom: $measure--medium8;
        padding-top: $measure--medium8;

        h2 {
            @include heading--centered($color-pink80);
        }

        .related-content {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    .slider:not([data-small='true']) {
        margin-bottom: 0;
    }

    .lifelong-learning__testimonials {
        .related-content {
            .card__details {
                justify-content: center;
            }
        }
    }

    .lifelong-learning__video {
        background: $color-red5;
        margin-bottom: $measure--basic;
        padding: 0 $measure--basic;
        @include media('>=tablet') {
            padding: 0 $measure--small4;
        }

        .embedded-video {
            padding: $measure--basic 0;
            @include media('>=tablet') {
                padding: $measure--small4 0;
            }
        }
    }

    @include media('>=tablet') {
        .tabs .tabs__panel {
            border: $border-width-default $border-style-solid $color-gray3;
        }
    }

    .lifelong-learning__content {
        margin-top: $measure--basic;

        & + .lifelong-learning__contact {
            margin-top: $measure--small4;
        }

        .overview__subject-wrapper {
            margin-top: $measure--basic;
            padding: $measure--basic;
            background-color: $color-pink90;
            color: $color-purewhite;

            h3 {
                margin-top: 0;
            }

            .cta-block {
                a {
                    @include cta-basic-light-bg($color-gray3, $color-text);

                    &:focus,
                    &:active {
                        @include anchor-default-outline($color-outline--dark);
                    }
                }
            }
        }
    }

    .lifelong-learning__contact {
        @include default-padding();

        background-color: $color-pink90;
        color: $color-purewhite;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding-bottom: $measure--small4;
        padding-top: $measure--small4;

        @include media('<small') {
            @include default-margin();
        }

        @include media('>=between') {
            flex-direction: row;
        }

        .lifelong-learning__contact__heading {
            margin-top: 0;
            text-align: center;

            @include media('>=between') {
                align-self: center;
                text-align: left;
            }
        }

        .cta-block {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            max-width: unset;

            @include media('<between') {
                @include smalltype();
            }

            @include media('>=desktop') {
                flex-direction: row;
            }

            li {
                align-self: stretch;
                margin-top: $measure--basic;

                @include media('<desktop') {
                    margin-right: 0;
                }

                @include media('>=desktop') {
                    margin-right: $measure--basic;
                    margin-top: 0;
                }

                a {
                    @include cta-basic-light-bg($color-gray3, $color-text);

                    align-self: stretch;
                    display: flex;
                    justify-content: center;

                    &:focus,
                    &:active {
                        @include anchor-default-outline($color-outline--dark);
                    }

                    &,
                    &:active,
                    &:focus,
                    &:hover {
                        span {
                            .fa-phone {
                                @include fa-icon-rotate(90deg, 1);
                            }
                        }
                    }
                }
            }
        }
    }

    .wrapper--text-highlight-block {
        background: $color-purewhite;
        margin-top: $measure--basic;
        border: $border-width-hairline $border-style-solid $color-gray2;
    }

    .text-highlight-block {
        @include default-padding();
        padding-top: $measure--basic;
        padding-bottom: $measure--small1;
        background-image: url('https://www.city.ac.uk/__data/assets/file/0006/698784/grey-stripes.svg');
        background-size: 10%;

        .text-highlight__preheading {
            @include text-highlight(
                $color-lifelong-learning--lead,
                $color-purewhite,
                $font-weight-normal
            );
        }

        .text-highlight__heading {
            margin: 0;
            span {
                @include text-highlight(
                    $color-lifelong-learning--accent,
                    $color-purewhite,
                    $font-weight-normal
                );
                margin-bottom: $measure--half;
                padding: $measure--quarter 0;
                font-weight: $font-weight-normal;
            }
        }
    }

    .text-highlight__body {
        @include default-padding();
        padding-bottom: $measure--small3;
    }

    .text-highlight__body__intro {
        font-weight: $font-weight-medium;
    }
}

@mixin display-tabs-content(
    $link-background: $color-purewhite,
    $panel-background: $color-gray1,
    $panel-border-color: $color-gray3
) {
    @include standard-box();

    background-color: $link-background;
    padding-top: $measure--small4;

    .tabs__links {
        display: none;
    }

    .tabs__panel {
        background-color: $panel-background;
        border-top: 0;
        padding: 0;

        @include media('>=tablet') {
            border: $border-width-default $border-style-solid
                $panel-border-color;
        }

        &[hidden] {
            display: block;
        }

        &:not([hidden]) {
            .tabs__panel__content {
                margin-left: 0;
                margin-right: 0;
            }
        }
    }

    .tabs__panel__heading {
        @include heading--centered($color-pink80);
    }
}

@include media('screen') {
    .js .lifelong-learning .modal__content,
    .js .lifelong-learning .modal__content a:not(:hover):not(:active) {
        color: $color-text;
    }

    .js .lifelong-learning .paginated-list .page ul,
    .js .lifelong-learning .paginated-list .page ol {
        list-style-type: none;
        margin-left: 0;
    }

    .js .lifelong-learning .modal__trigger {
        @include cta-basic-light-bg($color-red50, $color-purewhite);
        padding: $measure--threequarters $measure--small2;
    }

    .js .lifelong-learning__content .tabs__links {
        @include default-margin();
    }

    .js .lifelong-learning__content .accordion {
        @include default-margin();
    }

    .js .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
        @include display-tabs-content();
    }

    .js .lifelong-learning--bespoke .tabs {
        @include media('<tablet') {
            @include display-tabs-content();
        }
    }

    .js main {
        > .lifelong-learning {
            .lifelong-learning__content .tabs__panel:not([hidden]) {
                padding: 0;

                .tabs__panel__content {
                    @include standard-grid();

                    grid-template-rows: repeat(2, auto);

                    @include media('<=mobile') {
                        @include default-padding();
                    }

                    .tabs__panel__heading {
                        grid-row: 1;
                    }

                    .tabs__panel__body {
                        grid-row: 2;
                    }
                }
            }
        }

        > .navigation--secondary + .lifelong-learning {
            .lifelong-learning__content .tabs__panel:not([hidden]) {
                > .tabs__panel__content {
                    @include media('>=small') {
                        @include content-grid();
                    }
                }
            }
        }
    }
}

.js main {
    > .lifelong-learning {
        .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
            > .tabs__panel {
                @include standard-grid();

                grid-template-rows: repeat(2, auto);
                padding: 0;

                @include media('<=mobile') {
                    @include default-padding();
                }

                .tabs__panel__heading {
                    grid-row: 1;
                }

                .tabs__panel__body {
                    grid-row: 2;
                }

                .tabs__panel__heading,
                .tabs__panel__body {
                    grid-row: 2;
                    grid-column: 1 / span 24;

                    @include media('>mobile', '<=between') {
                        grid-column: 5 / span 16;
                    }

                    @include media('>between') {
                        grid-column: 7 / span 12;
                    }
                }
            }
        }
    }

    > .navigation--secondary + .lifelong-learning {
        .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
            > .tabs__panel {
                @include media('>=small') {
                    @include content-grid();

                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 4 / span 12;
                    }
                }
            }
        }
    }
}

.lifelong-learning {
    .slider-block {
        grid-column: 1 / -1;
    }

    .slide__controls__progress__active {
        color: $color-pink90;
    }

    .tabs__panel__content {
        @include media('>tablet', '<=between') {
            @include standard-grid();
            .tabs__panel__heading,
            .tabs__panel__body {
                grid-column: 5 / span 16;
            }
        }
        @include media('>between', '<=small') {
            @include standard-grid();
            .tabs__panel__heading,
            .tabs__panel__body {
                grid-column: 7 / span 12;
            }
        }
        @include media('>small') {
            @include content-grid();
            .tabs__panel__heading,
            .tabs__panel__body {
                grid-column: 4 / span 12;
            }
        }
    }
}

.lifelong-learning {
    .slide__controls__progress__active {
        color: $color-pink90;
    }

    .swiper-slider-arrow {
        @include control-button($color-pink90);
    }
}

// Pattern overrides
.js .lifelong-learning .slider + .slider__controls {
    @include media('<small') {
        margin-right: $measure--basic;
    }
}

.js .lifelong-learning .slider {
    padding-left: 0;
    padding-right: 0;
    @include media('<small') {
        @include default-margin();
        &[data-cardsperrow='3'][data-small='true'] {
            @include default-margin();
        }
    }
}

.js .lifelong-learning .related-content:not(.slider) {
    padding-bottom: $measure--small2;
    @include media('<small') {
        @include default-margin();
    }
    li:only-child {
        grid-column: 4/-4;
    }
}

.js
    .accordion
    > .accordion__heading[data-open='true']
    + .accordion__body
    p.lifelong-learning__content__contributory-credits__short {
    margin: 0;
}

.no-js
    .lifelong-learning
    .wrapper--key-information--lifelong-learning__register-interest {
    @include media('>=between') {
        margin-top: $measure--basic;
    }
    @include media('>=between', '<small') {
        @include x-column-grid(24);
    }
    &[data-onload-mobile-only='true'] {
        @include media('>=tablet') {
            display: grid;
        }
    }
}
