.user-journey {
    .content-wrapper {
        @include standard-grid();
        grid-template-rows: auto;

        @include media('<=mobile') {
            grid-template-areas: 'main main main main main main';
        }

        @include media('>mobile') {
            grid-template-areas: '. . main main main main main main main main . .';
        }

        @include media('>between') {
            grid-template-areas: '. . . . . main main main main main main main main . . . . .';
        }

        > .content {
            @include standard-box();
            grid-area: main;
        }
    }
}

.user-journey__steps {
    li:not(:last-of-type) {
        &:after {
            @include font-family('sharp');
            @include fa-icon-rotate(90deg, 1);
            content: '\f54b';
            display: block;
            font-size: $font-size--icon;
            font-weight: $font-weight-light;
            margin-top: $measure--basic;
            text-align: center;
        }
    }

    li:last-of-type {
        &:after {
            @include font-family('sharp');
            font-size: $font-size--icon;
            content: '\f11e';
            display: block;
            font-weight: $font-weight-light;
            margin-top: $measure--basic;
            text-align: center;
        }
    }

    h3 {
        @include body-baseline();
        @include body-font-features();
        font-weight: $font-weight-medium;
    }
}
