.persona {
    @include standard-box();
    @include standard-grid();

    @include media('<=mobile') {
        grid-template-rows: repeat(9, auto);
        grid-template-areas:
            'thumbnail thumbnail thumbnail thumbnail thumbnail thumbnail'
            'details details details details details details'
            'motivation motivation motivation motivation motivation motivation'
            'needs needs needs needs needs needs'
            'challenges challenges challenges challenges challenges challenges'
            'education education education education education education'
            'devices devices devices devices devices devices';
    }

    @include media('>mobile') {
        grid-template-rows: repeat(8, auto);
        grid-template-areas:
            'thumbnail thumbnail thumbnail thumbnail details details details details details details details details'
            'motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation'
            'needs needs needs needs needs needs challenges challenges challenges challenges challenges challenges'
            'devices devices devices devices devices devices education education education education education education';
    }

    @include media('>between') {
        grid-template-rows: repeat(6, auto);
        grid-template-areas:
            'thumbnail thumbnail thumbnail thumbnail thumbnail thumbnail details details details details details details details details details details details details'
            'motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation motivation'
            'needs needs needs needs needs needs needs needs needs challenges challenges challenges challenges challenges challenges challenges challenges challenges'
            'devices devices devices devices devices devices devices devices devices education education education education education education education education education';
    }
}

.persona__thumbnail,
.persona__needs,
.persona__devices {
    @include media('>mobile') {
        margin-right: 0;
    }
}

.persona__personal-details,
.persona__challenges,
.persona__education {
    @include media('>mobile') {
        margin-left: 0;
    }
}

.persona__thumbnail {
    grid-area: thumbnail;
    text-align: center;
    img {
        width: $measure--large2;
    }
}

.persona__personal-details {
    grid-area: details;

    ul {
        list-style-type: none;
        margin-left: 0;

        li > span {
            font-weight: $font-weight-medium;

            &:after {
                content: ':';
            }
        }
    }
}

.persona__motivation {
    grid-area: motivation;
}

.persona__needs {
    grid-area: needs;
}

.persona__challenges {
    grid-area: challenges;
}

.persona__education {
    grid-area: education;
}

.persona__devices {
    grid-area: devices;
}
