////
/// Cookiebot style override
/// @group patterns
////

#CybotCookiebotDialog {
    #CybotCookiebotDialogBodyLevelButtonLevelOptinAllowallSelectionWrapper {
        .CybotCookiebotDialogBodyButton {
            padding-top: $measure--half;
            padding-bottom: $measure--half;
        }
    }

    #CybotCookiebotDialogBodyLevelButtonLevelOptinDeclineAll,
    #CybotCookiebotDialogBodyLevelButtonLevelOptinAllowallSelection {
        border: 1px solid transparent;
        color: $color-red50;
        background-color: transparent;
        transition: border $transition-duration--medium;

        &:hover {
            @include media('hover') {
                border: 1px solid $color-red50;
            }
        }

        &:focus,
        &:active {
            border: 1px solid $color-red50;
        }
    }

    #CybotCookiebotDialogBodyLevelButtonLevelOptinAllowAll {
        background-color: $color-red50;
        transition: background-color $transition-duration--medium;
        border: 2px dotted transparent;

        &:hover {
            @include media('hover') {
                background-color: darken($color-red50, 10%);
                border: 2px dotted transparent;
            }
        }

        &:focus,
        &:active {
            border: 2px dotted $color-purewhite;
        }
    }
}
