// <Desktop-specific layout - secondary navigation never present
main > .search {
    @include media('<small') {
        .wrapper--search {
            @include default-margin();
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .search {
            grid-column: 1 / span 24;

            .wrapper--search,
            .search-form {
                @include standard-grid();
                grid-template-rows: repeat(3, auto);
            }

            .tabs,
            .wrapper--search__results,
            .wrapper--search__bestbets {
                grid-column: 1 / span 16;
            }

            .wrapper--search__othersites {
                grid-column: 17 / span 8;
                grid-row: 2 / span 2;
                margin-top: $measure--medium3;
            }

            .search-form {
                grid-column: span 24;

                .search-form__query {
                    grid-column: 4 / span 18;
                }
            }
        }
    }
}

.search {
    .content {
        padding: 0;
    }

    .banner--search {
        background-color: $color-gray10;
        color: $color-purewhite;
        display: block;
        height: $measure--huge5;
        text-align: center;

        h1 {
            margin-top: $measure--medium1;
        }
    }

    .wrapper--search {
        margin-top: -#{$measure--medium1};
        padding-bottom: $measure--small4;
        position: relative;
    }

    .search-form {
        @include media('>=small') {
            @include standard-box();
        }
    }

    .search-form__query {
        @include box-shadow();

        background-color: $color-purewhite;
        width: 100%;

        @include media('>=tablet') {
            height: $measure--medium11;
        }

        .wrapper--search-form__query__fields {
            align-items: center;
            display: flex;
            flex-wrap: nowrap;
            height: 100%;
            padding: $measure--basic;

            @include media('<tablet') {
                align-items: stretch;
                flex-direction: column;
            }

            @include media('>=tablet') {
                flex-direction: row;
            }

            .wrapper--search-form__query__fields__input {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                width: 100%;
            }
        }
    }

    .search__query {
        background-color: $color-red50;
        padding: $measure--small2;
        position: relative;

        & > div {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            height: 100%;
        }

        @include media('>=tablet') {
            margin-left: $measure--small2;
            margin-right: $measure--small2;
        }
    }

    .search__query__input {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        @include media('>=tablet') {
            @include gammatype();
        }

        background-color: $color-purewhite;
        display: flex;
        flex-grow: 1;
        font-weight: $font-weight-text;
        padding: 0;
        position: relative;

        &:focus-within {
            @include outline-properties($color-nearblack);
        }

        input {
            @include font-family('default');
            @include body-font-features();
            @include gammatype();

            border-bottom: $border-width-hairline solid $color-gray3;
            border-right: $border-width-hairline solid $color-gray3;
            border-top: $border-width-hairline solid $color-gray3;
            border-left: 0;
            font-style: normal;
            height: 100%;
            min-width: 0; // Constrains width in Firefox
            padding-bottom: $measure--half;
            padding-left: $measure--half;
            padding-right: $measure--medium5;
            padding-top: $measure--half;
            width: 100%;

            @include media('<tablet') {
                border-left: $border-width-hairline solid $color-gray3;
            }

            &:focus {
                color: $color-gray10;
                font-style: normal;
                font-weight: $font-weight-medium;
                outline: 0;
            }

            &::placeholder {
                font-style: italic;
            }

            &:placeholder-shown {
                color: $color-gray7;
            }
        }

        .search__clear {
            @include epsilontype();

            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            height: $measure--small5;
            justify-content: center;
            position: absolute;
            right: $measure--half;
            top: $measure--quarter;
            width: $measure--small5;

            &:focus {
                @include outline-properties();

                outline-offset: -#{$border-width-thicker};
            }

            .search__clear__text {
                @include sr-only();
            }

            .icon {
                /*
                // temp overide with xmark-temp-styling below

                --fa-primary-color: #{$color-gray7};
                --fa-primary-opacity: 1;
                --fa-secondary-color: #{$color-gray1};
                --fa-secondary-opacity: 1;
                */
                @include xmark-temp-styling;

                letter-spacing: 0;
                line-height: 1;
            }
        }
    }

    .search__query__icon--wrapper {
        background-color: $color-purewhite;
        border-bottom: $border-width-hairline solid $color-gray3;
        border-left: $border-width-hairline solid $color-gray3;
        border-top: $border-width-hairline solid $color-gray3;
        border-right: 0;
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--basic;
        padding-top: 0.375rem; // FIXME

        @include media('<tablet') {
            display: none;
        }
    }

    .search__query__submit {
        @include gammatype();
        @include cta--basic(
            $color-purewhite,
            $color-red50,
            $color-purewhite,
            $color-red70,
            $color-nearblack,
            round
        );

        flex-shrink: 0;
        font-weight: $font-weight-book;
        margin-left: $measure--half;

        &:hover {
            cursor: pointer;
        }

        @include media('>=tablet') {
            margin-left: $measure--basic;
        }

        @include media('<tablet') {
            padding: $measure--basic $measure--small1;

            &:hover {
                padding: $measure--basic $measure--small1;
            }
        }

        span {
            margin: auto;
        }

        .search__query__submit__text {
            @include media('<tablet') {
                @include screenreader-only();
            }
        }

        .search__query__submit__icon {
            @include media('<tablet') {
                display: block;
                padding: 0;
            }

            @include media('>=tablet') {
                display: none;
            }
        }
    }

    .tabs__links {
        li button > span .icon {
            border: unset;
            font-size: $measure--basic;
            line-height: unset;
        }
    }
}
