    background: linear-gradient(to bottom, #e5e5e5, #ffffff 75%);
}

.banner-svg--wrapper {
    grid-template-columns: repeat(24, 1fr);
    grid-column-gap: 0;
    display: grid;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

@media (min-width: 768px) {
    .banner-svg--wrapper {
        grid-column-gap: 1rem;
    }
}

@media (min-width: 901px) {
    .banner-svg--wrapper {
        grid-column-gap: 1.5rem;
    }
}

@media (min-width: 901px) {
    .banner-svg--wrapper {
        padding-left: 2rem;
        padding-right: 2rem;
    }
}

.banner-svg--wrapper__svg {
    grid-column: 1/-1;
    position: relative;
}

.banner-svg--wrapper__svg .hero-image {
    height: 14rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

.banner-svg--wrapper__svg .hero-image img {
    width: 17.5rem;
}

.banner-svg--wrapper__title {
    grid-column: 1/-1;
    position: relative;
    padding-top: 4rem;
    padding-bottom: 2rem;
    text-align: center;
}

@media (max-width: 768px) {
    .banner-svg--wrapper__title {
        padding-bottom: 2rem;
    }
}

.banner-svg--wrapper__title h1,
.banner-svg--wrapper__title p {
    max-width: none;
}

.banner-svg--wrapper__title:after {
    background-color: #d61726;
    content: '';
    display: block;
    height: 0.25rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1.5rem;
    width: 3rem;
}

@media (max-width: 900px) {
    .banner-svg--wrapper__title,
    .banner-svg--wrapper__svg {
        grid-column: 1/-1;
    }
}

