@mixin cta--basic(
    $color: $color-purewhite,
    $background: $color-red50,
    $hover-color: $color-purewhite,
    $hover-background: $color-red70,
    $outline: $color-purewhite,
    $corners: square
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);

    display: inline-block;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;
    transition: background-color $transition-duration--long;

    @if $corners == round {
        border-radius: $measure--quarter;
    }

    &,
    &:focus,
    &:active {
        background-color: $background;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-background;
            color: $hover-color;
        }
    }
}

@mixin cta--basic--subelement(
    $color: $color-purewhite,
    $background: $color-red50,
    $hover-color: $color-purewhite,
    $hover-background: $color-red70,
    $outline: $color-purewhite,
    $corners: square,
    $class: 'text'
) {
    @include anchor-reset();

    display: inline-block;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;
    transition: background-color $transition-duration--long;

    @if $corners == round {
        border-radius: $measure--quarter;
    }

    &,
    &:focus,
    &:active {
        background-color: $background;
        color: $color;
    }

    &:focus {
        .#{$class} {
            @include outline-properties($outline);
        }
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-background;
            color: $hover-color;
        }
    }
}
