@mixin tabs-links-style() {
    .tabs__links {
        align-items: stretch;
        bottom: -0.4rem;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        list-style-type: none;
        margin-bottom: 0;
        margin-left: 0;
        margin-top: $measure--basic;
        max-width: unset;
        overflow-x: hidden;
        position: relative;
    }

    .tabs__links li {
        margin-bottom: 0;
        margin-top: 0;
        max-width: unset;
        text-align: center;
        margin-right: $measure--threequarters;
        display: flex;
        align-items: flex-end;
    }

    .tabs__links li:last-of-type {
        margin-right: 0;
    }

    .tabs__links li button {
        @include anchor-span();
        @include anchor-reset();
        background-image: none;
        border-bottom-color: transparent;
        border-bottom-style: $border-style-dotted;
        border-bottom-width: $border-width-default;
        border-color: transparent;
        border-style: $border-style-dotted;
        border-width: $border-width-default;
        color: $color-gray7;
        display: block;
        overflow: hidden;
        padding: 0;
        position: relative;
        transition: none;
        width: 100%;

        > span {
            @include h6style();
            align-items: flex-end;
            border-bottom-color: transparent;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $measure--half;
            display: flex;
            height: 100%;
            line-height: $line-height-ratio--body;
            margin-top: 0;
            padding-bottom: $measure--half;
            padding-left: $measure--half;
            padding-right: $measure--half;
            padding-top: $measure--half;

            @include media('>between') {
                padding-left: $measure--threequarters;
                padding-right: $measure--threequarters;
            }
        }

        > span::before {
            @include ghost-out();
        }

        &[aria-selected='false'] {
            &:hover > span::before {
                @include media('hover') {
                    @include ghost-in();
                }
            }
        }

        &[aria-selected='true'] {
            font-weight: $font-weight-bold;

            > span {
                border-bottom-color: $color-red50;
                color: $color-gray11;
            }
        }

        &:hover {
            @include media('hover') {
                background: none;
                border-bottom-color: transparent;
                border-bottom-style: $border-style-dotted;
                border-bottom-width: $border-width-default;
                border-color: transparent;
                border-style: $border-style-dotted;
                border-width: $border-width-default;
            }
        }

        &:focus {
            background: $color-gray1;
            border-color: $color-gray11;
            border-style: $border-style-dotted;
            border-width: $border-width-default;
            color: $color-gray7;
            padding: 0;

            span {
                outline: none;
            }
        }

        &:active {
            background: $color-gray1;
            border-color: $color-gray11;
            border-style: $border-style-dotted;
            border-width: $border-width-default;
            color: $color-gray11;

            span {
                outline-width: 0;
            }
        }
    }
}

.tabs {
    .tabs__links li {
        &:only-child {
            display: none;
        }
    }
}

@include media('print') {
    .js .tabs > .tabs__links,
    .js .tabs--accordion > .accordion {
        display: none;
    }

    .js .tabs .tabs__panel[hidden] {
        display: block;
    }
}

@include media('screen') {
    .js .tabs {
        @include tabs-links-style();
    }

    .js .tabs .tabs__panel {
        padding: $measure--threequarters $measure--half $measure--half
            $measure--half;
        background: $color-gray1;
        border-top: $border-width-default $border-style-solid $color-gray3;

        @include media('>=tablet') {
            border: $border-width-default $border-style-solid $color-gray3;
        }

        &:focus {
            @include outline-properties($color-nearblack);

            outline-offset: 0;
        }
    }

    .js .tabs .tabs__panel__content {
        padding: $measure--small4 0;
        @include media('<tablet') {
            padding-bottom: $measure--basic;
        }
    }

    .js .tabs .tabs__panel__heading {
        margin-top: 0;
    }
}

@include media('screen', '<=tablet') {
    .js .tabs--accordion > .accordion {
        display: block;
    }

    .js .tabs--accordion > .accordion .accordion__heading {
        border-bottom-color: $color-gray1;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        outline-color: transparent;
        outline-style: $border-style-dotted;
        outline-width: $border-width-default;

        &:active {
            border-bottom-color: $color-gray1;
            text-decoration: underline;
        }

        &[data-open='true'] button {
            .accordion__heading__text {
                background: $color-pink90;
                color: $color-purewhite;
                text-decoration: underline;
            }
            .accordion__heading__indicator {
                padding: $measure--basic 0.89rem;
            }
        }

        &[data-open='false'] {
            &:focus button {
                outline-color: $color-gray11;
            }
            button {
                .accordion__heading__text {
                    background: transparent;
                    color: $color-gray10;
                }
                .accordion__heading__indicator {
                    background: $color-pink90;
                    color: $color-purewhite;
                }
            }
        }
    }

    .js .tabs--accordion > .accordion .accordion__heading button {
        padding: 0;
        display: block;

        &:hover {
            @include media('hover') {
                background: $color-purewhite;
                color: $color-gray10;
            }
        }
    }

    .js
        .tabs--accordion
        > .accordion
        .accordion__heading
        button
        .accordion__heading__text {
        padding: $measure--basic;
    }

    .js
        .tabs--accordion
        > .accordion
        .accordion__heading
        button
        .accordion__heading__indicator {
        align-self: normal;
        padding: $measure--basic;
        text-align: center;
        text-decoration: underline;
        background: $color-pink90;
        color: $color-purewhite;
        &:before {
            vertical-align: sub;
        }
    }

    .js .tabs--accordion > .accordion .accordion {
        .accordion__heading {
            button {
                .accordion__heading__text,
                .accordion__heading__indicator {
                    background: transparent;
                    color: $color-linkblue;
                }
            }
            &[data-open='false'] {
                border-bottom-color: $color-gray2;
                border-bottom-style: $border-style-solid;
                border-bottom-width: $border-width-hairline;
            }
        }
    }

    // Nested tab inside tab that switches to accordion
    .js .tabs--accordion .tabs--accordion > .accordion .accordion__heading {
        &[data-open='true'] {
            button {
                .accordion__heading__indicator,
                .accordion__heading__text {
                    background: $color-pink90;
                    color: $color-purewhite;
                }
            }
        }
        &[data-open='false'] {
            button {
                .accordion__heading__indicator {
                    background: $color-pink90;
                    color: $color-purewhite;
                }
                .accordion__heading__text {
                    color: $color-gray10;
                }
            }
        }
    }

    .js .tabs--accordion > .tabs {
        display: none;
    }
}

@include media('screen', '>tablet') {
    .js .tabs--accordion > .accordion {
        display: none;
    }

    .js .tabs--accordion > .tabs {
        display: block;
    }

    .js .tabs--accordion > .tabs .accordion--nested {
        @include media('>tablet') {
            display: block;
        }
    }
}

// tabs variation with grey underline
.js .tabs--grey-underline {
    .tabs__links li button > span {
        border-bottom-color: $color-gray1;
    }
    .tabs__links li button[aria-selected='true'] > span {
        border-bottom-color: $color-pink80;
    }

    .tabs__panel__body .tabs {
        // nested tabs
        .tabs__links {
            justify-content: left;

            li button > span {
                border-bottom-color: $color-gray3;
                &:before {
                    background-color: $color-gray5;
                }
            }

            li button[aria-selected='true'] > span {
                border-bottom-color: $color-red50;
            }
        }

        .tabs__panel {
            background: $color-gray1;
        }
    }
}

// Desktop specific layout if secondary navigation is present for standard pages
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .standard-content {
                @include media('>=small', '<desktop') {
                    .tabs .tabs__links li {
                        margin-right: $measure--half;
                        button > span {
                            @include extrasmalltype();
                            padding-left: $measure--half;
                            padding-right: $measure--half;
                        }
                    }
                }

                @include media('>=desktop', '<large') {
                    .tabs .tabs__links li button > span {
                        @include smalltype();
                    }
                }
            }
        }
    }
}
