/*
    19/9/2019
    General Usage:

     <h3>
        <a href="#" class="linked-heading">
            <span class="linked-heading__text">Linked heading</span>
            <span class="linked-heading__icons">
                <span class="fas fa-chevron-right"></span>
                <span class="fas fa-long-arrow-right"></span>
            </span>
        </a>
    </h3>
*/

.linked-heading {
    @include anchor-reset();
    @include anchor-default-outline($color-outline);
    position: relative;
    padding-right: $measure--medium1;
    display: inline-block;
    transition: none;
    background-image: none;
    background-size: 0;

    &:hover {
        transition: none;
        background-image: none;
        background-size: 0;

        .linked-heading__text {
            &:before {
                opacity: 1;
                width: 100%;
            }
        }

        .linked-heading__icons {
            span:nth-child(1) {
                display: none;
                visibility: hidden;
                opacity: 0;
                animation: slide-right-out 600ms;
            }

            span:nth-child(2) {
                display: inherit;
                visibility: visible;
                opacity: 1;
                animation: slide-left-in 600ms;
            }
        }
    }

    &:active {
        .linked-heading__text {
            &:before {
                opacity: 1;
                width: 100%;
            }
        }
    }

    &:focus,
    &:active {
        outline-color: $color-gray11;
    }

    &:visited {
        color: $color-gray11;
    }
}

.linked-heading__icons {
    background: $color-red50;
    color: $color-purewhite;
    position: absolute;
    right: 0;
    top: 0;
    background: $color-red50;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
        position: absolute;
        font-weight: $font-weight-normal;

        &:nth-child(1) {
            font-size: $font-size--mobile;
        }

        &:nth-child(2) {
            font-size: $font-size--1080p;
            font-weight: $font-weight-light;
            display: none;
            opacity: 0;
            visibility: hidden;
            animation: slide-right-out 600ms;
        }
    }
}

h2 {
    .linked-heading__icons {
        height: $measure--small4;
        width: $measure--small4;
    }
}

h3 {
    .linked-heading__icons {
        height: 1.8rem;
        width: 1.8rem;
    }
}

.linked-heading__text {
    @include anchor-discreet-before($color-red50);

    border-bottom-color: $color-text;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;
    color: $color-gray11;
    position: relative;
}
