.highlighted-content__border {
    border-left: $measure--half $border-style-solid $color-red50;
    margin-bottom: $measure--basic;
    margin-top: $measure--basic;
    padding-bottom: $measure--basic;
    padding-left: $measure--medium1;
    padding-top: $measure--basic;
}

.highlighted-content__info {
    margin-bottom: $measure--basic;
    margin-top: $measure--basic;
    padding-bottom: $measure--basic;
    padding-left: $measure--medium3;
    padding-top: $measure--basic;
    position: relative;

    &:before {
        @include smalltype();
        @include font-family('sharp');

        color: $color-gray6;
        content: '\f05a';
        font-size: $font-size--icon;
        left: $measure--quarter;
        margin-top: $measure--quarter;
        position: absolute;
    }
}

.highlighted-content__info,
.highlighted-content__border {
    p {
        margin: 0;
        max-width: calc(#{$max-line-length} - #{$measure--medium3});
    }

    p:nth-child(n + 2) {
        display: none;
    }
}
