@import './dropdown-entry-requirement-page';

.dropdown-filter {
    margin-top: $measure--basic;
    ul {
        &.data-group {
            // Filter content is grouped into list items, so when user selects option from
            // dropdown filter, disable list item type and left margin.
            margin-left: 0;
            li {
                list-style-type: none;
                // Nested list items should default back to standard bullet point styles
                ul {
                    margin-left: $measure--small1;

                    li {
                        list-style-type: disc;
                    }
                }
            }
        }

        li[data-hidden='true'] {
            display: none;
        }
    }

    select {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();
        @include dropdown-filter-select(
            $color-gray7,
            $color-nearblack,
            $color-purewhite
        );

        bottom: $measure--quarter;
        font-weight: $font-weight-medium;
        height: $measure--medium4;
        position: relative;
        width: 100%;
        margin-top: $measure--basic;
        border-color: $color-gray7;

        @include media('<between') {
            -webkit-appearance: menulist;
        }

        &:focus {
            @include outline-properties();
        }
    }

    ul:first-of-type {
        li {
            list-style-type: none;
        }
    }
}
