.wrapper--feedback {
    @include media('>=small') {
        @include standard-box();
    }
}

.feedback {
    @include media('print') {
        display: none;
    }

    @include box-shadow();
    margin: $measure--small2 0;
    @include alphatype();

    background-color: $color-purewhite;
    display: block;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;
    grid-column: 1/-1;

    @include media('<small') {
        @include default-margin();
    }

    @include media('>=between') {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

    @include media('>=large') {
        @include standard-box();
        width: 100%;
    }

    .feedback__form-link,
    .feedback__direct {
        @include content-style();
        margin-top: 0;
        max-width: unset;
    }

    .feedback__direct {
        legend {
            float: left; // unavoidable float because legend sucks
            margin-bottom: $measure--basic;
            margin-right: $measure--half;
            margin-top: $measure--basic;
        }

        p {
            margin-bottom: $measure--basic;
            margin-top: $measure--basic;
        }

        .feedback__direct__buttons {
            display: inline-flex;
            flex-direction: row;
            flex-wrap: nowrap;
        }

        button {
            @include cta-basic-light-bg($color-purewhite, $color-nearblack);
            box-shadow: none;
            margin-bottom: $measure--half;
            margin-top: $measure--half;
            border-color: $color-gray3;

            &:active,
            &:focus {
                border-color: $color-gray3;
            }

            @include media('<=mobile') {
                display: inline-block;
            }

            & + button {
                margin-left: $measure--basic;
            }
        }
    }

    .feedback__form-link {
        margin-bottom: $measure--basic;
        margin-top: $measure--basic;

        a {
            @include anchor-inline();
        }
    }
}
