// <Desktop-specific layout - secondary navigation never present
main > article {
    @include media('<small') {
        .banner {
            @include x-column-grid(24);
            display: grid;

            .banner__image {
                &,
                & + .banner__overlay {
                    grid-column: 1 / span 24;
                }
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > article {
            .banner {
                @include x-column-grid(24);
                display: grid;

                .banner__image {
                    &,
                    & + .banner__overlay {
                        grid-column: 1 / span 24;
                    }
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + article {
                .banner {
                    @include content-grid();

                    .banner__image {
                        &,
                        & + .banner__overlay {
                            grid-column: 1 / span 18;
                        }
                    }
                }
            }
        }
    }
}

// Generic styles
.banner {
    position: relative;
    overflow: hidden;
    grid-template-rows: auto;
    grid-column: 1/-1;
}

.banner__image {
    height: 100%;
    // always on first row
    grid-row: 1;

    @include media('<=tablet') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('>tablet') {
        max-height: $banner-tablet-image-max-height;
    }

    @include media('>=desktop') {
        max-height: $banner-desktop-image-max-height;
    }

    @include media('>=4K') {
        max-height: $banner-4k-image-max-height;
    }

    @include media('>=8K') {
        max-height: $banner-8k-image-max-height;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

.banner__overlay {
    height: 100%;
    grid-row: 1;
    position: relative;
    width: 100%;
    padding: $measure--basic 0;

    @include media('<tablet') {
        // overlay should drop to row two
        // at >tablet
        grid-row: 2;
    }

    // If grid layout is required, an extra wrapping element is needed with flex properties.
    // This allows you to then have a grid nested inside.
    &.flex-wrapper--banner__overlay {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        .banner__overlay__heading--grid {
            @include content-grid();

            @include media('<=small') {
                @include default-padding();
            }
        }
    }
}

.banner__overlay__heading {
    @include default-margin();
    @include default-padding();
    padding-bottom: $measure--basic;
    color: $color-purewhite;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: inherit; // Ensures h1 is centered vertically and no adverse background colour fill on Safari

    h1 {
        @include text-shadow();
    }
}

.banner--center {
    .banner__overlay {
        .banner__overlay__heading {
            text-align: center;
        }
    }

    h1,
    p {
        max-width: unset;
    }
}
