// ////
/// Icons
/// @group base
////

@mixin zoom-in-icon {
    display: block;
    width: $measure--small5;
    height: $measure--small5;
    background-color: $color-nearblack;
    color: $color-purewhite;
    border-radius: 50%;
    border-style: none;
    transition: background-color $transition-duration--long,
        transform $transition-duration--long;

    span {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &:before {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
    }

    &:focus {
        background: $color-nearblack;
        color: $color-purewhite;
    }

    &:focus,
    &:active {
        @include anchor-default-outline($color-outline);
    }

    &:hover,
    &:active {
        @include media('hover') {
            transform: scale(1.2);
            background-color: $color-red50;
            color: $color-purewhite;
            background-image: none;
        }
    }

    &:visited {
        color: $color-purewhite;
    }

    &:active,
    &:hover,
    &:focus,
    &:visited {
        border-bottom-style: none;
    }
}
