.wrapper--tooltip__label {
    @include smalltype();

    background-color: $color-gray10;
    display: flex;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('above');

    &[data-open='false'] {
        .tooltip__label {
            display: none;
        }
    }

    .tooltip__label {
        color: $color-purewhite;
        padding: $measure--half;

        &:focus {
            @include outline-properties($color-purewhite);
            outline-offset: -#{$border-width-default};
        }

        a {
            @include anchor-discreet--notransition(
                $color-purewhite,
                $color-gray3,
                $color-purewhite,
                $color-purewhite,
                $color-purewhite
            );
        }

        .tooltip__label__licence {
            display: table;
        }
    }

    .tooltip--image-credit {
        @include smalltype();
        background-color: $color-gray10;
        border: 0;
        color: $color-purewhite;
        font-weight: $font-weight-normal;
        margin: 0;
        padding: $measure--half;
        width: auto;
        pointer-events: auto;
        &:hover {
            @include media('hover') {
                background-color: $color-gray8;
            }
        }

        &:active,
        &:focus {
            outline: 0;

            .icon {
                outline: $border-width-default $border-style-dotted
                    $color-purewhite;
            }
        }

        .icon {
            padding: $measure--half;
        }
    }
}
