.carousel-gallery {
    overflow: hidden;
    position: relative;

    h2,
    h3,
    h4 {
        margin-top: 0;
        padding-bottom: $measure--half;
        color: $color-purewhite;
    }

    .swiper-slide {
        margin-right: 0;
        margin-top: 0;
    }

    .swiper-slide__text {
        @include smalltype;
        display: block;
        top: 0;
        background-color: initial;
        width: 100%;
        margin-right: unset;
        margin-left: unset;
        padding: $measure--basic 0;

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-nearblack,
                $color-outline--dark
            );
        }
    }

    .swiper-buttons-wrap {
        bottom: $measure--half;
        right: $measure--half;

        @include media('<between') {
            position: initial;
            padding: $measure--half;
            justify-content: flex-end;
        }

        .swiper-slider-arrow {
            @include control-button($color-purewhite);

            &:focus {
                background-color: transparent;
            }
            &:hover,
            &:active {
                color: $color-pink90;
                background-color: $color-purewhite;
            }

            &:hover,
            &:focus:focus,
            &:active:active {
                outline-color: $color-purewhite;
            }
        }
    }

    .swiper-slide__image-wrapper {
        height: 22rem;
        background-color: $color-gray10;

        @include media('<desktop') {
            height: 16rem;
        }

        @include media('<tablet') {
            height: $measure--huge5;
        }

        picture {
            display: flex;
        }

        & img {
            object-fit: contain;
        }
    }
}

.galleria-thumbnails-wrap {
    width: calc(100% - 7rem);
    overflow: hidden;

    @include media('<between') {
        width: calc(100%);
    }
}

.no-js {
    .galleria-thumbnails-wrap {
        display: none;
    }
}

.js {
    .carousel-gallery-default {
        padding: $measure--basic;
        background-color: $color-gray8;
        margin-top: $measure--basic;
        display: grid; // FF fix
    }

    .swiper-wrapper {
        align-items: initial;
    }

    .galleria-thumbnails-list {
        --thumbnails: 1;
        display: flex;
        list-style: none;
        margin: 0;
        width: calc(var(--thumbnails) * 25%);
        max-width: none;
        align-items: baseline;
        transition: transform 0.5s;

        @include media('<between') {
            width: calc(var(--thumbnails) * 33%);
        }

        .galleria-thumbnail {
            max-width: calc(100% / var(--thumbnails));
            margin-top: 0;
            margin-right: $measure--half;

            &:first-child {
                margin-left: $border-width-default;
            }

            & input {
                opacity: 0.7;
                max-width: 100%;
                height: auto;
                border: 0;
                max-height: $measure--medium3;
                margin-top: $measure--quarter;
                transition: opacity 0.6s;

                @include anchor-default-outline($color-outline--dark);

                &:focus {
                    opacity: 1;
                    outline-offset: 0;
                }

                &:active {
                    outline-style: solid;
                    outline-color: $color-red50;
                    outline-width: $border-width-default;
                    outline-offset: 0;
                }

                &:hover {
                    opacity: 1;
                }
            }

            &.active input,
            &.active input:focus {
                opacity: 1;
                outline-style: solid;
                outline-color: $color-purewhite;
                outline-width: $border-width-default;
                outline-offset: 0;
            }

            &.blurSlider {
                opacity: 0.7;
            }

            .galleria-thumbnail__current-thumbnail {
                @include screenreader-only;
            }
        }
    }
}
