.destination-leaver {
    grid-column: 1/-1;

    @include media('>tablet') {
        .destination-leaver-content {
            display: flex;
            flex-wrap: wrap;
        }
        .destination-leaver-content:not([data-length='1']) {
            div {
                width: 50%;
            }
        }
    }
}

.wrapper--destination-leavers-HE {
    margin-bottom: $measure--basic;

    @include media('>tablet') {
        .wrapper--finder__filters {
            .finder__filters {
                .finder__filter:not(.finder__tag) {
                    width: 100%;
                }
            }
        }

        .finder__query__submit {
            margin-left: initial;
        }
    }
    .wrapper--finder__filters {
        .finder__filters {
            label:not(.finder__filters__label--always) {
                border: initial;
                clip: initial;
                height: initial;
                overflow: initial;
                position: initial;
                width: initial;
                margin-bottom: $measure--half;
                color: $color-nearblack;
            }

            .finder__filters__reset--desktop {
                margin-top: $measure--basic;
            }
        }
    }
    .wrapper--finder_filters--filters {
        display: block;
    }

    .wrapper--destination-leavers-HE__info-text {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

.destination {
    .destination-content {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

.wrapper--finder__filters[data-open='false'] {
    .wrapper--finder__filters--mobile__filters__content--text {
        display: none;
    }
}

.wrapper--finder__filters--mobile__filters__content--text {
    @include media('<small') {
        padding-left: $measure--small2;
        padding-right: $measure--small2;
    }
}

.destination-content {
    border-top: $border-width-hairline solid $color-gray4;
}

.destination-leaver {
    @include media('>=tablet') {
        @include default-margin();
    }

    .chart-row {
        margin-top: $measure--basic;
        padding-left: initial;
        padding-right: initial;
        margin-left: initial;
        margin-right: initial;

        // @include media('>=tablet', '<small') {
        //     display: block;
        //     .chart {
        //         width: 100%;
        //     }

        //     .chart__columns {
        //         display: flex;
        //     }
        // }

        .chart {
            @include media('>=small') {
                width: 50%;
            }

            path:nth-child(1) {
                stroke: $color-pink30;
            }
            path:nth-child(2) {
                stroke: $color-teal100;
            }
            path:nth-child(3) {
                stroke: $color-blue60;
            }
            path:nth-child(4) {
                stroke: $color-brown80;
            }
            path:nth-child(5) {
                stroke: $color-red40;
            }
            path:nth-child(6) {
                stroke: $color-blue80;
            }
            path:nth-child(7) {
                stroke: $color-purple40;
            }

            .chart__legend__item:nth-child(1) {
                .fa.fa-circle {
                    color: $color-pink30;
                }
            }
            .chart__legend__item:nth-child(2) {
                .fa.fa-circle {
                    color: $color-teal100;
                }
            }
            .chart__legend__item:nth-child(3) {
                .fa.fa-circle {
                    color: $color-blue60;
                }
            }
            .chart__legend__item:nth-child(4) {
                .fa.fa-circle {
                    color: $color-brown80;
                }
            }
            .chart__legend__item:nth-child(5) {
                .fa.fa-circle {
                    color: $color-red40;
                }
            }
            .chart__legend__item:nth-child(6) {
                .fa.fa-circle {
                    color: $color-blue80;
                }
            }
            .chart__legend__item:nth-child(7) {
                .fa.fa-circle {
                    color: $color-purple40;
                }
            }
        }
    }
}
