@import 'search--inline';

.search {
    .search__results__summary__heading {
        @include font-family('default');
        @include alphatype();
        @include body-font-features();
    }

    .wrapper--search__results:not(.tabs) {
        .tabs__links {
            display: none;
        }
    }

    .wrapper--search__results,
    .wrapper--search__othersites {
        @include media('<=between') {
            margin-left: -#{$measure--basic};
            margin-right: -#{$measure--basic};
        }

        @include media('>between', '<small') {
            margin-left: -#{$measure--small2};
            margin-right: -#{$measure--small2};
        }
    }

    .wrapper--search__results {
        .tabs__panel__body {
            padding-bottom: $measure--small4;
        }
    }

    .search-form__query + .wrapper--search__results {
        .tabs__links {
            margin-top: 1.75rem;
        }
    }

    .search__bestbets {
        margin-top: $measure--medium3;
    }

    .search__othersites {
        background-color: $color-gray1;
        border-color: $color-gray3;
        border-style: $border-style-solid;
        border-width: $border-width-default;
        padding: $measure--basic;

        @include media('<small') {
            margin-top: $measure--small4;
        }

        .search__othersites__heading {
            @include h3style();

            margin-top: 0;
        }
    }

    .search__results__summary {
        border-bottom: $border-width-hairline $border-style-solid $color-gray3;
        padding-bottom: $measure--basic;
        margin-left: auto;
        margin-right: auto;
        max-width: calc(
            #{$max-line-length} + #{$measure--huge5} + #{$measure--basic}
        );
    }

    .search__results__list,
    .search__results__browse-more,
    .search__results__browse-more ul,
    .search__bestbets__list {
        list-style: none;
        margin-left: auto;
        margin-right: auto;
        max-width: calc(
            #{$max-line-length} + #{$measure--huge5} + #{$measure--basic}
        );

        .card--search {
            padding-bottom: $measure--basic;

            .card__heading {
                .icon {
                    color: $color-inline-link;
                    margin-left: $measure--half;
                }
            }

            a {
                @include anchor-reset();

                background-color: transparent;
                color: $color-nearblack;

                &:hover {
                    @include media('hover') {
                        color: $color-nearblack;
                    }
                }

                &:active,
                &:focus {
                    @include outline-properties($color-nearblack);

                    color: $color-nearblack;
                }

                .card__heading {
                    @include h4style();

                    background-image: linear-gradient(
                        120deg,
                        $color-inline-link 0%,
                        $color-inline-link 100%
                    );
                    background-position: 0 100%;
                    background-repeat: no-repeat;
                    background-size: 100% 0;
                    border-bottom-color: $color-inline-link;
                    border-bottom-style: $border-style-solid;
                    border-bottom-width: $border-width-default;
                    color: $color-inline-link;
                    display: inline;
                    max-width: $max-line-length;
                    transition: background-size $transition-duration
                        $transition-function;

                    @include media('reducemotion') {
                        transition: none;
                    }
                }

                &:hover {
                    @include media('hover') {
                        .card__thumbnail {
                            transform: scale(1.02);
                        }

                        .card__heading {
                            background-size: 100% 100%;
                            background-image: linear-gradient(
                                120deg,
                                $color-inline-link 0%,
                                $color-inline-link 100%
                            );
                            color: $color-purewhite;
                        }
                    }
                }
            }

            .card__label {
                @include smalltype();

                color: $color-gray8;
                font-style: italic;
            }

            .card__details {
                display: flex;
                flex-wrap: nowrap;

                @include media('<small') {
                    flex-direction: column;
                }

                @include media('>=small') {
                    flex-direction: row;
                }
            }

            .card__thumbnail + .card__details__text {
                @include media('<small') {
                    padding-left: 0;
                    padding-top: $measure--basic;
                }

                @include media('>=small') {
                    padding-left: $measure--basic;
                    padding-top: 0;
                }
            }

            .card__details__text {
                width: 100%;
            }

            .card__description {
                @include content-style();

                margin-top: $content-element-margin;
                max-width: $max-line-length;
            }

            .card__url {
                @include smalltype();
                margin-top: $measure--basic;
                max-width: unset;
                color: $color-gray7;
                word-break: break-word;
            }

            .card__thumbnail {
                align-self: flex-start;
                flex-shrink: 0;
                height: auto;
                transition: transform $transition-duration ease-in-out;
                width: $measure--huge5;

                @include media('reducemotion') {
                    transition: none;
                }
            }

            & + .card--search {
                border-top: $border-width-hairline $border-style-solid
                    $color-gray3;
                padding-top: $measure--basic;
            }
        }

        .card {
            .card__anchor {
                .card__type {
                    color: $color-nearblack;
                    font-size: $font-size--alpha;
                }

                .card__heading {
                    @include h4style();

                    background-image: linear-gradient(
                        120deg,
                        $color-inline-link 0%,
                        $color-inline-link 100%
                    );
                    background-position: 0 100%;
                    background-repeat: no-repeat;
                    background-size: 100% 0;
                    border-bottom-color: $color-inline-link;
                    border-bottom-style: $border-style-solid;
                    border-bottom-width: $border-width-default;
                    color: $color-inline-link;
                    display: inline;
                    max-width: $max-line-length;
                    transition: background-size $transition-duration
                        $transition-function;

                    @include media('reducemotion') {
                        transition: none;
                    }
                }

                .card__details__text {
                    padding: initial;
                    padding-bottom: $measure--basic;
                }

                &:hover {
                    @include media('hover') {
                        .card__heading {
                            background-size: 100% 100%;
                            background-image: linear-gradient(
                                120deg,
                                $color-inline-link 0%,
                                $color-inline-link 100%
                            );
                            color: $color-purewhite;
                        }
                    }
                }
            }

            & + .card {
                border-top: $border-width-hairline $border-style-solid
                    $color-gray3;
                padding-top: $measure--basic;
            }

            .card--profile--thumbnailAvailable {
                flex-direction: row-reverse;
                justify-content: space-between;
                padding-bottom: $measure--basic;

                @include media('<=mobile') {
                    flex-direction: column-reverse;
                }
            }
        }
    }

    .search__bestbets__list {
        @include media('>small') {
            max-width: initial;
        }
        .card--search .card__details {
            padding: $measure--basic;

            &,
            &:active,
            &:focus {
                border-color: $color-gray3;
                border-style: $border-style-solid;
                border-width: $border-width-hairline;
            }

            &:hover {
                @include media('hover') {
                    border-color: $color-gray3;
                    border-style: $border-style-solid;
                    border-width: $border-width-hairline;
                }
            }
        }
    }

    .search__results__list {
        .card {
            margin-top: initial;
            .card__anchor {
                &:after {
                    content: '';
                    background-color: initial;
                    color: initial;
                }
            }

            .card__tag {
                .icon {
                    color: $color-nearblack;
                }
            }
        }
    }

    .search__othersites__noresults {
        display: block;
    }

    .search__othersites__site + .search__othersites__noresults {
        display: none;
    }

    .search__query__suggestions {
        background-color: $color-purewhite;
        border-radius: 0 0 $measure--half $measure--half;
        border-top: 0;
        border: $border-width-hairline $border-style-solid $color-gray2;
        box-shadow: rgba(0, 0, 0, 0.25) 0 $measure--basic $measure--medium3 -$measure--threequarters;
        color: $color-purewhite;
        font-weight: $font-weight-bold;
        left: 0;
        list-style: none;
        margin-left: 0;
        max-width: unset;
        position: absolute;
        top: $measure--medium1;
        width: 100%;
        z-index: z-index('above');

        li {
            font-weight: $font-weight-text;
            margin-top: 0;

            button {
                color: $color-text;
                height: 100%;
                padding: $measure--half $measure--small4 $measure--half
                    $measure--small4;
                text-align: left;
                width: 100%;

                &:focus {
                    outline-color: $color-nearblack;
                    outline-offset: (-$border-width-default);
                    outline-style: $border-style-dotted;
                    outline-width: $border-width-default;
                    text-decoration: underline;
                }

                &:active {
                    @include outline-properties();

                    outline-offset: 0;
                }

                &:hover {
                    background-color: $color-linkblue;
                    color: $color-purewhite;
                    text-decoration: underline;
                }

                .search__suggestion {
                    font-weight: $font-weight-bold;
                }
            }
        }
    }

    .search__query__suggestions.hide {
        display: none;
    }

    .search__othersites__site__heading {
        @include font-family('default');
        @include alphatype();
        @include body-font-features();

        font-weight: $font-weight-text;
    }

    .search__othersites__site__heading,
    .search__results__more,
    .search__summary__noresults {
        a {
            @include anchor-inline();
        }

        .icon {
            color: $color-inline-link;
        }
    }

    .search__results__more {
        max-width: calc(
            #{$max-line-length} + #{$measure--huge5} + #{$measure--basic}
        );
        margin-left: auto;
        margin-right: auto;
        text-align: right;
    }

    @mixin selected-select() {
        background-color: $color-purewhite;
        border-color: $color-red60;
        color: $color-red60;
        font-weight: $font-weight-medium;
    }

    .search__select {
        color: $color-nearblack;
        height: auto;
        margin-top: $measure--half;
        position: relative;

        @include media('<tablet') {
            margin-bottom: $measure--half;
        }

        @include media('>=tablet') {
            margin-right: $measure--basic;
        }

        label {
            @include sr-only();
            @include smalltype();

            color: $color-red50;
            display: block;
            font-weight: $font-weight-text;
            padding-bottom: $measure--basic;
        }

        select {
            @include font-family('default');
            @include body-font-features();
            @include alphatype();

            background-color: $color-gray1;
            border-color: $color-gray4;
            border-style: $border-style-solid;
            border-width: $border-width-default;
            bottom: $measure--quarter;
            color: $color-nearblack;
            font-weight: $font-weight-text;
            height: $measure--medium4;
            position: relative;
            width: 100%;

            &:focus {
                @include outline-properties();
            }
        }

        &:not(.search__select--sort) {
            &.search__select--selected {
                select {
                    @include selected-select();
                }
            }
        }

        &.search__select--sort {
            &.search__select--selected {
                select {
                    @include media('<tablet') {
                        @include selected-select();
                    }
                }
            }
        }
    }
}
