.banner__standfirst {
    background: $color-blue90;
    color: $color-purewhite;
    padding: $measure--small4;
    position: relative;

    @include media('<desktop') {
        grid-column: 1 / -1;
    }

    @include media('<tablet') {
        padding: $measure--small4 $measure--basic $measure--small4
            $measure--basic;
    }

    @include media('>=desktop') {
        grid-column: 4 / 16;
        margin-top: -#{$measure--medium9};
    }
}
